import { createContext, useContext, useEffect, useReducer } from 'react';
// import { ImagePickerResponse } from 'react-native-image-picker';
import AppApi from '../api/AppApi';
import { ChildrenProps, Cliente, ClientesResponse, ClieteFilterResponse, DataFilters, MapPointer } from '../interfaces/interfaces';
import { clienteReducer, ClienteState } from '../reducers/clienteReducer';
import { AppContext } from './AppContext';
import { AuthContext } from './AuthContext';
// import { MessagesContext } from './MessagesContext';

//TODO: Fotos de los clientes


type ClienteContextProps = {
    clientes: Cliente[],
    clientesFiltrados: Cliente[],
    clienteSeleccionado: Cliente | null,
    idClienteSeleccionado: string | null,
    dataFiltros: DataFilters[],
    indexClienteSeleccionado: number | null,
    mapPointers : MapPointer[],
    cargaClientes: () => void;
    cargaClientesFiltrado: () => void;
    addFiltrosCliente: (filtros: any) => void;
    clearFiltrosCliente: () => void;
    seleccionaCliente: (cliente: Cliente) => void;
    crearCliente: (cliente: any) => void;
    updateCliente: (cliente: any) => void;
    purgaClienteSeleccionado: () => void;

    cargaClientesFiltrados: (clientes: Cliente[]) => void;
    purgaClientesFiltrados: () => void;

    seleccionaIndexCliente: (index : number) => void;
    clearIndexCliente: () => void;

    cargaMapPointers: (mapPointers: MapPointer[]) => void;
    purgaMapPointers: () => void;


    // uploadImage: (userIde: string, data: ImagePickerResponse) => void;
}

const initialState: ClienteState = {
    clientes: [],
    clientesFiltrados: [],
    dataFiltros: [],
    clienteSeleccionado: null,
    idClienteSeleccionado: null,
    indexClienteSeleccionado: null,
    mapPointers : [],
}

const limite = 500;



export const ClienteContext = createContext({} as ClienteContextProps)
export const ClienteProvider = ({ children }: ChildrenProps) => {

    const [state, dispatch] = useReducer(clienteReducer, initialState)
    const { status, user } = useContext(AuthContext)
    const { showProcesing , hideProcesing} = useContext(AppContext)

    const cargaClientes = async () => {
        const { data } = await AppApi.get<ClientesResponse>(`/clientes?limite=${limite}&desde=0`);
        dispatch({
            type: 'carga-data-clientes',
            payload: data.clientes
        })
    }

    const cargaClientesFiltrado = async () => {
        if (state.dataFiltros.length === 0) {
            dispatch({
                type: 'carga-data-cliente-filtrados',
                payload: state.clientes
            })
        } else {
            const { data } = await AppApi.post<ClieteFilterResponse>(`/search/clientes`, state.dataFiltros);
            dispatch({
                type: 'carga-data-cliente-filtrados',
                payload: data.results
            })
        }
    }

    useEffect(() => {
        if (status === 'authenticated') {
            cargaClientes()
        }
    }, [status])

    useEffect(() => {
        if (status === 'authenticated') {
            cargaClientesFiltrado()
        }
    }, [status, state.dataFiltros, state.clientes])


    // useEffect(() => {
    //     if (state.indexClienteSeleccionado !== null) {
    //         seleccionaCliente(state.clientesFiltrados[state.indexClienteSeleccionado])
    //     }
    // }, [state.indexClienteSeleccionado])


    const seleccionaCliente = (cliente: Cliente) => {
        dispatch({
            type: 'selecciona-cliente',
            payload: cliente
        })
    }

    const addFiltrosCliente = (filtros: any) => {
        dispatch({
            type: 'carga-data-filtros',
            payload: filtros
        })
    }

    const clearFiltrosCliente = () => {
        dispatch({
            type: 'purga-data-filtros',
        })
    }


    const purgaClienteSeleccionado = () => {
        dispatch({
            type: 'purga-cliente',
        })
    }

    const crearCliente = async (cliente: any) => {
        const { _id, ...dataClean } = cliente
        const dataSend = {
            ...dataClean,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.post<ClientesResponse>(`/clientes`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaClientes()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Cliente registrado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }


    // const uploadImage = async (clienteId: string, data: ImagePickerResponse) => {

    //     const fileToUpload = {
    //         uri: data.assets![0].uri,
    //         type: data.assets![0].type,
    //         name: data.assets![0].fileName
    //     }

    //     const formData = new FormData()
    //     formData.append('archivo', fileToUpload)

    //     try {
    //         const resp = await AppApi.put(`/uploadsimg/clientes/${clienteId}`, formData);
    //         // dispatch({
    //         //     type: 'registraUsuario',
    //         //     payload: resp.data.modelo,
    //         // })
    //     } catch (error) {
    //         registraNotification({
    //             title: 'Error',
    //             msg: 'No se pudo procesar la fotografa',
    //             type: 'error'
    //         })
    //     }

    // }


    const updateCliente = async (cliente: any) => {
        const dataSend = {
            ...cliente,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.put<ClientesResponse>(`/clientes/${cliente._id}`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaClientes()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Cliente actualizado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }

    const cargaClientesFiltrados  = (clientes: Cliente[]) => {
        dispatch({
            type: 'carga-dataFilter-clientes',
            payload : clientes
        })
    }
    const purgaClientesFiltrados  = () => {
        dispatch({
            type: 'clear-dataFilter-clientes',
        })
    }
    const seleccionaIndexCliente  = (index : number) => {
        dispatch({
            type: 'carga-index-cliente',
            payload : index
        })
    }
    const clearIndexCliente  = () => {
        dispatch({
            type: 'clear-index-cliente',
        })        
    }

    const cargaMapPointers  = (mapPointers: MapPointer[]) => {
        dispatch({
            type: 'carga-map-pointers',
            payload : mapPointers
        }) 
    }
    
    const purgaMapPointers  = () => {
        dispatch({
            type: 'clear-map-pointers',
        })         
    }

    return (
        <ClienteContext.Provider value={{
            ...state,
            cargaClientes,
            seleccionaCliente,
            purgaClienteSeleccionado,
            crearCliente,
            updateCliente,
            addFiltrosCliente,
            clearFiltrosCliente,
            cargaClientesFiltrado,
            cargaClientesFiltrados,
            purgaClientesFiltrados,
            seleccionaIndexCliente,
            clearIndexCliente,
            cargaMapPointers,
            purgaMapPointers,
        }}>
            {children}
        </ClienteContext.Provider>
    )

}