import { useContext, useEffect, useState } from "react";
import { LogoEmm } from "../../components/ui/atom/FormControls";
import { AuthContext } from "../../context/AuthContext";

export const LoginPage = () => {
  const { singIn } = useContext(AuthContext);

  const [form, setForm] = useState({
    correo: '',
    password: '',
    rememberme: false,
  });

  useEffect(() => {
    const correo = localStorage.getItem("correo");
    correo &&
      setForm((form) => ({
        ...form,
        rememberme: true,
        correo,
      }));
  }, []);

  const onChange = ({ target }: any) => {
    const { name, value } = target;
    setForm({
      ...form,
      [name]: value,
    });
  };

  const toggleCheck = () => {
    setForm({
      ...form,
      rememberme: !form.rememberme,
    });
  };

  const onSubmit = async (ev: any) => {
    ev.preventDefault();

    form.rememberme
      ? localStorage.setItem("correo", form.correo)
      : localStorage.removeItem("correo");

    const { correo, password } = form;
    singIn({ correo, password });
  };

  const todoOk = () => {
    return form.correo.length > 0 && form.password.length ? true : false;
  };

  return (
    <form
      className="login100-form validate-form flex-sb flex-w animate__animated animate__fadeInLeft"
      onSubmit={onSubmit}
    >
      <span className="login100-form-title mb-3">
        <LogoEmm />
      </span>

      <div className="wrap-input100 validate-input mb-3">
        <input
          className="input100"
          type="correo"
          name="correo"
          placeholder="correo"
          value={form.correo}
          onChange={onChange}
        />
        <span className="focus-input100"></span>
      </div>

      <div className="wrap-input100 validate-input mb-3">
        <input
          className="input100"
          type="password"
          name="password"
          placeholder="Password"
          value={form.password}
          onChange={onChange}
        />
        <span className="focus-input100"></span>
      </div>

      <div className="row mb-3">
        <div className="col" onClick={() => toggleCheck()}>
          <input
            className="input-checkbox100"
            id="ckb1"
            type='checkbox'
            name="rememberme"
            checked={form.rememberme}
            readOnly
          />
          <label className="label-checkbox100">Recordarme</label>
        </div>

      </div>

      <div className="container-login100-form-btn m-t-17">
        <button
          type="submit"
          disabled={!todoOk()}
          className="login100-form-btn"
        >
          Ingresar
        </button>
      </div>
    </form>
  );
};
