import { TransaccionInv, Lote, ProductoSaldo, ProductosTransaccion, ClienteProductos } from '../interfaces/interfaces';


export interface InventarioState {
    transacciones: TransaccionInv[],
    transaccionSeleccionada: TransaccionInv | null,
    lotes: Lote[]
    loteSeleccionado: Lote | null,
    productoSaldo: ProductoSaldo[]
    productoSaldoMM: ProductoSaldo[]
    productosTransaccion: ProductosTransaccion[]
    productoscliente: ClienteProductos[],

}

type InventarioAction =
    | { type: 'carga-data-transacciones', payload: TransaccionInv[] }
    | { type: 'selecciona-transaccion', payload: TransaccionInv }
    | { type: 'add-transaccion', payload: TransaccionInv }
    | { type: 'update-transaccion', payload: TransaccionInv }
    | { type: 'delete-transaccion', payload: string }
    | { type: 'purga-transaccion' }
    | { type: 'carga-data-lotes', payload: Lote[] }
    | { type: 'selecciona-lote', payload: Lote }
    | { type: 'add-lote', payload: Lote }
    | { type: 'update-lote', payload: Lote }
    | { type: 'delete-lote', payload: string }
    | { type: 'purga-lote' }
    | { type: 'add-producto-saldo-original', payload: ProductoSaldo }
    | { type: 'add-producto-saldo-MM', payload: ProductoSaldo }
    | { type: 'purga-producto-saldo-original' }
    | { type: 'purga-producto-saldo-MM' }
    | { type: 'add-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'delete-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'update-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'purga-producto-transaccion' }
    | { type: 'carga-data-productosclientes', payload: ClienteProductos[] }
    | { type: 'purga-productoscliente' }


export const inventarioReducer = (state: InventarioState, action: InventarioAction) => {

    switch (action.type) {
        case 'carga-data-transacciones':
            return {
                ...state,
                transacciones: action.payload
            }
        case 'selecciona-transaccion':
            return {
                ...state,
                transaccionSeleccionada: action.payload
            }
        case 'add-transaccion':
            return {
                ...state,
                transacciones: [...state.transacciones, action.payload]
            }
        case 'update-transaccion':
            return {
                ...state,
                transacciones: state.transacciones.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'delete-transaccion':
            return {
                ...state,
                transacciones: state.transacciones.filter((e) => e._id !== action.payload)
            }
        case 'purga-transaccion':
            return {
                ...state,
                transaccionSeleccionada: null
            }

        // Lotes

        case 'carga-data-lotes':
            return {
                ...state,
                lotes: action.payload
            }
        case 'selecciona-lote':
            return {
                ...state,
                loteSeleccionado: action.payload
            }
        case 'add-lote':
            return {
                ...state,
                lotes: [...state.lotes, action.payload]
            }
        case 'update-lote':
            return {
                ...state,
                lotes: state.lotes.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'delete-lote':
            return {
                ...state,
                lotes: state.lotes.filter((e) => e._id !== action.payload)
            }
        case 'purga-lote':
            return {
                ...state,
                loteSeleccionado: null
            }


        case 'add-producto-saldo-original':
            return {
                ...state,
                productoSaldo: [...state.productoSaldo, action.payload]
            }
        case 'add-producto-saldo-MM':
            return {
                ...state,
                productoSaldoMM: [...state.productoSaldoMM, action.payload]
            }
        case 'purga-producto-saldo-original':
            return {
                ...state,
                productoSaldo: []
            }
        case 'purga-producto-saldo-MM':
            return {
                ...state,
                productoSaldoMM: []
            }

        //Transaccion de muestras

        case 'add-producto-transaccion':
            return {
                ...state,
                productosTransaccion: [...state.productosTransaccion, action.payload]
            }
        case 'delete-producto-transaccion':
            return {
                ...state,
                productosTransaccion: state.productosTransaccion.filter((e) => e !== action.payload)
            }
        case 'update-producto-transaccion':
            return {
                ...state,
                productosTransaccion: state.productosTransaccion.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'purga-producto-transaccion':
            return {
                ...state,
                productosTransaccion: []
            }

        //Productos por Cliente
        case 'carga-data-productosclientes':
            return {
                ...state,
                productoscliente: action.payload
            }
        case 'purga-productoscliente':
            return {
                ...state,
                productoscliente: []
            }

        default:
            return state;
    }


}