import { useContext } from "react";
import { AuthContext } from "../../context/AuthContext";
import { LogoEmm } from "../ui/atom/FormControls";
import { Spiner } from "../ui/atom/Spiner";

export const UsuarioVerify = () => {
  const { logOut } = useContext(AuthContext);

  return (
    <div
      style={{
        position: "absolute",
        // height: "200px",
        // width: "600px",
        left: "50%",
        top: "50%",
        marginTop: "-200px",
        marginLeft: "-300px",
      }}
      className="img-thumbnail animate__animated animate__fadeInLeft"
    >
      <div
        className='alert alert-primary'
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          flexDirection: "column",
          textAlign: "center",
          backgroundColor : 'lightgray'
          // height: "200px",
          // width: "600px",          
        }}
      >
        <hr />
        <LogoEmm />
        <Spiner />
        <h4>Conectando al servidor y recuperando informacin</h4>
        <hr />

        <span>Espere por favor...!</span>
        <hr />
        <button onClick={logOut} className="btn btn-outline-danger mb-4">
          Cerrar Sesin...!
        </button>
      </div>
    </div>
  );
};
