import { useCallback, useEffect, useState } from "react";
import { io } from "socket.io-client";
//http://192.168.2.17:8080
export const useSocket = (serverPath: string = 'https://dif-visita-med.herokuapp.com') => {
// export const useSocket = (serverPath: string = process.env.REACT_APP_API_URL) => {
  // export const useSocket = (serverPath: string = 'http://192.168.2.17:8080') => {
  // export const useSocket = (serverPath: string = 'http://192.168.100.131:8080') => {
  const [socket, setSocket] = useState<any>();
  const [onLine, setOnLine] = useState(false);

  const conectarSocket = useCallback((token: string) => {
    const socketTemp = io(serverPath, {
      transports: ["websocket"],
      autoConnect: true,
      forceNew: true,
      query: {
        "x-token": token,
      },
    });
    setSocket(socketTemp);
    setOnLine(true)
  }, [serverPath]);

  const desconectarSocket = useCallback(() => {
    setOnLine(false)
    socket?.disconnect();
  }, [socket]);

  useEffect(() => {
    setOnLine(socket?.connected);
  }, [socket]);

  useEffect(() => {
    socket?.on('connect', () => {
      console.log("Conectado");
      setOnLine(true)
    });
  }, [socket]);

  useEffect(() => {
    socket?.on('disconnect', () => {
      console.log("Desconectado");
      setOnLine(false)
    });
  }, [socket]);

  return {
    socket,
    onLine,
    desconectarSocket,
    conectarSocket,
  };
};
