import { useParams } from "./useParams."
import { Usuario } from '../interfaces/interfaces';
import { useContext } from 'react';
import { SocketContext } from '../context/SocketContext';
import { AuthContext } from "../context/AuthContext";
import { MasterDataContext } from "../context/MasterDataContext";

export const useVisitadorPais  = () => {

    const visitadoresPais : Usuario[] = []
    const { paisMatriz } = useParams()
    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)
    const {paises} = useContext(MasterDataContext)

    const idPaisMatriz = paises.filter(pais => pais.codigo === paisMatriz)
    if (idPaisMatriz.length > 0) {
        const {_id: idPais} = idPaisMatriz[0]
        if(user?.pais === idPais) {
            visitadoresPais.push(...usuarios)
        } else {
            const visitadoresTemp = usuarios.filter(visit => visit.pais === user?.pais)
            visitadoresPais.push(...visitadoresTemp)
        }
    }

    const visitadores = (user?.rol === 'SUPER_ADMIN_ROLE' || user?.rol === 'ADMIN_ROLE') 
    ? usuarios
    : (user?.rol === 'JEF_VIS_ROLE')
        ? visitadoresPais.filter(visitadore => visitadore.rol !== 'SUPER_ADMIN_ROLE' && visitadore.rol !== 'ADMIN_ROLE')
        : visitadoresPais.filter(visitadore => visitadore.uid === user?.uid) 
        
    return {
        visitadores
    }
    
}