import { useContext } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { NavbarMain } from '../components/NavbarMain';
import { SpinerModal } from "../components/ui/atom/SpinerModal";
import { AuthContext } from "../context/AuthContext";
import { CalendarioPortada } from '../pages/calendario/CalendarioPortada';

import {
  PrincipalPage,
  PerfilPage,
  CalendarioScreen,
  AdminScreen,
  ClientesScreen,
  RutasScreen,
  UsuariosScreen,
  VademecumScreen,
  VisitasScreen,
  InventarioScreen,
  UsuariosAddScreen,
  GeoScreen,
  ParamsScreen,
  CaracteristicasScreen,
  CalendarioAgendas,
  CalendarioEventos,
  ClientesActivosScreen,
  ClientesInactivosScreen,
  ClientesMapeoScreen,
  VisitasHistoricoScreen,
  VademecumAllScreen,
  CaracteristicasTiposScreen,
  CalendarModal,
  InventarioTransaccionesScreen,
  InventarioAddScreen,
  RutasConsulta,
  VadeScreen,
  InventarioSaldosScreen,
  ClienteScreen,
  VisitaScreen,
  LoteModal,
  InventarioTransaccionScreen,
  MaximizeReportBI
} from "../pages";
import { ProcesingModal } from "../components/ui/atom/ProcesingModal";

export const DashboardRoutes = () => {
  const { user } = useContext(AuthContext)
  return (
    <>
      <SpinerModal />
      <ProcesingModal />
      <CalendarModal />
      <LoteModal />
      <NavbarMain />
      
      <div>
        <Switch>
          {/* Rutas protegidas para Admnistracion */}
          {(user?.rol === "SUPER_ADMIN_ROLE" || user?.rol === "ADMIN_ROLE") && (
            <Route exact path="/home/admin" component={AdminScreen} />
          )}
          {/* Rutas Publicas para todos los usuarios */}
          <Route exact path="/home" component={PrincipalPage} />
          <Route exact path="/home/agendas" component={CalendarioAgendas} />
          <Route exact path="/home/calendar" component={CalendarioPortada} />
          <Route exact path="/home/calendario" component={CalendarioScreen} />
          <Route exact path="/home/caracteristicas" component={CaracteristicasScreen} />
          <Route exact path="/home/caracteristicasgrupos" component={CaracteristicasTiposScreen} />
          <Route exact path="/home/clientes" component={ClientesScreen} />
          <Route exact path="/home/cliente" component={ClienteScreen} />
          <Route exact path="/home/clientesactivos" component={ClientesActivosScreen} />
          <Route exact path="/home/clientesinactivos" component={ClientesInactivosScreen} />
          <Route exact path="/home/clientesmapeo" component={ClientesMapeoScreen} />
          <Route exact path="/home/eventos" component={CalendarioEventos} />
          <Route exact path="/home/geo" component={GeoScreen} />
          <Route exact path="/home/inventario" component={InventarioScreen} />
          <Route exact path="/home/inventarioadd" component={InventarioAddScreen} />
          <Route exact path="/home/inventariosaldos" component={InventarioSaldosScreen} />
          <Route exact path="/home/inventariotransacciones" component={InventarioTransaccionesScreen} />
          <Route exact path="/home/inventariotransaccion" component={InventarioTransaccionScreen} />
          <Route exact path="/home/param" component={ParamsScreen} />
          <Route exact path="/home/rutas" component={RutasScreen} />
          <Route exact path="/home/rutasmax" component={MaximizeReportBI} />
          <Route exact path="/home/rutasconsulta" component={RutasConsulta} />
          <Route exact path="/home/user/perfil" component={PerfilPage} />
          <Route exact path="/home/usuarios" component={UsuariosScreen} />
          <Route exact path="/home/usuariosadd" component={UsuariosAddScreen} />
          <Route exact path="/home/vade" component={VadeScreen} />
          <Route exact path="/home/vademecum" component={VademecumScreen} />
          <Route exact path="/home/vademecumall" component={VademecumAllScreen} />
          <Route exact path="/home/visitas" component={VisitasScreen} />
          <Route exact path="/home/visita" component={VisitaScreen} />
          <Route exact path="/home/visitashistorico" component={VisitasHistoricoScreen} />
          <Redirect to="/home" />
        </Switch>
      </div>
    </>
  );
};


