import { useState} from 'react';
import { Divider } from "@material-ui/core"
import { NavbarLeft } from "../../components/NavbarLeft"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { PaisScreen } from './PaisScreen';
import { ProvinciaScreen } from './ProvinciaScreen';
import { CantonScreen } from './CantonScreen';
import { DistritoScreen } from './DistritoScreen';
import { BarrioScreen } from './BarrioScreen';
import { TitlePage } from '../../components/ui/atom/TitlePage';

export const GeoScreen = () => {
    const [estado, setEstado] = useState('')


    const hangleClick  = (option : string) => {
        setEstado(option)
    }

    const SelectedScreen  = () => {
        switch (estado) {
            case "pais":
                return <PaisScreen />
            case "provincia":
                return <ProvinciaScreen />
            case "canton":
                return <CantonScreen />
            case "distrito":
                return <DistritoScreen />
            case "barrio":
                return <BarrioScreen />
            default:
                return <h4>Seleccione una Opcin</h4>;
        }
    }



    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>

            <div
                className="col-md-10"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <div>
                    <TitlePage title='Distribucin Geogrfica' />
                    <SpacerHorizontal />
                    <button
                        className="btn btn-outline-dark mb-2 col-md-1"
                        onClick={() => hangleClick("pais")}
                    >
                        Pases
                    </button>
                    <button
                        className="btn btn-outline-dark mb-2 col-md-1"
                        onClick={() => hangleClick( "provincia")}
                    >
                        Provincias
                    </button>
                    <button
                        className="btn btn-outline-dark mb-2 col-md-1"
                        onClick={() => hangleClick( "canton")}
                    >
                        Cantones
                    </button>
                    <button
                        className="btn btn-outline-dark mb-2 col-md-1"
                        onClick={() => hangleClick("distrito")}
                    >
                        Distritos
                    </button>
                    <button
                        className="btn btn-outline-dark mb-2 col-md-1"
                        onClick={() => hangleClick("barrio")}
                    >
                        Barrios
                    </button> 
                    <SpacerHorizontal />
                    <Divider />
                </div>
                <SelectedScreen />
            </div>
        </div>
    )
}



