import moment from "moment"
import { useContext } from "react"
import { AuthContext } from "../../context/AuthContext"
import { SocketContext } from "../../context/SocketContext"
import { useForm } from "../../hooks/useForm"
import { BtnTypeEnter, SelectDateddMMyyyy, SelectVisitador, TextControlNumCol, SelectItem } from '../ui/atom/FormControls';
import { Evento } from '../../interfaces/interfaces';
// import { file_Download_Visitas } from '../../helpers/file_Download';
import { ClienteContext } from "../../context/ClienteContext"
import { VisitaContext } from '../../context/VisitaContext';
import { TableVisitas } from "../tablas/visitas/TableVisitas"
import { file_Download_Visitas } from "../../helpers/file_Download"
import { useVisitadorPais } from "../../hooks/useVisitadorPais"

interface Form {
    uid?: string
    inicio: any;
    fin: any;
    max: number;
    cliente:  string ,
}

export interface CalendarResponse {
    ok: boolean;
    msg: string;
    total: number;
    eventos: Evento[];
  }


export const VisitasFilterOptions  = () => {

    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)
    const {cargaDataFilter, visitasFilter, purgaDataFilter, clearDataFilterIndex} = useContext(VisitaContext)

    const initForm = {
        uid : user?.uid,
        inicio: new Date(moment().startOf('isoWeek').toDate()),
        fin: new Date(moment().endOf('isoWeek').toDate()),
        max: 50,
        cliente : '0',
    }
    const {uid, inicio, fin, max, cliente, onChange, setForm, form } = useForm<Form>(initForm)

    const {visitadores} =  useVisitadorPais()

    const { clientes } = useContext(ClienteContext)
    const clientesActivos = clientes.filter(cliente => cliente.causabaja.nombre === 'OPERATIVO' && cliente.visitador._id === user?.uid)
            
    const handleDownloadFile  = () => {
        file_Download_Visitas({visitasFilter})
    }

    const handleFilter  = () => {
        cargaDataFilter({inicio, fin, uid, max, cliente})
        
        
    }

      const onSubmitClear  = () => {
        purgaDataFilter()
        clearDataFilterIndex()
        setForm(initForm)
    }

    const handleStartDateChange = (e) => {
        setForm({
            ...form,
            inicio: e,
        });
        };
    
    const handleEndDateChange = (e) => {
        setForm({
            ...form,
            fin: e,
        });
    };

    return (
        <div>
            <div className="imput_box_container">
                <SelectDateddMMyyyy 
                    label={'Fecha Inicial'}
                    name = {"inicio"}
                    value = {inicio}
                    callBack={handleStartDateChange}
                />
                <SelectDateddMMyyyy 
                    label={'Fecha Final'}
                    name = {"final"}
                    value = {fin}
                    callBack={handleEndDateChange}
                />
                <TextControlNumCol
                    label='Mostrar'
                    value={max}
                    name='max'
                    callBack={(e) => onChange( e, 'max')}
                />
                <SelectVisitador 
                    label='Visitador'
                    name='uid'
                    value={uid}
                    data={visitadores}
                    callBack={(e) => onChange( e, 'uid')}
                />
                <SelectItem
                    label='Cliente'
                    name='cliente'
                    value = {cliente}
                    data={clientesActivos}
                    callBack={(e) => onChange( e, 'cliente')}
                />
                
                <div className='ml-4 mt-3'>
                    <BtnTypeEnter 
                        label='Filtrar'
                        callBack={handleFilter}
                        icon="fa fa-filter"
                        color='yellow'
                        type = 'small'
                    />
                    <BtnTypeEnter 
                        label='Limpiar'
                        callBack={onSubmitClear}
                        icon="fa fa-broom"
                        color='red'
                        type = 'small'
                    />
                    {
                        visitasFilter.length > 0 &&
                        <BtnTypeEnter 
                            label='Archivo'
                            callBack={handleDownloadFile}
                            icon="fa fa-file-csv"
                            color='green'
                            type = 'small'
                        />                    
                    }                            
                </div>
            </div>

            {
                visitasFilter.length > 0 &&
                <TableVisitas visitas={visitasFilter} />
            }

        </div>
    )
}