import { useContext, useState } from "react"
import { Divider } from "@material-ui/core"
import { SelectPais, SelectProvincia, TextControl, SelectCanton, SelectDistrito } from '../../components/ui/atom/FormControls';
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { TableBarrio } from "../../components/tablas/geo/TableBarrio";
import { TitlePage } from "../../components/ui/atom/TitlePage";

export const BarrioScreen = () => {

    const { distrito, barrio, onChange  } = useForm({
        distrito: "",
        barrio: "",
    })

    const {paises, provincias, cantones, distritos, crearBarrios} = useContext(MasterDataContext)
    const [pais, setPais] = useState('')
    const [provincia, setProvincia] = useState('')
    const [canton, setCanton] = useState('')
    const provinciasFilter = provincias.filter(provincia => provincia.pais._id === pais)
    const cantonesFilter = cantones.filter(canton => canton.provincia._id === provincia)
    const distritosFilter = distritos.filter(distrito => distrito.canton._id === canton)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearBarrios({nombre : barrio, distrito});
    }

    const changePais  = (e : string) => {
        setPais(e)
        setProvincia('')
        setCanton('')
        onChange('', 'barrio')
    }

    return (
        <div className="flexbox-container">
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Barrio' />

                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => changePais(e)}
                    data={paises}
                />   
                <SelectProvincia
                    label="Provincia"
                    name='provincia'
                    value={provincia}
                    callBack={(e) => setProvincia(e)}
                    data={provinciasFilter}
                /> 
                <SelectCanton
                    label="Cantn"
                    name='canton'
                    value={canton}
                    callBack={(e) => setCanton(e)}
                    data={cantonesFilter}
                />   
                <SelectDistrito
                    label="Distrito"
                    name='distrito'
                    value={distrito}
                    callBack={(e) => onChange(e, 'distrito')}
                    data={distritosFilter}
                />                                                        
                <TextControl
                    label="Barrio"
                    name='barrio'
                    value={barrio}
                    callBack={(e) => onChange(e, 'barrio')}
                />
            </div>
            <div
                className="col-md-8"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Barrios' />
                <Divider />
                <TableBarrio/>              
            </div>            
        </div>
    )
}

