import { useContext, useEffect } from "react";
import moment from "moment";
import Modal from "react-modal";
import DateTimePicker from "react-datetime-picker";

import { useForm } from "../../hooks/useForm";
import { AppContext } from "../../context/AppContext";
import { AuthContext } from "../../context/AuthContext";
import { CalendarContext } from '../../context/CalendarContext';
import { errorSimpleFire, exitoFire, viewErrors } from "../../components/ui/messagesUI";
import { useParams } from "../../hooks/useParams.";
import { ClienteContext } from '../../context/ClienteContext';
import { MasterDataContext } from '../../context/MasterDataContext';
import { SelectItem, TextControl, TextControlArea } from "../../components/ui/atom/FormControls";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

Modal.setAppElement("#root");

interface Form {
    id: string,
    tipo: string;
    title: string;
    start: any;
    end: any;
    notes: string;
    cliente: string;
  }

export const CalendarModal = () => {
  const {
    state,
    hideModalLoadingCalendar,
  } = useContext(AppContext);

  const {
    addEvento, 
    editEvento, 
    deleteEvento, 
    eventoSeleccionado, 
    purgaEventoSelecciondo
  } = useContext(CalendarContext)

  const { user : userContext } = useContext(AuthContext);
  const { clientes } = useContext(ClienteContext);
  const { caracteristricas, caracteristicasTipo } = useContext(MasterDataContext);
  const clienteActivos = clientes.filter(cliente => cliente.estado)
  const tipoCaracteristica = caracteristicasTipo.filter(tipoCaract => tipoCaract.nombre === 'TIP_EVE')
  const tipoEventos = caracteristricas.filter(caracteristica => caracteristica.tipo._id === tipoCaracteristica[0]._id)
  const { tipoEventoDefault } = useParams()

  const { isLoadingCalendar } = state;

  const now = new Date()
  const initEvent = {
    id: '',
    tipo: tipoEventoDefault,
    title: '',
    start: now,
    end: now,
    notes: '',
    cliente: '',
  }

  const { tipo, start, end, notes, title, cliente, onChange, setForm, form } = useForm<Form>(initEvent)



  useEffect(() => {
    
    if (eventoSeleccionado !== null) {
      setForm({
        id : eventoSeleccionado.id,
        tipo : eventoSeleccionado.tipo._id,
        title : eventoSeleccionado.title,
        start: new Date(eventoSeleccionado.start),
        end: new Date(eventoSeleccionado.end),
        notes : eventoSeleccionado.notes,
        cliente : eventoSeleccionado.cliente ? eventoSeleccionado.cliente._id : "",
      });
    } else {
      setForm({
        ...initEvent,
      });
    }
  }, [eventoSeleccionado]);



  const enabledButtons = () => {
    if (userContext) {
      if (userContext.rol === "ADMIN_ROLE" || userContext.rol === "SUPER_ADMIN_ROLE") {
        return false;
      } else {
        if (userContext.uid !== eventoSeleccionado?.user._id) {
          return false;
        } else {
          return true;
        }
      }
    } else {
      return true;
    }
  };

  const EtiquetaTitulo = () => {
    if (userContext?.rol !== "ADMIN_ROLE" && userContext?.rol !== "SUPER_ADMIN_ROLE") {
      return "Evento";
    } else if (eventoSeleccionado !== null) {
      return "  Editar evento";
    } else {
      return "  Nuevo evento";
    }
  };

  const closeModal = () => {
    hideModalLoadingCalendar();
  };


  const handleStartDateChange = (e) => {
    setForm({
      ...form,
      start: e,
    });
  };
  const handleEndDateChange = (e) => {
    setForm({
      ...form,
      end: e,
    });
  };

  const handleSubmitForm = async (e) => {
    e.preventDefault();
    const momentStart = moment(start);
    const momentEnd = moment(end);
    if (momentStart.isSameOrAfter(momentEnd)) {
      return errorSimpleFire("Fecha fin debe ser mayor a la fecha de inicio");
    }
    if (eventoSeleccionado === null) {
      const resp:any = await addEvento(form);
      if (!resp.ok) {
        return viewErrors(resp);
      }
      exitoFire("Evento Creado");
    } else {
      const resp:any = await editEvento(form.id , form);
      if (!resp.ok) {
        return viewErrors(resp);
      }
      exitoFire("Evento Actializado");
    }
    closeModal();
  };

  const handleDelete = async(e: any) => {
    e.preventDefault();
    const resp:any = await deleteEvento(form.id);
    
    if (!resp.ok) {
      return viewErrors(resp);
    }
    exitoFire("Evento Eliminado");

    hideModalLoadingCalendar();
    purgaEventoSelecciondo();
  };

  return (
    <Modal
      isOpen={isLoadingCalendar}
      onRequestClose={closeModal}
      style={customStyles}
      closeTimeoutMS={200}
      className="modal"
      overlayClassName="modal-fondo"
    >
      <div
        style={{ height: "73vh", overflowY: "scroll", alignItems: "center" }}
      >
        <div style={{ backgroundColor: "#EEEEEE", borderRadius: 5 }}>
          <h1 className="ml-2">{EtiquetaTitulo()}</h1>
        </div>
        <hr />
        <form className="container" onSubmit={handleSubmitForm}>

            
            {/* Fechas */}
          <div className="form-group">
            <label>Fecha y hora inicio</label>
            <DateTimePicker
              onChange={handleStartDateChange}
              value={start}
              className="form-control"
            />
          </div>
          <div className="form-group">
            <label>Fecha y hora fin</label>
            <DateTimePicker
              onChange={handleEndDateChange}
              value={end}
              className="form-control"
            />
          </div>
            {/* Fin de Fechas */}


          <hr />


          <div className="form-group" style={{display:'flex', alignItems:'center'}}>
              <SelectItem
                label = "Tipo"
                name="tipo"
                value = {tipo}
                data={tipoEventos}
                callBack={(e) => onChange(e, "tipo")}
              />
                {
                  eventoSeleccionado?.estado === 'Pendiente' 
                  ?
                  <div className="mt-4 ml-4">
                    <i className={'fa fa-check-circle fa-2x'} style={{color:'red'}} />
                  </div>
                  : 
                  <div className="mt-4 ml-4">
                    {/* <label>{value}</label>  */}
                    <i className={'fa fa-check-circle fa-2x'} style={{color:'green'}}  />
                  </div>              
                }
            </div>

            
            <div className="form-group">
              <SelectItem 
                label = "Cliente"
                name="cliente"
                value = {cliente}
                data={clienteActivos}
                callBack={(e) => onChange(e, "cliente")}
              />
            </div>


          <hr />
          <div className="form-group">
            {/* <label>Titulo y notas</label> */}
            <TextControl 
              label="Ttulo"
              name='title'
              value={title}
              callBack={(e) => onChange(e, "title")}
            />            

          </div>

          <div className="form-group">

            <TextControlArea
              label='Notas'
              name="notes"
              value={notes}
              callBack={(e) => onChange(e, "notes")}
            />


          </div>
          {!enabledButtons() && (
            <div className="divjustificadocentro">

              <button
                disabled={enabledButtons()}
                type="submit"
                className="btn btn-outline-primary btn-block mr-2 mt-lg-1"
              >
                <i className="far fa-save"></i>
                <span> Guardar</span>
              </button>
              <button
                onClick={handleDelete}
                disabled={enabledButtons()}
                className="btn btn-outline-danger btn-block ml-2"
              >
                <i className="far fa-trash-alt"></i>
                <span> Eliminar</span>
              </button>
            </div>
          )}
        </form>
      </div>
    </Modal>
  );
};
