import moment from 'moment';
import { useHistory } from 'react-router-dom';
import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage';
import { useContext } from 'react';
import { AuthContext } from '../../context/AuthContext';
import { SocketContext } from '../../context/SocketContext';
import { useForm } from '../../hooks/useForm';
import { BtnTypeEnter, SelectVisitador } from '../../components/ui/atom/FormControls';
import { InventarioContext } from '../../context/InventarioContext';
import { TransaccionInv } from '../../interfaces/interfaces';
import { createModifiersFromModifierFlags } from 'typescript';
import { useVisitadorPais } from '../../hooks/useVisitadorPais';
    

//TODO  : Implementar descargar el invetnario en un excel

interface Form {
    usuario: string;
    }

export const InventarioTransaccionesScreen = () => {

    const initForm :Form = {
        usuario : '',
    }
   
    const { transacciones, seleccionaTransaccion } = useContext(InventarioContext)
    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)
    const {usuario, onChange, setForm, form } = useForm<Form>(initForm)

    const {visitadores} =  useVisitadorPais()
        
    const transaccionesVisitador = transacciones.filter((item) => item.usuario._id === usuario)
    const history = useHistory();

    const handleViewTransaccion  = (transaccion : TransaccionInv) => {
        seleccionaTransaccion(transaccion)
        history.push("/home/inventariotransaccion");
    }

    return (
        <div className='flexbox-container'>
            <div className='col-md-2'>
                <NavbarLeft />
            </div>
    
            <div
                className='col-md-10'
                style={{ height: '88.5vh', overflowY: 'scroll' }}
            >
                <div>
                    <TitlePage title='Histrico de Transacciones' />
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <SelectVisitador 
                        label='Visitador'
                        name='uid'
                        value={usuario}
                        data={visitadores}
                        callBack={(e) => onChange( e, 'usuario')}
                    />
                    Descargar en excel?     
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <div
                        style={{
                            display:'flex', 
                            flexDirection:'row', 
                            justifyContent:'space-between', 
                            alignItems: 'center'}}                            
                    >
                        <label style={{ flex: 5, fontSize: 18, }}>
                            Detalle
                        </label>
                        <label style={{ flex: 1, fontSize: 18, }}>
                            Fecha
                        </label>
                        <label style={{ flex: 1, fontSize: 18, }}>
                            Aplicado
                        </label>
                        <label style={{ flex: 1, fontSize: 18, }}>
                            Descargado
                        </label>
                    </div>                   
                    <Divider />

                    {
                    transaccionesVisitador.map((item, index) => (
                        <div key={index}>
                            <SpacerHorizontal />
                            <div
                                style={{
                                    display:'flex', 
                                    flexDirection:'row', 
                                    justifyContent:'space-between', 
                                    alignItems: 'center'}}                            
                            >
                                <label style={{ flex: 5, fontSize: 18, }}>
                                    {item.anotaciones}
                                </label>
                                <label style={{ flex: 1, fontSize: 18, }}>
                                    {moment(item.fecha).format("DD-MM-YYYY")}
                                </label>
                                <input  
                                    style={{ flex: 1, fontSize: 18, }}
                                    type="checkbox"
                                    id="cbox1" 
                                    defaultChecked={item.aplicado}
                                    readOnly={true}
                                />
                                <input  
                                    style={{ flex: 1, fontSize: 18, }} 
                                    type="checkbox" 
                                    id="cbox2" 
                                    defaultChecked={item.descargado}
                                    readOnly={true}
                                />
                                <BtnTypeEnter 
                                    label='Ver'
                                    callBack={()=> handleViewTransaccion(item)}
                                    icon="fa fa-eye"
                                    color='orange'
                                    type = 'small'
                                />                                
                            </div>
                            <Divider />                            
                        </div>
                    ))
                }
                </div>
            </div>
        </div>
    )
}