import { useContext } from "react";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelInventario = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/inventario" caption={etiquetaHome} />

          <MenuItem
            route="/home/inventarioadd"
            caption="Cargar Inventario"
            icon="fa fa-charging-station"
          />    
          <MenuItem
            route="/home/inventariosaldos"
            caption="Saldos de Inventario"
            icon="fa fa-dolly-flatbed"
          />                          
          <MenuItem
            route="/home/inventariotransacciones"
            caption="Histrico de Transacciones"
            icon="fa fa-history"
          />                 
        </div>
      );
    default:
      return <PanelVacio />
  }
};
