import { Redirect, Route } from "react-router-dom";

export const PrivateRouter = ({
  isAuthenticated,
  component: Component,
  ...rest
}: any) => {
  return (
    <Route
      {...rest}
      component={(props: any) =>
        isAuthenticated === 'authenticated' ? <Component {...props} /> : <Redirect to="/auth" />
      }
    />
  );
};
