import {  useContext } from "react"
import { Divider } from "@material-ui/core"
import { TablePaises } from "../../components/tablas/geo/TablePaises"
import { TextControl, TextControlNum } from "../../components/ui/atom/FormControls"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const PaisScreen = () => {

    const { pais, codigo, gpsLong, gpsLat, onChange  } = useForm({
        pais: "",
        codigo: "",
        gpsLat: 0,
        gpsLong: 0,
    })

    const {crearPais} = useContext(MasterDataContext)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearPais({nombre : pais, codigo});
    }

    return (
        <div className="flexbox-container">
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Pas' />

                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <TextControl
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => onChange(e, 'pais')}
                />
                <TextControl
                    label="Cdigo"
                    name='codigo'
                    value={codigo}
                    callBack={(e) => onChange(e, 'codigo')}
                />
                <TextControlNum
                    label="Latitud"
                    name='gpsLat'
                    value={gpsLat}
                    callBack={(e) => onChange(e, 'gpsLat')}
                />

                <TextControlNum
                    label="Longitud"
                    name='gpsLong'
                    value={gpsLong}
                    callBack={(e) => onChange(e, 'gpsLong')}
                />                
            </div>
            <div
                className="col-md-8"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Pases' />
                <SpacerHorizontal />
                <Divider />
                <TablePaises/>              
            </div>            
        </div>
    )
}

