import { NavbarLeft } from "../../components/NavbarLeft"
import { Divider } from '@material-ui/core'
import { TableParametros } from "../../components/tablas/params/TableParametros"
import { TableValores } from "../../components/tablas/params/TableValores"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const ParamsScreen = () => {
    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>
            <div
                className="col-md-10"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <TitlePage title='Parmetros del Sistema' />
                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />

                <div style={{flexDirection:'row'}} className="flexbox-container">
                    <div
                        className="col-md-6"
                        style={{ height: "78vh", overflowY: "scroll" }}
                    >
                        <TableParametros />
                    </div>
                    <div
                        className="col-md-6"
                        style={{ height: "78vh", overflowY: "scroll" }}
                    >
                        <TableValores />
                    </div>



                </div>
            </div>            
        </div>
    )
}

