import { useContext } from "react";
import { NavLink, useHistory } from "react-router-dom";
import { AuthContext } from "../../../context/AuthContext";
import defaultUser from "../../../assets/defaultUser.png";
import "../../../css/img.css";
import { BtnExit, BtnTypeEnter } from "./FormControls";

interface Props {
  route: string,
  caption: string
}

export const MenuItemPerfil = ({ route, caption }: Props) => {
  const { user, logOut } = useContext(AuthContext);
  // const nombre = caption.split(" ");
  const style = "nav-item nav-link text-info";

  const history = useHistory();

  const handleLogout = () => {
    history.replace("/auth");
    logOut();
  };


  return (
    <div
      style={{
        flexDirection: "row",
        alignItems: "self-start",
      }}
    >
      <NavLink activeClassName="active" className={style} exact to={route}>
        <img
          alt={user?.nombre}
          src={user?.images ? user.images : defaultUser}
          style={{
            width: "40px",
            height: "40px",
            objectFit: "fill",
            borderRadius: 50,
            marginTop: 5,
            marginBottom: 5,
            marginRight: 10,
          }}
        />
          {caption}

          <BtnExit 
            label='Salir'
            callBack={handleLogout}
            icon="fa fa-sign-out-alt"
            type='small'
          />   

              
      </NavLink>

    </div>
  );
};
