import {  useContext } from "react"
import { Divider } from "@material-ui/core"
import { SelectPais, TextControl } from "../../components/ui/atom/FormControls"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { TableProvincias } from '../../components/tablas/geo/TableProvincias';
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const ProvinciaScreen = () => {

    const { provincia, pais, onChange  } = useForm({
        provincia: "",
        pais: "",
    })

    const {paises, crearProvincia} = useContext(MasterDataContext)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearProvincia({nombre : provincia, pais});
    }

    return (
        <div className="flexbox-container">
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Provincia' />
                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => onChange(e, 'pais')}
                    data={paises}
                />           
                <TextControl
                    label="Provincia"
                    name='provincia'
                    value={provincia}
                    callBack={(e) => onChange(e, 'provincia')}
                />
            </div>
            <div
                className="col-md-8"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Provincias' />
                <Divider />
                <TableProvincias/>              
            </div>            
        </div>
    )
}

