import { useContext } from "react";
import { useHistory } from "react-router";
import { SocketContext } from "../../context/SocketContext";

export const NuevosMensajesFab = () => {
  const { chatNoLeido } = useContext(SocketContext);

  const history = useHistory();
  const handleClickNew = () => {
    history.push("home/notispeak");
  };
  if (chatNoLeido.length === 0) {
    return null;
  }
  const textoToolChat =
    chatNoLeido.length === 1
      ? `${chatNoLeido.length} chat nuevo`
      : `${chatNoLeido.length} chats nuevos`;
  return (
    <div
      onClick={handleClickNew}
      className="tooltip fab-div ml-2 animate__animated animate__lightSpeedInLeft"
      style={{
        backgroundColor: "grey",
        borderRadius: 10,
        opacity: 0.4,
        width: 185,
        display: "flex",
        justifyContent: "center",
        cursor: "pointer",
        zIndex: 1000,
        right: 5,
      }}
    >
      <div>
        <span className="tooltiptext">{textoToolChat}</span>
        <h6>Actividad registrada!!!</h6>
      </div>
    </div>
  );
};
