import { useReducer, useEffect, useContext } from 'react'
import { createContext } from "react"


import AppApi from '../api/AppApi';
import { ChildrenProps, LoginData, LoginResponse, Status, Usuario, RegisterData } from '../interfaces/interfaces';
import { authReducer, AuthState } from '../reducers/authReducer';
import { AppContext } from './AppContext';
import { MessagesContext } from './MessagesContext';

interface UpdateUser {
  nombre?: string,
  correo?: string,
}

type AuthContextProps = {
  errorMessage: string;
  token: string | null;
  user: Usuario | null;
  status: Status;
  tmpImage: string | null;
  singUp: (newUserData: RegisterData) => void;
  singIn: (loginData: LoginData) => void;
  logOut: () => void;
  loadingAutenticate: () => void;
  removeError: () => void;
  nivelBoos: () => boolean;
  nivelAdmin: () => boolean;
  nivelSuperAdmin: () => boolean;
  addImgTmp: (path: string) => void;
  removeImgTmp: () => void;
  updateUser: (data: UpdateUser) => void;
}

const initialState: AuthState = {
  status: 'checking',
  token: null,
  user: null,
  errorMessage: '',
  tmpImage: null
}

export const AuthContext = createContext({} as AuthContextProps);
export const AuthProvider = ({ children }: ChildrenProps) => {

  const [state, dispatch] = useReducer(authReducer, initialState)
  const { registraNotification } = useContext(MessagesContext)
  const { showProcesing , hideProcesing} = useContext(AppContext)

  useEffect(() => {
    checkToken()
  }, [])


  // const updatePerfilUsuario = async (uid, datos, imgActual) => {
  //   const { images, ...resto } = datos;
  //   if (imgActual !== images) {
  //     // await fetchImgConToken(`uploadsimg/usuarios/${uid}`, images, "PUT");
  //     await AppApi.put<PatchUsuarioResponse>(`uploadsimg/usuarios/${uid}`, images);
  //   }
  //   // const resp = await fetchConToken(`usuarios/${uid}`, resto, "PATCH");
  //   const { data } = await AppApi.patch<PatchUsuarioResponse>(`usuarios/${uid}`, resto);
  //   if (data.ok) {
  //     // registraLogin(data, dispatch);
  //   }
  //   return { ok: data.ok, msg: data.msg };
  // };

  // const updatePassUsuario = async (uid, datos) => {
  //   // const resp = await fetchConToken(`usuarios/pass/${uid}`, data, "PATCH");
  //   const { data } = await AppApi.patch<PatchUsuarioResponse>(`usuarios/pass/${uid}`, datos);
  //   if (data.ok) {
  //     logOut();
  //   }
  //   return { ok: data.ok, msg: data.msg };
  // };


  const checkToken = async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      return dispatch({ type: 'notAuthenticated' })
    }
    const resp = await AppApi.get('/auth');
    if (resp.status !== 200) {
      return dispatch({ type: 'notAuthenticated' })
    }
    localStorage.setItem('token', resp.data.token)
    dispatch({
      type: 'singUp',
      payload: {
        user: resp.data.usuario,
        token: resp.data.token
      }
    })
    // conectarSocket()
  }

  const singIn = async ({ correo, password }: LoginData) => {

    try {
      const { data } = await AppApi.post<LoginResponse>('/auth/login', { correo, password });

      localStorage.setItem('token', data.token)
      dispatch({
        type: 'singUp',
        payload: {
          user: data.usuario,
          token: data.token
        }
      })
      // conectarSocket()

    } catch (error: any) {
      if (error.response.data.errors) {
        dispatch({
          type: 'addError',
          // payload : error.response.data.errors[0].msg || 'Informacin Incorrecta'
          payload: ''
        })

        registraNotification({
          title: 'Error',
          msg: error.response.data.errors[0].msg || 'Informacin Incorrecta',
          type: 'error'
        })
      } else {
        dispatch({
          type: 'addError',
          // payload : error.response.data.msg || 'Informacin Incorrecta'
          payload: ''
        })

        registraNotification({
          title: 'Error',
          msg: error.response.data.msg || 'Informacin Incorrecta',
          type: 'error'
        })


      }
    }
  }

  const singUp = async ({ nombre, correo, password, rePassword, pais }: RegisterData) => {

    if (password !== rePassword) {
      dispatch({
        type: 'addError',
        payload: ''
      })
      registraNotification({
        title: 'Error',
        msg: 'Las contraseas deben ser iguales' || 'Informacin Incorrecta',
        type: 'error'
      })
      return
    }

    try {
      const { data } = await AppApi.post<LoginResponse>('/usuarios',
        {
          correo,
          password,
          nombre,
          pais,
          rol: "NEW_USER_ROLE"
        });
      dispatch({
        type: 'singUp',
        payload: {
          user: data.usuario,
          token: data.token
        }
      })
      localStorage.setItem('token', data.token)
    } catch (error: any) {
      if (error.response.data.errors) {
        dispatch({
          type: 'addError',
          payload: ''
        })
        registraNotification({
          title: 'Error',
          msg: error.response.data.errors[0].msg || 'Informacin Incorrecta',
          type: 'error'
        })

      } else {
        dispatch({
          type: 'addError',
          payload: ''
        })
        registraNotification({
          title: 'Error',
          msg: error.response.data.msg || 'Informacin Incorrecta',
          type: 'error'
        })
      }
    }
  }

  const logOut = async () => {
    localStorage.removeItem('token')
    dispatch({ type: 'logout' })
  }

  const removeError = () => {
    dispatch({ type: 'removeError' })
  }

  const loadingAutenticate = () => {
    dispatch({ type: 'loadingAuth' })
  }

  const addImgTmp = (path: string) => {
    dispatch({
      type: 'add-imgTmp',
      payload: path
    })
  }

  const removeImgTmp = () => {
    dispatch({ type: 'remove-imgTmp' })
  }



  // const uploadImage = async (userIde: string, data: ImagePickerResponse) => {

  //     const fileToUpload = {
  //         uri: data.assets![0].uri,
  //         type: data.assets![0].type,
  //         name: data.assets![0].fileName
  //     }

  //     const formData = new FormData()
  //     formData.append('archivo', fileToUpload)

  //     try {
  //         const resp = await AppApi.put(`/uploadsimg/usuarios/${userIde}`, formData);
  //         dispatch({
  //             type: 'registraUsuario',
  //             payload: resp.data.modelo,
  //         })
  //     } catch (error) {
  //         registraNotification({
  //             title: 'Error',
  //             msg: 'No se pudo procesar la fotografa',
  //             type: 'error'
  //         })
  //     }

  // }

  const updateUser = async ({ nombre, correo }: UpdateUser) => {

    const user = state.user
    const data = {
      correo: correo ? correo : user?.correo,
      nombre: nombre ? nombre : user?.nombre,
      rol: user?.rol,
    }

    try {
      showProcesing()
      const resp = await AppApi.put(`/usuarios/${user?.uid}`, data);
      dispatch({
        type: 'registraUsuario',
        payload: resp.data.usuario,
      })
      hideProcesing()
    } catch (error) {
      hideProcesing()
      registraNotification({
        title: 'Error',
        msg: 'No se pudo actualizar la informacin',
        type: 'error'
      })
    }

  }

  const nivelBoos = () => {
    if (state.user?.rol === 'SUPER_ADMIN_ROLE'
      || state.user?.rol === 'ADMIN_ROLE'
      || state.user?.rol === 'JEF_VIS_ROLE') { return true } else { return false }
  }

  const nivelAdmin = () => {
    if (state.user?.rol === 'SUPER_ADMIN_ROLE'
      || state.user?.rol === 'ADMIN_ROLE') { return true } else { return false }
  }

  const nivelSuperAdmin = () => {
    if (state.user?.rol === 'SUPER_ADMIN_ROLE') { return true } else { return false }
  }

  return (
    <AuthContext.Provider value={{
      ...state,
      singUp,
      singIn,
      logOut,
      removeError,
      loadingAutenticate,
      addImgTmp,
      removeImgTmp,
      // uploadImage,
      updateUser,
      nivelBoos,
      nivelAdmin,
      nivelSuperAdmin,
    }}>
      {children}
    </AuthContext.Provider>
  )
}





