// import { Status, Usuario } from '../interfaces/interfaces';

export interface MessagesState {
    errorMessage: Message[];
    notificationMessage: Message[];

}

export interface Message {
    type: 'any_custom_type' | 'success' | 'error' | 'info'
    title: string,
    msg: string,
    tiempo?: number,
}

type MessagesAction =
    | { type: 'addError', payload: Message[] }
    | { type: 'removeError' }
    | { type: 'addNotification', payload: Message[] }
    | { type: 'removeNotification' }

export const messagesReducer = (state: MessagesState, action: MessagesAction): MessagesState => {

    switch (action.type) {

        case 'addError':
            return {
                ...state,
                errorMessage: action.payload
            }
        case 'removeError':
            return {
                ...state,
                errorMessage: []
            }
        case 'addNotification':
            return {
                ...state,
                notificationMessage: action.payload
            }
        case 'removeNotification':
            return {
                ...state,
                notificationMessage: []
            }
        default:
            return state;
    }

}