import { useContext, useEffect } from "react"
import { ClienteContext } from "../../context/ClienteContext"
import { MasterDataContext } from "../../context/MasterDataContext"
// import { file_Download_Clientes } from "../../helpers/file_Download"
import { useForm } from "../../hooks/useForm"
import { Cliente, MapPointer } from "../../interfaces/interfaces"
import { BtnTypeEnter, SelectBarrio, SelectCanton, SelectDistrito, SelectItem, SelectPais, SelectProvincia, TextControl } from "../ui/atom/FormControls"

interface Form {
    clienteNombre: string,
    especialidad : string
    pais: string | undefined,
    provincia: string | undefined,
    canton: string | undefined,
    distrito: string | undefined,
    barrio: string | undefined,
  }

export const ClientesActivosMapFilter = () => {
    const initForm = {
        clienteNombre : "", 
        especialidad: '0',
        pais: undefined,
        provincia: undefined,
        canton: undefined,
        distrito: undefined,
        barrio: undefined,
}

    const { caracteristricas, caracteristicasTipo ,paises, provincias, cantones, distritos, barrios} = useContext(MasterDataContext)
    const { clientes,  cargaMapPointers, purgaMapPointers } = useContext(ClienteContext)

    const tipo = caracteristicasTipo.filter(tipo => tipo.nombre === 'ESPE')
    const especialidades = caracteristricas.filter(espec => espec.tipo._id === tipo[0]._id )
    
    const { 
        clienteNombre, 
        especialidad, 
        pais,
        provincia,
        canton,
        distrito,
        barrio,
        onChange, 
        setForm
    } =  useForm<Form>(initForm)

   
    const provinciasFilter = provincias.filter(provincia => provincia.pais._id === pais)
    const cantonesFilter = cantones.filter(canton => canton.provincia._id === provincia)
    const distritosFilter = distritos.filter(distrito => distrito.canton._id === canton)
    const barriosFilter = barrios.filter(barrio => barrio.distrito._id === distrito)

    const cargaClientesFiltrados  = (filtro : Cliente[]) => {
        const data : MapPointer[] = []
        filtro.map(point => {
            data.push({
                lng: point.gpsLong,
                lat: point.gpsLat,
                id: point._id,
                name: point.nombre,
                description: point.direccion,
                especialidad : point.especialidad.nombre,                
            })
        })
        cargaMapPointers(data)
    }
    

    useEffect(() => {
        
        purgaMapPointers()           
        let filtro: Cliente[] = []

        if (pais && !provincia && !canton && !distrito && !barrio) {
            filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre === 'OPERATIVO'
                && cliente.pais._id === pais                          
            )
            cargaClientesFiltrados(filtro)
        } else if (pais && provincia && !canton && !distrito && !barrio) {
            filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre === 'OPERATIVO'
                && cliente.pais._id === pais                          
                && cliente.provincia._id === provincia                          
            )
            cargaClientesFiltrados(filtro)
        }
        
        if (especialidad !== '0' && clienteNombre.length >= 2) {
            filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre === 'OPERATIVO' 
                && cliente.especialidad._id.includes(especialidad) 
                && cliente.nombre.toUpperCase().includes(clienteNombre.toUpperCase())
            )
            cargaClientesFiltrados(filtro)            
        } else if(especialidad !== '0' && clienteNombre.length < 2) {
            filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre === 'OPERATIVO'
                && cliente.especialidad._id.includes(especialidad)                 
            )
            cargaClientesFiltrados(filtro)            
        } else if(especialidad === '0' && clienteNombre.length >= 2) {
            filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre === 'OPERATIVO'
                && cliente.nombre.toUpperCase().includes(clienteNombre.toUpperCase())               
            )
            cargaClientesFiltrados(filtro)            
        } else {
            filtro = []
            // cargaClientesFiltrados(filtro)            
        }

        //Hacer Aqui el filtro Geogrfico



    }, [clienteNombre, especialidad, pais, provincia])
    
    const onSubmitClear  = () => {
        purgaMapPointers()  
        setForm(initForm)
    }
    // const handleDownloadFile  = () => {
    //     file_Download_Clientes({clientesFiltrados})
    // }
    return (
        <div >
            <div style={{display:'flex', flexDirection:'column', alignItems:'end'}}  >
                <SelectItem
                    label='Esp'
                    name='especialidad'
                    value={especialidad}
                    callBack={(e) => onChange(e, 'especialidad')}
                    data={especialidades}
                /> 
                <TextControl 
                    label='Cliente'
                    name='clienteNombre'
                    value={clienteNombre}
                    callBack={(e) => onChange(e, 'clienteNombre')}
                />
                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => onChange(e, 'pais')}
                    data={paises}
                /> 
                <SelectProvincia
                    label="Provincia"
                    name='provincia'
                    value={provincia}
                    callBack={(e) => onChange(e, 'provincia')}
                    data={provinciasFilter}
                /> 
                <SelectCanton
                    label="Cantn"
                    name='canton'
                    value={canton}
                    callBack={(e) => onChange(e, 'canton')}
                    data={cantonesFilter}
                /> 
                <SelectDistrito
                    label="Distrito"
                    name='distrito'
                    value={distrito}
                    callBack={(e) => onChange(e, 'distrito')}
                    data={distritosFilter}
                />
                <SelectBarrio
                    label="Barrio"
                    name='barrio'
                    value={barrio}
                    callBack={(e) => onChange(e, 'barrio')}
                    data={barriosFilter}
                />
            </div>
            <BtnTypeEnter 
                label='Limpiar'
                callBack={onSubmitClear}
                icon="fa fa-broom"
                color='red'
                type = 'small'
            />

            {/* <button className="btn btn-primary">Ver</button> */}

            {/* <BtnTypeEnter 
                label='Archivo'
                callBack={handleDownloadFile}
                icon="fa fa-file-csv"
                color='green'
                type = 'small'
            /> */}
        </div>
   )
}