import { useContext } from "react";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelVademecum = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);
  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/vademecum" caption={etiquetaHome} />
          <MenuItem
            route="/home/vade"
            caption="Vademecum"
            icon="fa fa-laptop-medical"
          />             
          <MenuItem
            route="/home/vademecumall"
            caption="Lista de Productos"
            icon="fa fa-laptop-medical"
          />                 
        </div>
      );
    default:
      return <PanelVacio />
  }
};
