import { AppState, appStateActions } from "../context/AppContext"

export const appReducer = (state: AppState, action: appStateActions): AppState => {

    switch (action.type) {
        case 'set_drawer_true':
            return {
                ...state,
                drawerActive: true,
            }
        case 'set_drawer_false':
            return {
                ...state,
                drawerActive: false,
            }
            case 'set_isconected':
            return {
                ...state,
                isConected: action.payload,
            }
        case 'set_typeconection':
            return {
                ...state,
                typeConnection: action.payload,
            }
        case 'set_procesing_true':
            return {
                ...state,
                isProcesing: true,
            }
        case 'set_procesing_false':
            return {
                ...state,
                isProcesing: false,
            }
        case 'toogle_update_saldos':
            return {
                ...state,
                updateSaldos: !state.updateSaldos
            }
        case 'showLoadingCalendar':
            return {
                ...state,
                isLoadingCalendar: true,
            };
        
        case 'hideLoadingCalendar':
        return {
            ...state,
            isLoadingCalendar: false,
        };            
        case 'showLoteAdd':
            return {
                ...state,
                isLoadingLoteAdd: true,
            };
        
        case 'hideLoteAdd':
        return {
            ...state,
            isLoadingLoteAdd: false,
        };            

        case 'showProcesing':
            return {
                ...state,
                isLoadingProcesing: true,
            };
        
        case 'hideProcesing':
        return {
            ...state,
            isLoadingProcesing: false,
        }; 

        default:
            return state
    }

}




