import styled from 'styled-components'
import { useTable, usePagination } from 'react-table'
import { useContext, useEffect, useMemo, useState } from 'react'
import { MasterDataContext } from '../../../context/MasterDataContext'


//COMPONENTE PRINCIPAL DE LA TABLA
export const TableCargaTipoCaracteristica = () => {
  //Configuracion de las columnas
  const columns = useMemo(
    () => [
      {
        Header: 'Tipos de Caractersticas',
        columns: [
          {
            Header: 'Nombre',
            accessor: 'nombre',
          },
          {
            Header: '',
            accessor: 'guardar',
          },          
        ],
      },
    ],
    []
  )

  const { 
    caracteristicasTipo, 
    cargaTipoCaracteristicaNewData , 
    limpiaCaracteristicaNewData
  } = useContext(MasterDataContext)
  const [skipPageReset, setSkipPageReset] = useState(false)
  const dataFilter = caracteristicasTipo
  const [data, setData] = useState(dataFilter)

  const updateMyData = (rowIndex, columnId, value) => {
    setSkipPageReset(true)
    setData(old =>
      old.map((row, index) => {
        if (index === rowIndex) {
          return {
            ...old[rowIndex],
            [columnId]: value,
          }
        }
        return row
      })
    )
  }
  
  useEffect(() => {
    const datos = caracteristicasTipo
    setData(datos)
  }, [caracteristicasTipo])


  useEffect(() => {
    setSkipPageReset(false)
  }, [data])

  useEffect(() => {
    cargaTipoCaracteristicaNewData(data)
    return () => {
      limpiaCaracteristicaNewData()
    }      
  }, [data])

  return (
    <Styles>
      <Table
        columns={columns}
        data={data}
        updateMyData={updateMyData}
        skipPageReset={skipPageReset}
      />
    </Styles>
  )
}

// CONSTRUCCION DE LA CELDA
const EditableCell = ({
  value: initialValue,
  row: { index },
  column: { id },
  updateMyData,
}) => {
  const { actualizaTipoCaracteristica } = useContext(MasterDataContext)
  const [value, setValue] = useState(initialValue)

  const onChange = (e) => {
    setValue(e.target.value)
  }

  const onBlur = () => {
    updateMyData(index, id, value)
  }

  const handleSave = () => {
    actualizaTipoCaracteristica(index);
  }

  useEffect(() => {
    setValue(initialValue)
  }, [initialValue])

  switch (id) {
    case 'nombre':
      return <input 
                value={value} 
                onChange={onChange} 
                onBlur={onBlur} 
                style={{ width: 250 }} 
              />
    case 'guardar':
      return <button 
                className='btn btn-outline-dark' 
                onClick={handleSave} 
                style={{ alignItems: 'center' }}
              >
        <div style={{ color: 'blue' }}>
          <i className={`fas fa-save`} />
        </div>
      </button>
    default:
      return <p style={{ width: 300 }}>{value}</p>
  }
}
//gpsLong, gpsLat,
//TIPO DE LA COLUMNA
const defaultColumn = {
  Cell: EditableCell,
}

const Table = ({ columns, data, updateMyData, skipPageReset }) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      autoResetPage: !skipPageReset,
      updateMyData,
    },
    usePagination
  )

  // Render the UI for your table
  return (
    <>

 
      <table {...getTableProps()}>
        <thead>
          {headerGroups.map(headerGroup => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th 
                  style={{ backgroundColor: 'lightgrey' }} 
                  {...column.getHeaderProps()}
                >
                  {column.render('Header')}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row, i) => {
            prepareRow(row)
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map(cell => {
                  return <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                })}
              </tr>
            )
          })}
        </tbody>
      </table>
     {/* Paginacion */}
     <div className="pagination" style={{ justifyContent: 'flex-start', alignItems: 'center' }}>
        <button className='btn btn-dark mr-1' onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
          {'<<'}
        </button>{'     '}
        <button className='btn btn-dark mr-1' onClick={() => previousPage()} disabled={!canPreviousPage}>
          {'< '}
        </button>{'     '}
        <button className='btn btn-dark mr-1' onClick={() => nextPage()} disabled={!canNextPage}>
          {' >'}
        </button>{'     '}
        <button className='btn btn-dark mr-1' onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
          {'>>'}
        </button>{'     '}
        <span>
          Pgina{'     '}
          <strong>
            {pageIndex + 1} de {pageOptions.length}
          </strong>{' '}
        </span>
        <span>
          | Ir a la Pgina:{' '}
          <input
            type="number"
            defaultValue={pageIndex + 1}
            onChange={e => {
              const page = e.target.value ? Number(e.target.value) - 1 : 0
              gotoPage(page)
            }}
            style={{ width: '100px', borderWidth: 1, borderColor: 'black' }}
          />
        </span>{' '}
        <select
          style={{ width: 100 }}
          value={pageSize}
          onChange={e => {
            setPageSize(Number(e.target.value))
          }}
        >
          {[10, 20, 30, 40, 50].map(pageSize => (
            <option key={pageSize} value={pageSize}>
              Ver {pageSize}
            </option>
          ))}
        </select>
      </div>
      {/* Fin de Paginacion */}

    </>
  )
}


// ESTILOS
const Styles = styled.div`
  padding: 1rem;
  table {
    border-spacing: 0;
    border: 0.5px solid black;
    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }
    th,
    td {
      margin: 0;
      padding: 0.2rem;
      border-bottom: 0.5px solid black;
      border-right: 0.5px solid black;
      :last-child {
        border-right: 0;
      }
      input {
        font-size: 1rem;
        padding: 0;
        margin: 0;
        border: 0;
      }
    }
  }
  .pagination {
    padding: 0.2rem;
  }
`