type colors = 'blue' |
              'green' |
              'red' | 
              'yellow' | 
              'purple' | 
              'black' |
              'orange' |
              'grey' |
              'darkblue'
type typeBtn = 'small' | 
              'mid' | 
              'large'


interface BtnProps {
  label : string,
  callBack: () => void,
  icon? : string,
  color? : colors,
  style? : React.CSSProperties
}

interface BtnCustonProps {
  label : string,
  callBack: () => void,
  icon? : string,
  type? : typeBtn,
  color? : colors,
  style? : React.CSSProperties
  disabled? : boolean
  to? : string
}

interface BtnCustonPropsCallBack {
  label : string,
  callBack: (e: any) => void,
  icon? : string,
  type? : typeBtn,
  color? : colors,
  style? : React.CSSProperties
  disabled? : boolean
  to? : string
}


export const BtnEnter = ({
  label, 
  callBack, 
  icon, 
  color = 'blue', 
  style={}
} : BtnProps) => {

  return (
    <button 
      className= {`ml-4 mr-4 mt-3 btn-${color}`}
      onClick={callBack} 
      style={{
        ...style
      }}
    >
      {label}
      {
        icon && (
          <i
            className={`${icon} btn-ico`}
          />
        )
      }
    </button>
  )
}

export const BtnExit = ({
  label, 
  callBack, 
  icon = 'mouse', 
  type = 'mid'
} : BtnCustonProps) => {
  return (
    <button 
      className={`btn-exit-${type} ml-5 mt-1`}
      onClick={callBack}   
    >
      <span>{label}</span>
      <span>
        {icon ? (
          <i
            className={`${icon} btn-ico`}
          />
        ) : (
          <a>Click...!</a>
        )}      
      </span>
    </button>            
  )
}

export const BtnTypeEnter = ({
  label, 
  callBack, 
  icon = 'mouse', 
  type = 'mid', 
  color = 'blue', 
  disabled = false,
  style = {}
} : BtnCustonProps) => {
  return (
    <button 
      className={`btn-${color}-${type} ml-1 mt-1`}
      onClick={callBack}   
      disabled={disabled}
      style={style}
    >
      <span>{label}</span>
      <span>
        {icon ? (
          <i
            className={`${icon} btn-ico`}
          />
        ) : (
          <a>Click...!</a>
        )}      
      </span>
    </button>            
  )
}

