export const BtnTypeEnterInvert = ({
  label, 
  callBack, 
  icon = 'mouse', 
  type = 'mid', 
  color = 'blue', 
  disabled = false,
  style = {}
} : BtnCustonProps) => {
  return (
    <button 
      className={`btn-${color}-${type} ml-1 mt-1`}
      onClick={callBack}   
      disabled={disabled}
      style={style}
    >
      <span>
        {icon ? (
          <i
            className={`${icon} btn-ico`}
          />
        ) : (
          <a>Click...!</a>
        )}      
      </span>
      <span>{label}</span>

    </button>            
  )
}

export const BtnTypeLink = ({
  label, 
  callBack, 
  icon = 'mouse', 
  type = 'mid', 
  color = 'blue', 
  to = ''
} : BtnCustonProps) => {
  return (
    <LinkRouter 
      className={`btn-${color}-${type} ml-1 mt-1`}
      to={`${to}`}
      // to="/home/cliente"
      onClick={callBack}   
    >
      <span>
        {icon ? (
          <i
            className={`${icon} btn-ico`}
          />
        ) : (
          <a>Click...!</a>
        )}      
      </span>
      <span>{label}</span>
    </LinkRouter>            
  )
}

interface MapStyleProps {  
  label : string,
  name : string ,
  value : string ,
  callBack: (value: stylesMap) => void;
}

export const MapStyleControlSelect = ({ label, name, value, callBack }: MapStyleProps) => {
  return (
    <div className="imput_box_container" >
      <label className="imput_box_label">
        {label}:
      </label>
      <select
        className='imput_box_box'
        name={name}
        value={value}
        onChange={(e : any) => callBack(e.target.value)}
      >
        <option value="calles">Normal Plano</option>
        <option value="exteriores">Normal Relieve</option>
        <option value="claro">Ligth</option>
        <option value="oscuro">Dark</option>
        <option value="satelite">Satlite</option>
        <option value="calles+satelite">Satlite + Calles</option>
        <option value="navegacion+dia">Navegacin de Da</option>
        <option value="navegacion+noche">Navegacin de Noche</option>
      </select>
    </div>
  );
};