import { Producto } from '../interfaces/interfaces';

export interface Productostate {
    productos: Producto[],
    productoseleccionado: Producto | null,
    idproductoseleccionado: string | null,
    idcategoriaseleccionada: string | null,
}

type ProductoAction =
    | { type: 'carga-data-productos', payload: Producto[] }
    | { type: 'selecciona-producto', payload: Producto }
    | { type: 'selecciona-id-producto', payload: string }
    | { type: 'seleccionado-producto', payload: string }
    | { type: 'selecciona-categoria', payload: string }
    | { type: 'purga-producto' }
    | { type: 'purga-categoria' }
    | { type: 'add-producto', payload: Producto }
    | { type: 'update-producto', payload: Producto }
    | { type: 'delete-producto' }

export const productoReducer = (state: Productostate, action: ProductoAction) => {

    switch (action.type) {
        case 'carga-data-productos':
            return {
                ...state,
                productos: action.payload
            }
        case 'selecciona-producto':
            return {
                ...state,
                productoseleccionado: action.payload
            }
        case 'selecciona-id-producto':
            return {
                ...state,
                idproductoseleccionado: action.payload
            }
        case 'selecciona-categoria':
            return {
                ...state,
                idcategoriaseleccionada: action.payload
            }
        case 'purga-categoria':
            return {
                ...state,
                idcategoriaseleccionada: null
            }
        case 'purga-producto':
            return {
                ...state,
                productoseleccionado: null
            }
        case 'add-producto':
            return {
                ...state,
                productos: [...state.productos, action.payload]
            }
        case 'update-producto':
            return {
                ...state,
                productos: state.productos.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'delete-producto':
            return {
                ...state,
                productos: state.productos.filter((e) => e._id !== state.idproductoseleccionado)
            }
        default:
            return state;
    }


}