import { Divider } from "@material-ui/core"
import { MasterDataContext } from "../../context/MasterDataContext"
import { NavbarLeft } from "../../components/NavbarLeft"
import { SelectItem, TextControl } from "../../components/ui/atom/FormControls"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { TableCaracteristicas } from "../../components/tablas/params/TableCaracteristicas"
import { useContext } from "react"
import { useForm } from "../../hooks/useForm"
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const CaracteristicasScreen = () => {

    const { nombre, tipo, onChange  } = useForm({
        nombre : "",
        tipo : "",
    })

    const {caracteristicasTipo, crearCaracteristica} = useContext(MasterDataContext)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearCaracteristica({nombre, tipo});
    }

    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>             
            <div
                className="col-md-4"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Caractersticas' />
                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <SelectItem
                    label='Tipo'
                    name='tipo'
                    value={tipo}
                    callBack={(e) => onChange(e, 'tipo')}
                    data={caracteristicasTipo}
                />           
                <TextControl
                    label="Nombre"
                    name='nombre'
                    value={nombre}
                    callBack={(e) => onChange(e, 'nombre')}
                />
            </div>
            <div
                className="col-md-6"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Caractersticas' />
                <Divider />
                <TableCaracteristicas/>              
            </div>            
        </div>
    )
}

