import { useContext, useEffect, useState } from "react"
import { MasterDataContext } from '../context/MasterDataContext';

export const useParams = () => {
    const { param } = useContext(MasterDataContext)

    const [alertaDefault, setAlertaDefault] = useState<string>('')
    const [tipoVisitaDefault, setTipoVisitaDefault] = useState<string>('')
    const [noVistaDefault, setNoVistaDefault] = useState<string>('')
    const [tipoEventoDefault, setTipoEventoDefault] = useState<string>('')
    const [cliEstadoDefault, setCliEstadoDefault] = useState<string>('')
    const [correoSoporte, setCorreoSoporte] = useState<string>('')
    const [paisMatriz, setPaisMatriz] = useState<string>('')

    useEffect(() => {
        SetalertaDefault()
        SetTipoVisitaDefault()
        SetNoVisitaDefault()
        TipoEventoDefault()
        ClieEstadoDefault()
        CorreoSoporteDefault()
        PaisMatriz()
    }, [])

    const CorreoSoporteDefault = () => {
        const alert = param.filter(param => param.parametro === 'correosoporte')
        if (alert.length > 0) {
            setCorreoSoporte(alert[0].valor)
        }
    }
    
    const SetalertaDefault = () => {
        const alert = param.filter(param => param.parametro === 'def_alertavisita')
        if (alert.length > 0) {
            setAlertaDefault(alert[0].valor)
        }
    }

    const SetTipoVisitaDefault = () => {
        const alert = param.filter(param => param.parametro === 'def_tipovisita')
        if (alert.length > 0) {
            setTipoVisitaDefault(alert[0].valor)
        }
    }

    const SetNoVisitaDefault = () => {
        const alert = param.filter(param => param.parametro === 'def_novisita')
        if (alert.length > 0) {
            setNoVistaDefault(alert[0].valor)
        }
    }

    const TipoEventoDefault = () => {
        const alert = param.filter(param => param.parametro === 'def_tipoevento')
        if (alert.length > 0) {
            setTipoEventoDefault(alert[0].valor)
        }
    }

    const ClieEstadoDefault = () => {
        const alert = param.filter(param => param.parametro === 'def_cliestado')
        if (alert.length > 0) {
            setCliEstadoDefault(alert[0].valor)
        }
    }

    const PaisMatriz = () => {
        const alert = param.filter(param => param.parametro === 'paismatriz')
        if (alert.length > 0) {
            setPaisMatriz(alert[0].valor)
        }
    }


    return {
        alertaDefault,
        tipoVisitaDefault,
        noVistaDefault,
        tipoEventoDefault,
        cliEstadoDefault,
        correoSoporte,
        paisMatriz
    }

}



