import noimage from "../../../assets/portadas/noimagen.jpg"

interface Props {
    img?: any
}

export const ImgPortada = ({img} : Props) => {

const imagen = img ? img : noimage

  return (
    <div 
      className="img_portada_box animate__animated animate__fadeIn"
      style={{alignSelf:'center', justifyContent:'center'}} 
    >
        <img alt='portada' src={imagen} 
        style={{
            borderRadius :10,
            position:'absolute', 
            width: '95%', height:'80%',
        }} 
        />
    </div>
  )
}


export const ImgPerfil = ({img} : Props) => {

  const imagen = img ? img : noimage
  
    return (
      <div 
        className="img_perfil_box animate__animated animate__fadeIn"
        style={{
          alignSelf:'center', 
          justifyContent:'center'
        }} 
      >
          <img alt='portada' src={imagen} 
            style={{
                display:'block',
                margin :'auto',
                borderRadius :15,
                height:'100%'
            }} 
          />
      </div>
    )
  }
  
  