import { NavLink } from "react-router-dom";
import "../../../css/img.css";

interface Props {
  route: string,
  caption: string,
  icon?: string
}

export const MenuItem = ({ route, caption, icon }: Props) => {
  const style = "nav-item nav-link ml-2";
  return (
    <div
      style={{
        flexDirection: "row",
        alignItems: "self-start",
        justifyContent: 'center'
      }}
    >
      <NavLink 
        activeClassName="active" 
        className={style} 
        exact to={route} 
        style={{ 
          flexDirection: "row", 
          alignItems: "self-start", 
          justifyContent: 'center',
          // color : '#095CA2'
        }} 
      >
        {icon && (
          <i
            className={`${icon} bigicon mr-2`}
            style={{ color: "#E02729"}}
          />
        )}
        {caption}
      </NavLink>
    </div>
  );
};
