import { useContext } from 'react';
import moment from 'moment';
import { SpacerHorizontal } from '../ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import {  SelectVisitador, TextControlArea } from '../ui/atom/FormControls';
import { AppContext } from '../../context/AppContext';
import { useForm } from '../../hooks/useForm';
import { SocketContext } from '../../context/SocketContext';
import { AuthContext } from '../../context/AuthContext';
import { InventarioContext } from '../../context/InventarioContext';
import { MessagesContext } from '../../context/MessagesContext';
import { InventarioDetalleAdd } from './InventarioDetalleAdd';
import { Separacion } from '../miselaneos/Separacion';
import { useVisitadorPais } from '../../hooks/useVisitadorPais';

interface Form {
    notes: string;
    usuario: string;
  }

  
export const InventarioAdd = () => {

    const initForm :Form = {
        notes : '',
        usuario : '',
    }

    const {showLoteAdd} = useContext(AppContext);
    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)

    const { productosTransaccion, crearTransaccionEntrada, prugaProductoTransaccion } = useContext(InventarioContext)
    const { registraNotification } = useContext(MessagesContext)

    const {visitadores} =  useVisitadorPais()


    const {notes, usuario, onChange, setForm, form } = useForm<Form>(initForm)

    // useEffect(() => {
    //     prugaProductoTransaccion()
    // }, [usuario])

    const handleLoteAdd  = (e) => {
        e.preventDefault()
        showLoteAdd()
    }

    const handleCreate  = (e) => {
        e.preventDefault();
        saveOrUpdate()
    }

    const saveOrUpdate = () => {
        
        if (productosTransaccion.length === 0) {
            registraNotification({
                type: 'error',
                title: 'Inventario',
                msg: 'No hay productos por cargar...!'
            })
        }
        
        const dataSend = {
            fecha: moment(),
            signo: 1,
            anotaciones: `Carga ${notes}`,
            usuario: usuario,
            detalle: productosTransaccion
        }
        
        crearTransaccionEntrada(dataSend)
    }

    
    
    return (
        <div className='flexbox-container'>
            <div
                className='col-md-12'
                style={{ height: '78vh', overflowY: 'scroll' }}
            >
                {/* <form
                    onSubmit={saveOrUpdate}
                > */}

                    <div className='ml-4'>
                        <button 
                            className={`btn-${'green'}-${'mid'} ml-1 mt-1`}
                            onClick={handleCreate}
                        >
                            <span>{"Guardar"}</span>
                            <span>
                                <i
                                    className={`${'fa fa-floppy-o'} btn-ico`}
                                />
                            </span>
                        </button>

                        <button 
                            className={`btn-${'yellow'}-${'mid'} ml-1 mt-1`}
                               onClick={handleLoteAdd}  
                        >
                            <span>{"Crear Lote"}</span>
                            <span>
                                <i
                                    className={`${'fa fa-file'} btn-ico`}
                                />
                            </span>
                        </button>

                    </div>
                    <Separacion />
                    <div style={{display:'flex'}}>
                        <TextControlArea
                            label='Anotaciones'
                            name="notes"
                            value={notes}
                            callBack={(e) => onChange(e, "notes")}
                        />                
                        <SelectVisitador 
                            label='Visitador'
                            name='uid'
                            value={usuario}
                            data={visitadores}
                            callBack={(e) => onChange( e, 'usuario')}
                        />                    
                    </div>
                {/* </form> */}


                <Separacion />
                {(usuario !== "" && usuario !== "0") && (
                    <InventarioDetalleAdd
                        visitador = {usuario}
                    />
                )
            }
            </div>
        </div>
    )
}

