import { createContext, useContext, useEffect, useReducer } from 'react';
import moment from 'moment';
import AppApi from '../api/AppApi';
import { confirmDeleteFire, confirmUpdateFire } from '../components/ui/messagesUI';
import { ChildrenProps, Usuario } from '../interfaces/interfaces';
import { Chat, Notificaciones, NotificationsResponse } from '../interfaces/socketInterfaces';
import { useSocket } from '../hooks/useSocket';
import { socketReducer, SocketState } from '../reducers/socketReducer';
import { AuthContext } from './AuthContext';
import { MessagesContext } from './MessagesContext';

interface DataNewUsuario {
  nombre: string,
  password: string,
  correo: string,
  rol: string,
  pais: string,
}

type SocketsContextProps = {
  usuarios : Usuario[]
  usuariosOnLine: Usuario[],
  usuariosNewData: Usuario[],
  total: number,
  chat: Chat[],
  chatNoLeido: Chat[],
  chatActivo: string | null,
  notificacionesEnviadas: Notificaciones[],
  notificacionesRecibidas: Notificaciones[],
  notificacionActiva: Notificaciones | null,
  usuariosSeleccionados: Usuario[],
  usuariosDisponibles: Usuario[],

  activaChat: (uid?: string) => void;
  enviarMensaje: (mensaje: Chat) => void;
  chatLeido: (mensaje: Chat) => void;
  leidoChat: (mensaje: Chat) => void;
  activaNotifi: (notifi: Notificaciones) => void;
  selecDestinatario: (user: Usuario) => void;
  noSelecDestinatario: (user: Usuario) => void;
  selecAllDestinatarios: () => void;
  purgaAllDestinatarios: () => void;
  setDisponibles: () => void;
  addDisponible: (users: Usuario) => void;
  delDisponible: (users: Usuario) => void;
  purgDisponible: () => void;
  sedNotifications: (title: string, notes: string) => void;
  notiLeida: (notifi: Notificaciones) => void;
  cargaUsuariosNewData: (usuarios: Usuario[]) => void;
  limpiaUsuariosNewData: () => void;
  actualizaUsuario: (index: number) => void;
  eliminaUsuario: (index: number) => void;
  crearUsuario: (usuario: DataNewUsuario) => void;
}

const initialState: SocketState = {
  usuarios: [],
  usuariosOnLine: [],
  usuariosNewData: [],
  total: 0,
  chat: [],
  chatNoLeido: [],
  chatActivo: '',
  notificacionesEnviadas: [],
  notificacionesRecibidas: [],
  usuariosSeleccionados: [],
  notificacionActiva: null,
  usuariosDisponibles: [],
}

export const SocketContext = createContext({} as SocketsContextProps);
export const SocketProvider = ({ children }: ChildrenProps) => {
  const { socket, conectarSocket, desconectarSocket } = useSocket()
  const [state, dispatch] = useReducer(socketReducer, initialState)
  const { status, user, token } = useContext(AuthContext)
  const { registraNotification } = useContext(MessagesContext)


    //USUARIOS
    const registraUsuarios = (usuarios: Usuario[]) => {
      const Usuarios: Usuario[] = usuarios.filter(
        (usuario) => usuario.rol !== "NEW_USER_ROLE" 
      );
      dispatch({
        type: 'carga-usuarios',
        payload: Usuarios
      });

    };

    const registraUsuariosOnline = (usuarios: Usuario[], id: string) => {
      const UsuariosOnLine: Usuario[] = usuarios.filter(
        (usuario) => usuario.rol !== "NEW_USER_ROLE" && usuario.uid !== id,
      );
      dispatch({
        type: 'carga-usuarios-online',
        payload: UsuariosOnLine
      });
    };
  
    const limpiaUsuariosOnline = () => {
      dispatch({
        type: 'purga-usuarios-online'
      });
    };

    
  //LISTENERS

  //CONEXION/DESCONEXION
  useEffect(() => {
    //Conectar el Socket cuando el usuario se logea
    if (status === 'authenticated') {
      registraNotification({
        title: 'Conexin',
        msg: `Se conect al servicio RealTime de Infarma`,
        type: 'success'
      })
      // const token = localStorage.getItem("token");
      conectarSocket(token!);
    }
  }, [status]);

  useEffect(() => {
    //Desconectar el Socket cuando el usuario se desconecta
    if (status !== 'authenticated') {

      registraNotification({
        title: 'Conexin',
        msg: 'Se desconect del servicio RealTime',
        type: 'info'
      })
      desconectarSocket();
      limpiaUsuariosOnline();
    }
  }, [status]);

  useEffect(() => {
    //Escuchar los cambios en los Usuarios Conectados
    if (status === 'authenticated') {
      socket?.on("lista-usuarios", (usuarios: any) => {
        registraUsuariosOnline(usuarios, user!.uid);
        registraUsuarios(usuarios)
      });
    }
  }, [socket]);

  // CHAT
  useEffect(() => {
    //Emitir solicitud cuando se selecciona un chat
    if (status === 'authenticated' && state.chatActivo !== '') {
      const payload = {
        miId: user?.uid,
        mensajesDe: state.chatActivo,
      };
      socket?.emit("obtener-chat", payload);
    } else {
      dispatch({ type: 'purga-chats' })
    }
  }, [state.chatActivo, status])

  useEffect(() => {
    //Escuchar la respuesta de la solicitud cuando se selecicona un chat
    if (status === 'authenticated') {
      socket?.on("obtener-chat", (resp: any) => {
        if (resp.ok) {
          cargarChat(resp.chat);
        }
      });
    }
  }, [socket, status]);

  useEffect(() => {
    //Escuchar los mensajes enviados por otros usuarios
    socket?.on("mensaje-personal", (resp: any) => {
      if (resp.ok) {
        dispatch({
          type: 'reg-msg-chat',
          payload: resp.mensaje
        })
      }
    });
  }, [socket]);

  useEffect(() => {
    socket?.on("chat-leido", (resp: any) => {
      if (resp.ok) {
        leidoChat(resp.mensajes);
      }
    });
  }, [socket]);

  useEffect(() => {
    socket?.on("chat-no-leido", (resp: any) => {
      if (resp.ok) {
        dispatch({
          type: 'no-leidos-msg-chat',
          payload: resp.chatSinLeer
        })

      }
    });
  }, [socket]);

  useEffect(() => {
    if (state.chatNoLeido.length > 0) {
      if (state.chatActivo === null || state.chatActivo === '') {
        registraNotification({
          title: 'Chat',
          msg: 'Tiene actividad en el chat o mensajes sin leer, verifique...!',
          type: 'info'
        })
      }
    }
  }, [state.chatNoLeido,])

  //NOTIFICACIONES
  useEffect(() => {
    socket?.on("tus-notificaciones-todas", (resp: NotificationsResponse) => {
      dispatch({
        type: 'carga-notifis',
        payload: {
          notificacionesEnviadas: resp.notificacionesEnviadas,
          notificacionesRecibidas: resp.notificacionesRecibidas
        },
      });
    });
  }, [socket]);


  useEffect(() => {
    socket?.on("tus-notificaciones-recibidas", (resp: NotificationsResponse) => {
      if (resp.ok) {
        dispatch({
          type: 'carga-notifis-recibidas',
          payload: resp.notificacionesRecibidas
        });
      }
    });
  }, [socket]);

  useEffect(() => {
    socket?.on("tus-notificaciones-enviadas", (resp: NotificationsResponse) => {
      if (resp.ok) {
        dispatch({
          type: 'carga-notifis-enviadas',
          payload: resp.notificacionesEnviadas
        });
      }
    });
  }, [socket]);



  useEffect(() => {
    socket?.on("resp-notifi-leida", (resp: any) => {
      if (resp.ok) {
        updateNotiSelected(resp.notificacion);
      }
    });
  }, [socket]);


  useEffect(() => {
    socket?.on("resp-notifi", (resp: any) => {
      if (resp.ok) {
        registraNotification({
          title: 'Mensaje',
          msg: resp.msg,
          type: 'success'
        })
        purgaAllDestinatarios();
      } else {
        registraNotification({
          title: 'Mensaje',
          msg: resp.msg,
          type: 'error'
        })
      }
    });
  }, [socket]);


  useEffect(() => {
    socket?.on("notifi-personal", (resp: any) => {
      if (resp.ok) {
        registraNotification({
          title: 'Mensaje',
          msg: resp.msg,
          type: 'success'
        })
      }
    });
  }, [socket]);

  //ACCIONES


  //CHAT
  const activaChat = (uid: string = '') => {
    dispatch({
      type: 'activa-chat',
      payload: uid,
    });
  }

  const cargarChat = (chat: Chat[]) => {
    dispatch({
      type: 'carga-chats',
      payload: chat
    })
  }

  const enviarMensaje = (msg: Chat) => {
    socket.emit("mensaje-personal", msg)
  }

  const chatLeido = (msg: Chat) => {
    socket.emit("chat-leido", msg);
  };

  const leidoChat = (chat: Chat) => {
    dispatch({
      type: 'leido-msg-chat',
      payload: chat,
    });
  };

  //NOTIFICACIONES
  const activaNotifi = (notifi: Notificaciones) => {
    dispatch({
      type: 'activa-notifi',
      payload: notifi,
    })
  }

  const selecAllDestinatarios = () => {
    dispatch({
      type: 'carga-all-destino-notifi',
    })
  }

  const purgaAllDestinatarios = () => {
    dispatch({
      type: 'purga-destino-notifi',
    })
  }

  const selecDestinatario = (user: Usuario) => {
    dispatch({
      type: 'carga-destino-notifi',
      payload: user,
    })
  }

  const noSelecDestinatario = (user: Usuario) => {
    dispatch({
      type: 'elimina-destino-notifi',
      payload: user,
    })
  }

  const setDisponibles = () => {
    dispatch({
      type: 'carga-disponibles'
    })
  }
  const addDisponible = (user: Usuario) => {
    dispatch({
      type: 'agregar-disponible',
      payload: user,
    })
  }
  const delDisponible = (user: Usuario) => {
    dispatch({
      type: 'eliminar-disponible',
      payload: user,
    })
  }
  const purgDisponible = () => {
    dispatch({
      type: 'purgar-disponibles',
    })
  }

  const sedNotifications = (title: string, notes: string) => {
    let seleccionados: any = [];
    state.usuariosSeleccionados.map((usuario) =>
      seleccionados.push({ uid: usuario.uid, correo: usuario.correo })
    );
    const data = {
      title: title,
      notes: notes,
      para: seleccionados,
      de: user?.uid,
      fechaenviado: moment(),
    };
    socket.emit("enviar-notificacion", data);
    purgaAllDestinatarios()
    setDisponibles()
  }

  const notiLeida = (notifi: Notificaciones) => {
    socket.emit("notificacion-leida", notifi);
  }

  const updateNotiSelected = (notifi: Notificaciones) => {
    dispatch({
      type: 'actualiza-notifi-activa',
      payload: notifi
    })
  }

  const cargaUsuariosNewData = (usuarios: Usuario[]) => {
    dispatch({
      type: 'carga-usuarios-newdata',
      payload: usuarios
    })
  }

  const limpiaUsuariosNewData = () => {
    dispatch({
      type: 'purga-usuarios-newdata',
    })
  }

  const actualizaUsuario = async (index: number) => {
    const resp = await confirmUpdateFire('Desea actualizar datos del Usuario?')
    if (resp) {
      // const usuarioOriginal = state.usuariosOnLine[index]
      const usuarioNuevo = state.usuariosNewData[index]
      const payload = { usuario: usuarioNuevo }
      socket?.emit("update-usuario", payload);
      socket?.emit("lista-usuarios");
    }
  }

  const eliminaUsuario = async (index: number) => {
    const resp = await confirmDeleteFire('Desea desactivar el Usuario?')
    if (resp) {
      const usuarioDelete = state.usuariosNewData[index]
      const payload = { usuario: usuarioDelete }
      socket?.emit("delete-usuario", payload);
      socket?.emit("lista-usuarios");
    }
  }


  useEffect(() => {
    socket?.on("resultado-usuario", (resp: any) => {
      if (resp.ok) {
        if (resp.ok) {
          registraNotification({
            title: 'Actualizacin de Usuario',
            msg: resp.msg,
            type: 'success'
          })
        } else {
          registraNotification({
            title: 'Actualizacin de Usuario',
            msg: resp.msg,
            type: 'error'
          })
        }
      }
    });
  }, [socket]);


  //"lista-Usuarios"


  const crearUsuario = async (usuario: DataNewUsuario) => {
    try {
      const { data } = await AppApi.post<any>(`/usuarios`, usuario);
      if (data.ok) {
        socket?.emit("lista-usuarios");
        registraNotification({
          title: 'Creacin de Usuario',
          msg: data.msg,
          type: 'success'
        })
      } else {
        registraNotification({
          title: 'Creacin de Usuario',
          msg: data.msg,
          type: 'error'
        })
      }
    } catch (error: any) {
      registraNotification({
        title: 'Creacin de Usuario',
        msg: error.response.data.msg,
        type: 'error'
      })
    }
  }

  //RETURN
  return (
    <SocketContext.Provider value={{
      ...state,
      activaChat,
      enviarMensaje,
      chatLeido,
      leidoChat,
      activaNotifi,
      selecDestinatario,
      noSelecDestinatario,
      selecAllDestinatarios,
      purgaAllDestinatarios,
      setDisponibles,
      addDisponible,
      delDisponible,
      purgDisponible,
      sedNotifications,
      notiLeida,
      cargaUsuariosNewData,
      limpiaUsuariosNewData,
      actualizaUsuario,
      eliminaUsuario,
      crearUsuario,
    }}>
      {children}
    </SocketContext.Provider>

  )

}