import { Evento } from "../interfaces/interfaces";
import { fechaDiff } from "./diasDif";

export const eventsFuturos = (eventos : Evento[]) => {
  const eventosFilter = eventos.filter((item: any) => fechaDiff(item.start) > 0);
  return eventosFilter;
};

export const eventsHoy = (eventos : Evento[]) => {
  const eventosFilter = eventos.filter((item: any) => fechaDiff(item.start) === 0);
  return eventosFilter;
};

export const eventsHoyAndFuturos = (eventos : Evento[]) => {
  const eventosFilter = eventos.filter((item: any) => fechaDiff(item.start) >= 0);
  return eventosFilter;
};

export const eventsPasados = (eventos : Evento[]) => {
  const eventosFilter = eventos.filter((item: any) => fechaDiff(item.start) < 0);
  return eventosFilter;
};

export const DateColorEvent = (date) => {
  const diffDays = fechaDiff(date);
  if (diffDays < 0) {
    return "#FFD8E1";
  }
  if (diffDays > 0) {
    return "#CDEBFF";
  }
  return "#eeeeee";
};
