import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage';
import { VisitasFilterOptions } from '../../components/visitas/VisitasFilterOptions';
    
export const VisitasHistoricoScreen = () => {
    return (
        <div className='flexbox-container'>
           <div className='col-md-2'>
               <NavbarLeft />
           </div>
    
           <div
               className='col-md-10'
               style={{ height: '88.5vh', overflowY: 'scroll' }}
           >
               <div>
                   <TitlePage title='Histrico de Visitas' />
                   <SpacerHorizontal />
                   <Divider />
                   <VisitasFilterOptions />
               </div>
           </div>
       </div>
   )
}