import { useContext } from "react";
import { Divider } from "@material-ui/core";

import { MenuItem } from "../ui/atom/MenuItem";
import imgFB from '../../assets/FB.png';
import imgInst from "../../assets/instagram.png";
import imgEMM from "../../assets/logo1.png";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";
import { ExternalLink } from "../ui/atom/FormControls";
import { SpacerHorizontal } from "../ui/atom/Spacer";
import { UsuariosOnline } from "../../pages/admin/UsuariosOnline";

interface Props {
  etiquetaHome: string
}

export const PanelHome = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "VIS_USER_ROLE":
    case "JEF_VIS_ROLE":
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse" style={{ height: "88.5vh", overflowY: "scroll" }}>
          <MenuItem route="/home" caption={etiquetaHome} />

          <Divider />
          <ExternalLink
            url="https://www.instagram.com/laboratorioinfarma/"
            img={imgInst}
            label="Instagram"
          />
          <ExternalLink
            url="https://www.facebook.com/Infarmamedicamentos"
            img={imgFB}
            label="Facebook"
          />
          <ExternalLink
            url="https://infarmamedicamentos.com"
            img={imgEMM}
            label="Sitio WEB"
          />
          <SpacerHorizontal />

          <MenuItem
            route="/home/notisends"
            caption="Mensajes"
            icon="fa fa-envelope-open-text"
          />
          <MenuItem
            route="/home/notisends"
            caption="Chat"
            icon="fa fa-comments"
          />
          <SpacerHorizontal />
          <Divider />

          <UsuariosOnline />

        </div>
      );
    default:
      return <PanelVacio />
  }
};
