import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { ImgPortada } from '../../components/ui/atom/ImgPortada';
import { TitlePage } from '../../components/ui/atom/TitlePage';

export const ClientesScreen = () => {

    const portada = "https://res.cloudinary.com/desm1rvpy/image/upload/v1641503440/Infarma/logo/clientes_lhryxt.jpg"

    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>

            <div
                className="col-md-10"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <div>
                    <TitlePage title='Panel de Clientes' />
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <ImgPortada img={portada} />
                </div>
            </div>
        </div>
    )
}
