import { Divider } from "@material-ui/core";
import { useContext, useEffect, useState } from "react";
import {
  TextControl,
  TextControlPass,
} from "../../components/ui/atom/FormControls";
import { AuthContext } from "../../context/AuthContext";
import FileUpload from "../../helpers/fileUpload";
import defaultUser from "../../assets/defaultUser.png";
// import { AppContext } from "../../context/AppContext";
import { useForm } from "../../hooks/useForm";
import { TitlePage } from "../../components/ui/atom/TitlePage";

export const PerfilPage = () => {
  const { user, } = useContext(
    AuthContext
  );
  // updatePerfilUsuario, updatePassUsuario

  // const { showModalLoading, hideModalLoading } = useContext(AppContext);

  const passInit = {
    passActual: "",
    passNew: "",
    rePassNew: "",
  };
  const [imgChange, setImgChange] = useState(false);
  const [passChange, setPassChange] = useState(false);
  const [dataChange, setDataChange] = useState(false);
  const [dataPassChange, setDataPassChange] = useState(false);
  const { form, onChange, setForm } = useForm({ correo: "", images: "", name: "", uid: "" });
  const { form: passValues, onChange: onChangePass } = useForm(passInit);


  useEffect(() => {
    if (
      correo !== form.correo ||
      name !== form.name ||
      images !== form.images
    ) {
      setDataChange(true);
    } else {
      setDataChange(false);
    }
  }, [form]);

  useEffect(() => {
    if (
      passValues.passActual &&
      passValues.passNew &&
      passValues.rePassNew &&
      passValues.passNew === passValues.rePassNew
    ) {
      setDataPassChange(true);
    } else {
      setDataPassChange(false);
    }
  }, [passValues]);

  useEffect(() => {
    if (user) {
      setForm({
        correo: user.correo,
        images: user.images,
        name: user.nombre,
        uid: user.uid,
      })
    }
  }, [user])

  if (!user) return null

  const { correo, images, nombre: name } = user;


  const updateFilesCb = (e) => {
    setForm({
      ...form,
      images: e,
    });
  };

  const handlePerfilSave = async (e) => {
    e.preventDefault();
    // showModalLoading();
    console.log('Save Perfil');

    // const resp = await updatePerfilUsuario(uid, form, images);
    // if (resp.ok) {
    //   exitoFire("Perfil actualizado...!");
    // } else {
    //   viewErrors(resp);
    // }
    // hideModalLoading();
  };

  const handlePassChange = async (e) => {
    e.preventDefault();
    // showModalLoading();
    console.log('Save Password');

    // const resp = await updatePassUsuario(uid, passValues);
    // if (resp.ok) {
    //   exitoFire("Contrasea cambiada, debe iniciar sesin de nuevo...!", 3000);
    // } else {
    //   viewErrors(resp);
    // }

    // hideModalLoading();
  };

  return (
    <div className='contaner_screen'>
      <div className="box_izq_25">
        <img
          src={form.images ? form.images : defaultUser}
          className="img-thumbnail animate__animated animate__fadeInDown mt-2 mb-2"
          alt={name}
        />
        <button
          className="btn btn-outline-warning mb-2 col-md-12"
          onClick={() => setPassChange(!passChange)}
        >
          Cambiar Contrasea
        </button>
        {!passChange && (
          <>
            <button
              className="btn btn-outline-warning mb-2 col-md-12"
              onClick={() => setImgChange(!imgChange)}
            >
              Cambiar Avatar
            </button>
            <button
              className={
                !dataChange
                  ? "btn btn-outline-primary col-md-12"
                  : "btn btn-primary col-md-12"
              }
              onClick={handlePerfilSave}
              disabled={!dataChange}
            >
              Actualizar Perfil
            </button>
          </>
        )}

      </div>
      <div className="box_cent_50" >
        <TitlePage title='Perfil de Usuario' />
        <Divider />
        <TextControl
          label={"Nombre*"}
          name={"name"}
          value={form.name}
          callBack={(e) => onChange(e, "name")}
        />
        <TextControl
          label={"Correo*"}
          name={"correo"}
          value={form.correo}
          callBack={(e) => onChange(e, "correo")}
        />

        <div
          className="mt-4"
          style={{
            flexDirection: "column",
            display: "flex",
            alignItems: "stretch",
          }}
        >

          {passChange && (
            <>
              <Divider />
              <h4 className="mt-2 mb-2">Cambio de Contrasea</h4>
              <TextControlPass
                label={"Contrasea Actual"}
                name={"passActual"}
                value={passValues.passActual}
                callBack={(e) => onChangePass(e, "passActual")}
              />
              <Divider  />
              <h6 className="mt-2 mb-2">Contraseas deben ser iguales</h6>

              <TextControlPass
                label={"Nueva Contrasea"}
                name={"passNew"}
                value={passValues.passNew}
                callBack={(e) => onChangePass(e, "passNew")}
              />
              <TextControlPass
                label={"Repita Contrasea"}
                name={"rePassNew"}
                value={passValues.rePassNew}
                callBack={(e) => onChangePass(e, "rePassNew")}
              />
              <button
                className={
                  dataPassChange
                    ? "btn btn-primary mt-2 col-md-4"
                    : "btn btn-outline-primary mt-2 col-md-4"
                }
                onClick={handlePassChange}
                disabled={!dataPassChange}
              >
                Actualizar Contrasea
              </button>
            </>
          )}
          {imgChange && (
            <div className="col-md-4">
              <FileUpload
                updateFilesCb={updateFilesCb}
                accept=".jpg,.png,.jpeg,.bmp"
                label="Foto de Perfil"
              />
            </div>
          )}

        </div>
      </div>
      <div className="box_der_25" />

    </div>
  );
};
