import { createContext, useReducer } from "react";
import { appReducer } from "../reducers/appReducer";

export type appStateActions =
    | { type: 'set_drawer_true' }
    | { type: 'set_drawer_false' }
    | { type: 'set_isconected', payload: boolean }
    | { type: 'set_typeconection', payload: string }
    | { type: 'set_procesing_true' }
    | { type: 'set_procesing_false' }
    | { type: 'toogle_update_saldos' }
    | { type: 'showLoadingCalendar'}
    | { type: 'hideLoadingCalendar'}
    | { type: 'showLoteAdd'}
    | { type: 'hideLoteAdd'}
    | { type: 'showProcesing'}
    | { type: 'hideProcesing'}

export interface AppState {
    drawerActive: boolean;
    isConected: boolean;
    typeConnection: string | null;
    isProcesing: boolean;
    updateSaldos: boolean;
    isLoadingCalendar: boolean;
    isLoadingLoteAdd: boolean;
    isLoadingProcesing: boolean;
}

export const appInitialState: AppState = {
    drawerActive: false,
    isConected: false,
    typeConnection: null,
    isProcesing: false,
    updateSaldos: true,
    isLoadingCalendar : false,
    isLoadingLoteAdd : false,
    isLoadingProcesing : false,
};


interface AppContextProps {
    state: AppState;
    activeDrawerTrue: () => void;
    activeDrawerFalse: () => void;
    setIsConected: (state: boolean) => void;
    setTypeConection: (type: string) => void;
    procesingTrue: () => void;
    procesingFalse: () => void;
    toogleUpdateSaldos: () => void;
    showModalLoadingCalendar : () => void;
    hideModalLoadingCalendar : () => void;
    showLoteAdd : () => void;
    hideLoteAdd : () => void;
    showProcesing : () => void;
    hideProcesing : () => void;                
}

export const AppContext = createContext({} as AppContextProps)
export const AppProvider = ({ children }: any) => {

    const [state, dispatch] = useReducer(appReducer, appInitialState)

    // useEffect(() => {
    //     const unsubscribe = NetInfo.addEventListener(state => {
    //         if (state.isConnected) {
    //             setIsConected(true)
    //             setTypeConection(state.type)
    //         } else {
    //             setIsConected(false)
    //             setTypeConection('')
    //         }
    //     });
    //     return () => {
    //         unsubscribe();
    //     }
    // }, [])

    const toogleUpdateSaldos = () => {
        dispatch({
            type: 'toogle_update_saldos'
        })
    }

    const activeDrawerTrue = () => {
        dispatch({
            type: 'set_drawer_true'
        })
    }
    const activeDrawerFalse = () => {
        dispatch({
            type: 'set_drawer_false'
        })
    }
    const procesingTrue = () => {
        dispatch({
            type: 'set_procesing_true'
        })
    }
    const procesingFalse = () => {
        dispatch({
            type: 'set_procesing_false'
        })
    }
    const setIsConected = (isconected: boolean) => {
        dispatch({
            type: 'set_isconected',
            payload: isconected
        })
    }
    const setTypeConection = (type: string) => {
        dispatch({
            type: 'set_typeconection',
            payload: type
        })
    }
    const showModalLoadingCalendar = () => {
        dispatch({
          type: 'showLoadingCalendar',
        });
      };
      const hideModalLoadingCalendar = () => {
        dispatch({
          type: 'hideLoadingCalendar',
        });
      };

      const showLoteAdd = () => {
        dispatch({
          type: 'showLoteAdd',
        });
      };
      const hideLoteAdd = () => {
        dispatch({
          type: 'hideLoteAdd',
        });
      };

      const showProcesing = () => {
        dispatch({
          type: 'showProcesing',
        });
      };
      const hideProcesing = () => {
        dispatch({
          type: 'hideProcesing',
        });
      };      
      
      



    return (
        <AppContext.Provider value={{
            state,
            activeDrawerTrue,
            activeDrawerFalse,
            setIsConected,
            setTypeConection,
            procesingTrue,
            procesingFalse,
            toogleUpdateSaldos,
            showModalLoadingCalendar,
            hideModalLoadingCalendar,
            showLoteAdd,
            hideLoteAdd,
            showProcesing,
            hideProcesing,           
        }}>
            {children}
        </AppContext.Provider>
    )

}