import { useContext, useReducer, useEffect } from 'react';
import { createContext } from 'react';
import AppApi from '../api/AppApi';
import { ChildrenProps, Evento, CalendarResponse, DataSendCalendar, DataFilterProps } from '../interfaces/interfaces';
import { calendarReducer, CalendarState } from '../reducers/calendarReducer';
import { AuthContext } from './AuthContext';
import moment from 'moment';
import { createCalendarDate } from '../helpers/diasDif';
import { AppContext } from './AppContext';



type CalendarContextProps = {
    eventos: Evento[],
    eventosToDay: Evento[],
    eventosProximos: Evento[],
    eventosFilter: Evento[],
    eventosSeleccionados: Evento[],
    eventoSeleccionado: Evento | null,
    eventosIndexFilter: number | null,
    diaSeleccionado: string,
    cargaEventos: () => void;
    cargaEventosUsuario: (uid: string) => void;
    addEvento: (data: DataSendCalendar) => void;
    editEvento: (id: string, data: DataSendCalendar) => void;
    deleteEvento: (id: string) => void;
    seleccionaDia: (dia: string) => void;
    eventosDay: (dia?: string) => void;
    editEstadoEvento: (id: string, estado: string) => void;
    cargaDataFilter: (Props : DataFilterProps) => void;
    selecEvento : (evento : Evento) => void;
    purgaEventoSelecciondo : () => void;    
    purgaDataFilter : () => void;
    setDataFilterIndex: (index : number) => void;
    clearDataFilterIndex: () => void;    
}

const initialState: CalendarState = {
    eventos: [],
    eventosToDay: [],
    eventoSeleccionado: null,
    eventosSeleccionados: [],
    eventosProximos: [],
    eventosFilter: [],
    eventosIndexFilter : null,
    diaSeleccionado: moment().format('YYYY-MM-DD').toString(),
}

const limite = 500;

export const CalendarContext = createContext({} as CalendarContextProps)
export const CalendarProvider = ({ children }: ChildrenProps) => {

    const [state, dispatch] = useReducer(calendarReducer, initialState)
    const { status } = useContext(AuthContext)
    const { showProcesing , hideProcesing} = useContext(AppContext)
    const cargaEventos = async () => {
        const { data } = await AppApi.get<CalendarResponse>
            (`/calendario?limite=${limite}&desde=0`);
        dispatch({
            type: 'carga-data-eventos',
            payload: data.eventos
        })
        eventosDay()
        eventosFututos(data.eventos)
        eventosToDay(data.eventos)
    }

    useEffect(() => {
        if (status === 'authenticated') {
            cargaEventos()
        }
    }, [status])

    useEffect(() => {
        if (state.eventosIndexFilter !== null) {
            selecEvento(state.eventosFilter[state.eventosIndexFilter])
        }
    }, [state.eventosIndexFilter])

    const cargaEventosUsuario = async (uid: string) => {
        const { data } = await AppApi.get<CalendarResponse>
            (`/calendario/${uid}`);
        if (data) {
            return {
                ok: true,
                msg: 'Datos recuperados con xito',
                eventos: data
            }
        } else {
            return {
                ok: false,
                msg: 'No hay datos',
                eventos: []
            }
        }
    }

    const seleccionaDia = (dia: string) => {
        dispatch({
            type: 'selecciona-dia',
            payload: dia
        })
        eventosDay(dia)
    }

    const eventosDay = (dia?: string) => {
        const { eventos } = state
        const fechaEval = dia
            ? toMs(dia)
            : toMs(moment().format('YYYY-MM-DD'))
        const eventosTem: Evento[] = eventos.filter((event) => {
            return toMs(moment(event.start).format('YYYY-MM-DD')) <= fechaEval
                && toMs(moment(event.end).format('YYYY-MM-DD')) >= fechaEval
        })
        dispatch({
            type: 'add-eventosSeleccionados',
            payload: eventosTem
        })
    }


    const eventosFututos = (eventos: Evento[]) => {

        const fechaEval = toMs(moment().format('YYYY-MM-DD'))
        const eventosTem: Evento[] = eventos.filter((event) => {
            return toMs(moment(event.end).format('YYYY-MM-DD')) >= fechaEval
        })
        dispatch({
            type: 'add-eventosProximos',
            payload: eventosTem
        })
    }

    const eventosToDay = (eventos: Evento[]) => {

        const fechaEval = toMs(moment().format('YYYY-MM-DD'))
        const eventosTem: Evento[] = eventos.filter((event) => {
            return toMs(moment(event.start).format('YYYY-MM-DD')) <= fechaEval
                && toMs(moment(event.end).format('YYYY-MM-DD')) >= fechaEval
        })
        dispatch({
            type: 'add-eventosToDay',
            payload: eventosTem
        })
    }

    const toMs = (dateStr: any) => {
        const [year, month, day] = dateStr.split("-")
        return new Date(year, month - 1, day).getTime()
    }

    const selecEvento  = (evento : Evento) => {
        dispatch({
            type: 'selecciona-evento',
            payload: evento
        })        
    }

    const purgaEventoSelecciondo  = () => {
        dispatch({
            type: 'purga-evento',
        })        
    }

    //CRUD

    const addEvento = async (dataAdd: DataSendCalendar) => {

        const startDate = createCalendarDate(dataAdd.start)
        const endDate = createCalendarDate(dataAdd.end)
        const dif = endDate.diff(startDate, 'minutes')

        if (dif < 0) {
            return {
                ok: false,
                msg: 'Fechas no vlidas'
            }
        }
        let dataSend
        if (dataAdd.cliente !== '') {
            dataSend = {
                title: dataAdd.title,
                notes: dataAdd.notes,
                tipo: dataAdd.tipo,
                start: startDate,
                end: endDate,
                cliente: dataAdd.cliente
            }
        } else {
            dataSend = {
                title: dataAdd.title,
                notes: dataAdd.notes,
                tipo: dataAdd.tipo,
                start: startDate,
                end: endDate,
            }
        }
        showProcesing()
        try {
            const { data } = await AppApi.post('/calendario', dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaEventos()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Evento registrado en el Calendario'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }

    const editEvento = async (id: string, dataEdit: DataSendCalendar) => {

        const startDate = createCalendarDate(dataEdit.start)
        const endDate = createCalendarDate(dataEdit.end)
        const dif = endDate.diff(startDate, 'minutes')


        if (dif < 0) {
            return {
                ok: false,
                msg: 'Fechas no vlidas'
            }
        }
        let dataSend
        if (dataEdit.cliente !== '') {
            dataSend = {
                title: dataEdit.title,
                notes: dataEdit.notes,
                tipo: dataEdit.tipo,
                start: startDate,
                end: endDate,
                cliente: dataEdit.cliente
            }
        } else {
            dataSend = {
                title: dataEdit.title,
                notes: dataEdit.notes,
                tipo: dataEdit.tipo,
                start: startDate,
                end: endDate,
            }
        }        
        try {
            const { data } = await AppApi.put(`/calendario/${id}`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaEventos()
                return {
                    ok: true,
                    msg: 'Evento Actualizado'
                }
            } else {
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }

    }

    const editEstadoEvento = async (id: string, estado: string) => {
        try {
            const { data } = await AppApi.put(`/calendario/${id}`, { estado });
            if (data.ok) {
                //Si todo sale bien
                await cargaEventos()
                return {
                    ok: true,
                    msg: 'Evento Actualizado'
                }
            } else {
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }

    const deleteEvento = async (id: string) => {
        try {
            const { data } = await AppApi.put(`/calendario/${id}`, { activo : false });
            if (data.ok) {
                //Si todo sale bien
                await cargaEventos()
                return {
                    ok: true,
                    msg: 'Evento Eliminado'
                }
            } else {
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }



    const cargaDataFilter = async ({ inicio, fin, uid, max } : DataFilterProps) => {
        const start = moment(inicio).format('DD-MM-YYYY').toString()
        const end = moment(fin).format('DD-MM-YYYY').toString()
        const { data } = await AppApi.get<CalendarResponse>
          (`/calendario/${uid}?inicio=${start}&final=${end}&limite=${max}&orden=asc`);
        if (data) {
            dispatch({
                type: 'add-eventosFilter',
                payload: data.eventos
            })            
        } else {
            dispatch({
                type: 'remove-eventosFilter',
            })
        }
      }

    const purgaDataFilter  = () => {
        dispatch({
            type: 'remove-eventosFilter',
        })    
    }


    const setDataFilterIndex  = (index :  number) => {
        dispatch({
            type: 'set-eventosIndexFilter',
            payload : index
        })    
    }

    const clearDataFilterIndex  = () => {
        dispatch({
            type: 'clear-eventosIndexFilter',
        })    
    }



    return (
        <CalendarContext.Provider value={{
            ...state,
            cargaEventos,
            seleccionaDia,
            eventosDay,
            addEvento,
            editEvento,
            deleteEvento,
            editEstadoEvento,
            cargaEventosUsuario,
            selecEvento,
            purgaEventoSelecciondo,   
            cargaDataFilter,
            purgaDataFilter,
            setDataFilterIndex,
            clearDataFilterIndex,            
        }}>
            {children}
        </CalendarContext.Provider>
    )
}


