import moment from 'moment';
import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { useContext } from 'react';
import { VisitaContext } from '../../context/VisitaContext';
import { TextControl, TextControlArea } from '../../components/ui/atom/FormControls';
import { MapVisitaContainer } from '../../components/visitas/MapVisitaContainer';
import { MapPointer } from '../../interfaces/interfaces';
import { TransaccionScreen } from '../../components/inventario/TransaccionScreen';
    
export const VisitaScreen = () => {

    const {visitasFilter, visitasIndexFilter} = useContext(VisitaContext)


    if (visitasIndexFilter == null) {
        return (
            <h1>Sin Datos</h1>
        )
    }

    const {cliente, fecha, tipo, novisita, alerta, anotaciones, gpsLong, gpsLat, _id} = visitasFilter[visitasIndexFilter];
    const fechaVisita = moment(fecha).format("DD-MM-YYYY | HH:MM")
    const mapPointers : MapPointer = {
        description : anotaciones,
        especialidad : '',
        id : _id,
        lat : gpsLong,
        lng : gpsLat,
        name: cliente.nombre
    }
    return (
        <div className='flexbox-container'>
           <div className='col-md-2'>
               <NavbarLeft />
           </div>
    
           <div
               className='col-md-10'
               style={{ height: '88.5vh', overflowY: 'scroll' }}
           >
               <div>
                   <TitlePage title='Visita' />
                   <SpacerHorizontal />
                   <Divider />
                   <SpacerHorizontal />

                    <div style={{display: 'flex', flexDirection:'column'}}>
                        <h2>{cliente.nombre}</h2>
                        <hr/>
                        <span>{`Fecha: ${fechaVisita}`}</span>
                        <br/>
                        <span>{`Tipo: ${tipo.nombre}`}</span>
                        <br/>
                        <span>{`Estatus: ${novisita.nombre}`}</span>
                        <br/>
                        <span>{`Alerta: ${alerta.nombre}`}</span>
                        <br/>
                        <TextControlArea label='Observaciones' name='obervaciones' value={anotaciones} callBack={() =>{}} />
                        <br/>


                    <TitlePage title='Localiacin del Registro' />
                    <br/>
                    <MapVisitaContainer 
                        mapPointers = {[mapPointers]}
                    />
                    <br/>
                    <br/>
                    <br/>
                    
                    <TransaccionScreen idVisita={_id} />
                    </div>

               </div>
           </div>
       </div>
   )
}