
import { useContext, useEffect } from "react"
import { ClienteContext } from "../../context/ClienteContext"
import { MasterDataContext } from "../../context/MasterDataContext"
import { file_Download_Clientes } from "../../helpers/file_Download"
import { useForm } from "../../hooks/useForm"
import { BtnTypeEnter, SelectItem, TextControl } from "../ui/atom/FormControls"

interface Form {
    clienteNombre: string,
    especialidad : string
  }

export const ClientesInactivosFilter = () => {
    const initForm = {clienteNombre : "", especialidad: '0'}

    const { caracteristricas, caracteristicasTipo } = useContext(MasterDataContext)
    const { clientes, cargaClientesFiltrados, clientesFiltrados } = useContext(ClienteContext)
    const tipo = caracteristicasTipo.filter(tipo => tipo.nombre === 'ESPE')
    const especialidades = caracteristricas.filter(espec => espec.tipo._id === tipo[0]._id )
    
    const { clienteNombre, especialidad, onChange, setForm} = useForm<Form>(initForm)
    
    useEffect(() => {
        if (especialidad !== '0' && clienteNombre.length >= 2) {
            const filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre !== 'OPERATIVO' 
                && cliente.especialidad._id.includes(especialidad) 
                && cliente.nombre.toUpperCase().includes(clienteNombre.toUpperCase())
            )
            cargaClientesFiltrados(filtro)
        } else if(especialidad !== '0' && clienteNombre.length <= 2) {
            const filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre !== 'OPERATIVO'
                && cliente.especialidad._id.includes(especialidad)                 
            )
            cargaClientesFiltrados(filtro)
        } else if(especialidad === '0' && clienteNombre.length >= 2) {
            const filtro = clientes.filter(cliente => 
                cliente.causabaja.nombre !== 'OPERATIVO'
                && cliente.nombre.toUpperCase().includes(clienteNombre.toUpperCase())               
            )
            cargaClientesFiltrados(filtro)
        } else {
            const filtro = clientes.filter(cliente => cliente.causabaja.nombre !== 'OPERATIVO')
            cargaClientesFiltrados(filtro)            
        }
    }, [clienteNombre, especialidad])
    
    const onSubmitClear  = () => {
        const filtro = clientes.filter(cliente => cliente.causabaja.nombre !== 'OPERATIVO')
        cargaClientesFiltrados(filtro)
        setForm(initForm)
    }
    const handleDownloadFile  = () => {
        file_Download_Clientes({clientesFiltrados})
    }
    return (
        <div style={{display:'flex'}}>
            <SelectItem
                label='Especialidad'
                name='especialidad'
                value={especialidad}
                callBack={(e) => onChange(e, 'especialidad')}
                data={especialidades}
            /> 
            <TextControl 
                label='Cliente'
                name='clienteNombre'
                value={clienteNombre}
                callBack={(e) => onChange(e, 'clienteNombre')}
            />
            <div className="ml-4 mb-2">
                <BtnTypeEnter 
                    label='Limpiar'
                    callBack={onSubmitClear}
                    icon="fa fa-broom"
                    color='red'
                    type = 'small'
                />
                <BtnTypeEnter 
                    label='Archivo'
                    callBack={handleDownloadFile}
                    icon="fa fa-file-csv"
                    color='green'
                    type = 'small'
                />                               
            </div>
        </div>
   )
}