import { useContext } from 'react';
import { ProductoContext } from '../../context/ProductoContext';
import { ProductoItem } from './ProductoItem';
    
export const ProductosList = () => {

    const { idcategoriaseleccionada, productos } = useContext(ProductoContext)
    const productosFiltrados = productos.filter(producto => producto.categoria._id === idcategoriaseleccionada 
                                                            && producto.original)
    
    return (
        <div
            className='col-md-10'
            style={{ height: '78vh', overflowY: 'scroll' }}
            >
                {
                    productosFiltrados.map(producto => (
                        <ProductoItem key={producto._id} producto ={producto}/>
                    ))
                }
        </div>
    )
}