import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { NuevosMensajesFab } from '../../components/chat/NuevosMensajesFab';
import { ImgPortada } from '../../components/ui/atom/ImgPortada';
import { TitlePage } from '../../components/ui/atom/TitlePage';
    
export const PrincipalPage = () => {
    const portada = "https://res.cloudinary.com/desm1rvpy/image/upload/v1641503441/Infarma/logo/portada_ljalwm.jpg"
    return (
        <div className='flexbox-container'>
           <div className='col-md-2'>
               <NavbarLeft />
           </div>
           <div
               className='col-md-10'
               style={{ height: '88.5vh', overflowY: 'scroll' }}
           >
               <NuevosMensajesFab />
               <div >
                   <TitlePage title='Panel Principal' />
                   <SpacerHorizontal />
                   <Divider />
                   <SpacerHorizontal />
                   <ImgPortada img={portada} />
               </div>
           </div>
        </div>
   )
}