import { createContext, useContext, useEffect, useReducer } from 'react';
import AppApi from '../api/AppApi';
import { 
    ChildrenProps, 
    ClienteProductos, 
    Lote, 
    LoteAddData, 
    LotesAddResponse, 
    LotesResponse, 
    ProductoSaldo, 
    ProductosTransaccion, 
    ResponsePostTransaccion, 
    TransaccionInv, 
    TransaccionResponse 
} from '../interfaces/interfaces';
import { inventarioReducer, InventarioState } from '../reducers/inventarioReducer';
import { AuthContext } from './AuthContext';
import { MessagesContext } from './MessagesContext';
import { ProductoContext } from './ProductoContext';
import moment from 'moment';
import { AppContext } from './AppContext';
import { ClienteContext } from './ClienteContext';
import { VisitaContext } from './VisitaContext';

type InventarioContextProps = {
    transacciones: TransaccionInv[],
    transaccionSeleccionada: TransaccionInv | null,
    lotes: Lote[]
    loteSeleccionado: Lote | null,
    productoSaldo: ProductoSaldo[]
    productoSaldoMM: ProductoSaldo[]
    productosTransaccion: ProductosTransaccion[]
    productoscliente: ClienteProductos[],


    cargaDataInventario: () => void
    crearTransaccionEntrada: (dataSend: any) => void
    crearTransaccionSalida: (visita: string) => void
    addProductoTransaccion: (detalle: ProductosTransaccion) => void
    deleteProductoTransaccion: (detalle: ProductosTransaccion) => void
    seleccionaTransaccion: (transaccion: TransaccionInv) => void
    prugaProductoTransaccion: () => void
    addLote: (dataSend: LoteAddData) => void
}


const initialState: InventarioState = {
    transacciones: [],
    transaccionSeleccionada: null,
    lotes: [],
    productoSaldo: [],
    productoSaldoMM: [],
    loteSeleccionado: null,
    productosTransaccion: [],
    productoscliente: [],

}

const limite = 500;

export const InventarioContext = createContext({} as InventarioContextProps)
export const InventarioProvider = ({ children }: ChildrenProps) => {

    const [state, dispatch] = useReducer(inventarioReducer, initialState)
    const { status, user } = useContext(AuthContext)
    const { productos } = useContext(ProductoContext)
    const { state: stateApp, toogleUpdateSaldos, showProcesing , hideProcesing } = useContext(AppContext)

    const { registraNotification } = useContext(MessagesContext)
    const { clienteSeleccionado } = useContext(ClienteContext)
    const { visitas } = useContext(VisitaContext)

    const actualizaInventarios = () => {
        productos.forEach(producto => {
            // const lotesProducto = state.lotes.filter(lote => lote.producto._id === producto._id && lote.usuario._id === user?.uid)
            const lotesProducto = state.lotes.filter(lote => lote.producto._id === producto._id)
            if (lotesProducto.length > 0) {
                lotesProducto.forEach(lote => {
                    if (lote.saldo > 0) {
                        const data = {
                            ...producto,
                            saldo: lote.saldo,
                            lote: lote.lote,
                            loteId: lote._id,
                            vence: lote.lotevence,
                            usuarioPropietario : lote.usuario._id
                        }
                        if (producto.original) {
                            dispatch({
                                type: 'add-producto-saldo-original',
                                payload: data
                            })
                        } else {
                            dispatch({
                                type: 'add-producto-saldo-MM',
                                payload: data
                            })
                        }
                    }
                })
            }
        })
    }

    const cargaProductosCliente = () => {
        const visitasTem = visitas.filter(visita => visita.cliente._id === clienteSeleccionado?._id)
        const data: ClienteProductos[] = [];
        visitasTem.forEach(visita => {
            const trasaccionesTem = state.transacciones.filter(transaccion => transaccion.visita === visita._id)
            trasaccionesTem.forEach(transactTem => {
                const { detalle: detalleItem } = transactTem
                detalleItem.forEach(detail => {
                    data.push({
                        id: detail._id,
                        fecha: transactTem.fecha,
                        producto: detail.producto.nombre,
                        lote: detail.lote._id,
                        cantidad: detail.cantidad,
                    })

                })
            })
        }
        )
        dispatch({
            type: 'carga-data-productosclientes',
            payload: data
        })
    }

    const cargaDataInventario = async () => {
        const [transacciones, lotes] = await Promise.all([
            AppApi.get<TransaccionResponse>(`/transacciones/all?limite=${limite}&desde=0`),
            AppApi.get<LotesResponse>(`/lotes/all?limite=${limite}&desde=0`)

        ])
        const { data: dataTransacciones } = transacciones
        const { data: dataLotes } = lotes
        dispatch({
            type: 'carga-data-transacciones',
            payload: dataTransacciones.transaccionInv
        })
        dispatch({
            type: 'carga-data-lotes',
            payload: dataLotes.lotes
        })
    }

    useEffect(() => {
        if (status === 'authenticated') {
            cargaDataInventario()
        }
    }, [status])

    useEffect(() => {
        if (stateApp.updateSaldos) {
            purgaSaldos()
            cargaDataInventario()
            actualizaInventarios()
            toogleUpdateSaldos()
        }
    }, [stateApp.updateSaldos])

    useEffect(() => {
        if (clienteSeleccionado && visitas.length > 0) {
            cargaProductosCliente()
        } else {
            dispatch({
                type: 'purga-productoscliente'
            })
        }
    }, [clienteSeleccionado])


    useEffect(() => {
        purgaSaldos()
        actualizaInventarios()
    }, [user, state.lotes, productos])

    const purgaSaldos = () => {
        dispatch({
            type: 'purga-producto-saldo-original',
        })
        dispatch({
            type: 'purga-producto-saldo-MM',
        })
    }



    const addProductoTransaccion = (detalle: ProductosTransaccion) => {
        dispatch({
            type: 'add-producto-transaccion',
            payload: detalle
        })
    }

    const deleteProductoTransaccion = (detalle: ProductosTransaccion) => {
        dispatch({
            type: 'delete-producto-transaccion',
            payload: detalle
        })
    }

    const prugaProductoTransaccion = () => {
        dispatch({
            type: 'purga-producto-transaccion',
        })
    }

    const seleccionaTransaccion = (transaccion: TransaccionInv) => {
        dispatch({
            type: 'selecciona-transaccion',
            payload: transaccion
        })
    }


    const crearTransaccionSalida = async (visita: string) => {
        if (state.productosTransaccion) {
            if (state.productosTransaccion.length > 0) {
                const dataSend = {
                    fecha: moment(),
                    visita: visita,
                    signo: -1,
                    anotaciones: 'Entrega de Muestras',
                    detalle: state.productosTransaccion
                }
                showProcesing()
                await AppApi.post<ResponsePostTransaccion>(`/transacciones`, dataSend);
                hideProcesing()
            }
        }
    }

    const crearTransaccionEntrada = async (dataSend: any) => {
        try {
            if (state.productosTransaccion && state.productosTransaccion.length > 0) {
                showProcesing()
                const { data } = await AppApi.post<ResponsePostTransaccion>(`/transaccionesweb`, dataSend);

                //TODO  : Falta Procesar las Alertas
                
                if (data.ok) {
                    const { data: aplicacion } = await AppApi.put<ResponsePostTransaccion>(`/transaccionesweb/aplica/${data.transaccionInv._id}`);
                    if (!aplicacion.ok) {
                        hideProcesing()
                        registraNotification({
                            type: 'error',
                            title: 'Inventario',
                            msg: aplicacion.msg
                        })
                    } else {
                        hideProcesing()
                        toogleUpdateSaldos()
                        prugaProductoTransaccion()
                        registraNotification({
                            type: 'success',
                            title: 'Inventario',
                            msg: aplicacion.msg
                        })
                    }
                } else {
                    hideProcesing()
                    registraNotification({
                        type: 'error',
                        title: 'Inventario',
                        msg: data.msg
                    })
                }
            } else {
                hideProcesing()
                registraNotification({
                    type: 'error',
                    title: 'Inventario',
                    msg: 'No hay productos que cargar',
                })
            }
        } catch (error: any) {
            console.log(error);
            hideProcesing()
            registraNotification({
                type: 'error',
                title: 'Inventario',
                msg: error.response.data.msg,
            })
        }
    }


    const addLote = async (dataSend: LoteAddData) => {
        try {
            showProcesing()
            const { data } = await AppApi.post<LotesAddResponse>(`/Lotes`, dataSend);
            
            if (data.ok) {
                await cargaDataInventario()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Lote registrado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }

    return (
        <InventarioContext.Provider value={{
            ...state,
            cargaDataInventario,
            crearTransaccionSalida,
            addProductoTransaccion,
            deleteProductoTransaccion,
            prugaProductoTransaccion,
            seleccionaTransaccion,
            addLote,
            crearTransaccionEntrada
        }}>
            {children}
        </InventarioContext.Provider>
    )

}