import { useContext } from 'react';
import { AuthContext } from '../../context/AuthContext';
import { MasterDataContext } from '../../context/MasterDataContext';
import { Pais } from '../../interfaces/interfaces';
import { reportsBI } from './reports';

interface EnvededReportBIprops {
    tipo : "kpis" | "rutas"
}

export const EnvededReportBIMax = ({tipo}:EnvededReportBIprops) => {

    const { user } = useContext(AuthContext)
    const { paises } = useContext(MasterDataContext)
    const paisUsuario: Pais[] = paises.filter(pais => pais._id === user?.pais)
    const reporte = reportsBI.filter(report => report.pais === paisUsuario[0].codigo && report.tipo === tipo)
    const mostrarReport =reporte.length > 0 ? reporte[0].report : ""

    return (
            <iframe 
                title="ODVM V1" 
                width="100%" 
                height="725" 
                src={mostrarReport}
                allowFullScreen={true}
            >
            </iframe>
    )
}