import { useContext } from "react";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelVisitas = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        <div className="navbar-collapse">
          <MenuItem route="/home/visitas" caption={etiquetaHome} />
          <MenuItem
            route="/home/visitashistorico"
            caption="Histrico de Visitas"
            icon="fa fa-th-list"
          />
        </div>
      );
    default:
      return <PanelVacio />
  }
};
