import { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router";
import defaultUser from "../../assets/defaultUser.png";
import { AuthContext } from "../../context/AuthContext";
import { SocketContext } from "../../context/SocketContext";

export const UsuarioOnLineItem = ({ usuario }) => {
  const history = useHistory();
  const {
    selecDestinatario,
    noSelecDestinatario,
    usuariosSeleccionados,
    chatNoLeido,
    activaChat,
  } = useContext(SocketContext);
  const { user } = useContext(AuthContext);
  const [msgSinLeer, setMsgSinLeer] = useState(0);

  const seleccionado = usuariosSeleccionados.filter(
    (e) => e.uid === usuario.uid
  );

  useEffect(() => {
    const noLeidos = chatNoLeido.filter(
      (msg) => msg.de === usuario.uid
    );
    setMsgSinLeer(noLeidos.length);
  }, [chatNoLeido, usuario.uid]);

  const color = usuario.conectado ? "lightgreen" : "lightgrey";
  const coloronLine = usuario.conectado ? "blue" : "grey";
  const colorMsgSinLeer = msgSinLeer > 0 ? "green" : "grey";

  const handleClick = () => {
    if (seleccionado.length > 0) {
      noSelecDestinatario(usuario);
    } else {
      selecDestinatario(usuario);
    }
  };

  const handleChat = async () => {
    await activaChat(user!.uid);
    history.push("home/notispeak");
  };

  return (
    <div>
      <div
        className="input-group col-md-12 "
        style={{
          backgroundColor: color,
          borderRadius: 5,
          marginBottom: 5,
          alignItems: "center",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <div className="divjustificadoizq">
          <img
            alt={usuario.nombre}
            src={usuario.images ? usuario.images : defaultUser}
            style={{
              width: "30px",
              height: "30px",
              objectFit: "contain",
              borderRadius: 50,
              marginTop: 5,
              marginBottom: 5,
            }}
          />
          <small className="ml-2">{usuario.nombre.substring(0, 25)}</small>
        </div>
        <div className="divjustificadoizq">
          <div
            style={{ cursor: "pointer", color: coloronLine }}
            onClick={handleClick}
          >
            <i className="fas fa-send mr-2 ml-2" />
          </div>
          <div
            style={{ cursor: "pointer", color: colorMsgSinLeer }}
            onClick={handleChat}
          >
            <i className="fas fa-comments mr-1" />
            {msgSinLeer}
          </div>
        </div>
      </div>
    </div>
  );
};
