import { useContext } from 'react';
import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { InventarioContext } from '../../context/InventarioContext';
import { SelectVisitador } from '../../components/ui/atom/FormControls';
import { useForm } from '../../hooks/useForm';
import { SocketContext } from '../../context/SocketContext';
import { AuthContext } from '../../context/AuthContext';
import moment from 'moment';
import { useVisitadorPais } from '../../hooks/useVisitadorPais';
    

//TODO  : Implementar descargar el invetnario en un excel

interface Form {
    usuario: string;
    }

export const InventarioSaldosScreen = () => {

    const initForm :Form = {
        usuario : '',
    }

    const { productoSaldoMM } = useContext(InventarioContext)
    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)
    const {usuario, onChange, setForm, form } = useForm<Form>(initForm)

    const {visitadores} =  useVisitadorPais()
    const saldosProductos = productoSaldoMM.filter((item) => item.usuarioPropietario === usuario)

    return (
        <div className='flexbox-container'>
            <div className='col-md-2'>
                <NavbarLeft />
            </div>
    
            <div
                className='col-md-10'
                style={{ height: '88.5vh', overflowY: 'scroll' }}
            >
                <div>
                    <TitlePage title='Saldos de Inventario' />
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <SelectVisitador 
                        label='Visitador'
                        name='uid'
                        value={usuario}
                        data={visitadores}
                        callBack={(e) => onChange( e, 'usuario')}
                    />
                    Descargar en excel?
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <div
                        style={{
                            display:'flex', 
                            flexDirection:'row', 
                            justifyContent:'space-between', 
                            alignItems: 'center'}}                            
                    >
                        <label style={{ flex: 1, fontSize: 18, }}>Cdigo</label>
                        <label style={{ flex: 5, fontSize: 18, }}>Nombre</label>
                        <label style={{ flex: 1, fontSize: 18, }}>Lote</label>
                        <label style={{ flex: 1, fontSize: 18, }}>Vence</label>
                        <label style={{ flex: 1, fontSize: 18, }}>Saldo</label>
                    </div>                   
                    <Divider />
                {
                    saldosProductos.map((item, index) => (
                        <div key={index}>
                            <SpacerHorizontal />
                            <div
                                style={{
                                    display:'flex', 
                                    flexDirection:'row', 
                                    justifyContent:'space-between', 
                                    alignItems: 'center'}}                            
                            >
                                <label style={{ flex: 1, fontSize: 18, }}>{item.codigo}</label>
                                <label style={{ flex: 5, fontSize: 18, }}>{item.nombre}</label>
                                <label style={{ flex: 1, fontSize: 18, }}>{item.lote}</label>
                                <label style={{ flex: 1, fontSize: 18, }}>{moment(item.vence).format("MM-YY")}</label>
                                <label style={{ flex: 1, fontSize: 18, }}>{item.saldo}</label>
                            </div>
                            <Divider />                            
                        </div>
                    ))
                }
                </div>
            </div>
        </div>
    )
}