
import { Divider } from '@material-ui/core';
import { Producto } from '../../interfaces/interfaces';
import { SpacerHorizontal } from '../ui/atom/Spacer';
import { TitlePage } from '../ui/atom/TitlePage';
import { ImgBlogAjus } from './ImgProductoAjust';

interface productoProps {
    producto : Producto
}

export const ProductoItem = ({producto} : productoProps) => {
    
const {nombre, images, composicion, posologia, indicaciones} = producto

    return (
        <div 
            className='mt-2'
            // style={{ backgroundColor:'green'}}
        >
            <TitlePage title={nombre}/>
            <SpacerHorizontal />        
            <div style={{display:'flex', flexDirection:'row'}}>
                <ImgBlogAjus 
                    alt={nombre}
                    image={images}
                />
                <div className='ml-4' style={{display:'flex', flexDirection: 'column'}}>
                    <span> <h4>Posologa:</h4> {posologia}</span>
                    <SpacerHorizontal />
                    <span> <h4>Indicaciones:</h4> {indicaciones}</span>
                    <SpacerHorizontal />
                    <span> <h4>Composicin: </h4>{composicion}</span>
                </div>
            </div>
            <SpacerHorizontal />        
            <SpacerHorizontal />        
        <Divider />            
        </div>
    )
}