import { useContext } from 'react';
import { useMap } from '../../hooks/useMap';
import { MapStyleControlSelect } from '../ui/atom/FormControls';
import { ClienteContext } from '../../context/ClienteContext';
import { ClientesActivosMapFilter } from '../clientes/ClientesActivosMapFilter';

export const MapContainerRutes = () => {
    const {mapPointers} = useContext(ClienteContext)
    const {handleSelectStyle, styleMap, setRef} = useMap(mapPointers)

    return (
        <div style={{display:'flex', flexDirection:'row', }}>
            <div className='mapContainer col-md-3'>
                <MapStyleControlSelect 
                    label = 'Capa'
                    value = {styleMap}
                    name = 'styleMap'
                    callBack={(e) => handleSelectStyle(e)}
                />
                <ClientesActivosMapFilter />
            </div>
            <div 
                className='mapContainer col-md-9'
                ref = {setRef}
            />
        </div>
    )
}