import { Divider } from "@material-ui/core";
import { SpacerHorizontal } from "../ui/atom/Spacer";

const defaultImg =
"https://res.cloudinary.com/desm1rvpy/image/upload/v1653601855/Infarma/default/istockphoto-1354776457-612x612_ndweuc.jpg";

interface ImgBlogAjusProps {
    image? : string
    alt : string
}


export const ImgBlogAjus = ({ image, alt } : ImgBlogAjusProps) => {
    return (
      <div className="doc">
        <div className="box">
          <img
            alt={alt}
            src={image ? image : defaultImg}
            className="imagenBlogajustada"
            style={{
              borderRadius: 30,
            }}
          />
        </div>
      </div>
    );
  };