interface Props {
    title? : string
}

export const TitlePage = ({title = "Aqu va un ttulo"} : Props) => {
  return (
    <div>
      <h2 style={{
            fontFamily:'monospace',
            color:"#E02729",
            marginTop:15
        }}
    >
          {title}
    </h2>
    </div>
  )
}

