import { useContext } from 'react'
import { Divider } from '@material-ui/core'
import { NavbarLeft } from '../../components/NavbarLeft'
import { RoleControlSelect, SelectPais, TextControl, TextControlEmail, TextControlPass } from '../../components/ui/atom/FormControls'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { useForm } from '../../hooks/useForm'
import { MasterDataContext } from '../../context/MasterDataContext';
import { SocketContext } from '../../context/SocketContext';
import { TitlePage } from '../../components/ui/atom/TitlePage'

export const UsuariosAddScreen = () => {
    const { nombre, correo, pais, rol, password, onChange, form } = useForm({
        nombre: "",
        password: "",
        correo: "",
        rol: "NEW_USER_ROLE",
        pais: ""
    })

    const { paises } = useContext(MasterDataContext)
    const { crearUsuario } = useContext(SocketContext)

    const handleAdd = async (e: any) => {
        e.preventDefault()
        crearUsuario(form);
    }

    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>
            <div
                className="col-md-10"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <TitlePage title='Creacin de Usuarios' />
                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button className='btn btn-success ml-2' onClick={handleAdd}>{`Guardar`}</button>
                <SpacerHorizontal />
                <TextControl
                    label="Nombre"
                    name='nombre'
                    value={nombre}
                    callBack={(e) => onChange(e, 'nombre')}
                />
                <TextControlEmail
                    label="Correo"
                    name='correo'
                    value={correo}
                    callBack={(e) => onChange(e, 'correo')}
                />
                <TextControlPass
                    label="Password"
                    name='password'
                    value={password}
                    callBack={(e) => onChange(e, 'password')}
                />
                <RoleControlSelect
                    label="Rol"
                    name='rol'
                    value={rol}
                    callBack={(e) => onChange(e, 'rol')}
                />

                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => onChange(e, 'pais')}
                    data={paises}
                />

            </div>
        </div>
    )
}
