import { useHistory } from 'react-router';
import { EnvededReportBI } from './EnvededReportBI';
import { BtnTypeEnter } from '../../components/ui/atom/FormControls';

//route de la pantalla: /home/rutasmax

export const MaximizeReportBI = () => {

    const history = useHistory();
    const handleMaximize  = () => {
        history.goBack();
    }

    return (
        <div>
            <div className='ml-4'>
                <BtnTypeEnter 
                    label='Volver'
                    callBack={handleMaximize}
                    icon="fa fa-window-restore "
                    color='yellow'
                    type = 'small'
                />            
            </div>
            <EnvededReportBI tipo ={"kpis"} />
        </div>
    )
}