import { useContext } from "react";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelRutas = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/rutas" caption={etiquetaHome} />

          <MenuItem
            route="/home/rutasconsulta"
            caption="Consulta de Recorridos"
            icon="fa fa-route"
          />                 
        </div>
      );
    default:
      return <PanelVacio />
  }
};
