import { useContext, useState } from "react"
import { Divider } from "@material-ui/core"
import { SelectPais, SelectProvincia, TextControl } from "../../components/ui/atom/FormControls"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { TableCanton } from "../../components/tablas/geo/TableCanton"
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const CantonScreen = () => {

    const { canton, provincia, onChange  } = useForm({
        canton: "",
        provincia: "",
    })

    const {paises, provincias, crearCanton} = useContext(MasterDataContext)
    const [pais, setPais] = useState('')
    const provinciasFilter = provincias.filter(provincia => provincia.pais._id === pais)
    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearCanton({nombre : canton, provincia});
    }
    return (
        <div className="flexbox-container">
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Cantn' />

                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => setPais(e)}
                    data={paises}
                />   
                <SelectProvincia
                    label="Provincia"
                    name='provincia'
                    value={provincia}
                    callBack={(e) => onChange(e, 'provincia')}
                    data={provinciasFilter}
                />                          
                <TextControl
                    label="Cantn"
                    name='canton'
                    value={canton}
                    callBack={(e) => onChange(e, 'canton')}
                />
            </div>
            <div
                className="col-md-8"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Cantones' />
                <Divider />
                <TableCanton/>              
            </div>            
        </div>
    )
}

