import React, { useContext } from "react";
import { Divider } from "@material-ui/core";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelClientes = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/clientes" caption={etiquetaHome} />

          <Divider />
          {/* {user?.rol !== "VIS_USER_ROLE" && (
            <>
              <MenuItem
                route="/home/agendas"
                caption="Agendas"
                icon="fa fa-calendar"
              />
              <Divider />
            </>
          )} */}
          <MenuItem
            route="/home/clientesactivos"
            caption="Clientes Activos"
            icon="fa fa-user-check"
          />
          <MenuItem
            route="/home/clientesinactivos"
            caption="Clientes Inactivos"
            icon="fa fa-user-lock"
          />    
          <MenuItem
            route="/home/clientesmapeo"
            caption="Mapeo de Clientes"
            icon="fa fa-map-signs"
          />                   
        </div>
      );
    default:
      return <PanelVacio />
  }
};
