import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { useContext } from 'react';
import { MasterDataContext } from '../../context/MasterDataContext';
import { ProductoContext } from '../../context/ProductoContext';
import { Caracteristicas } from '../../interfaces/interfaces';
import { ItemCategoria } from './ItemCategoria';

export const CategoriasList = () => {

    const { caracteristricas } = useContext(MasterDataContext)
    const { seleccionaCategoria } = useContext(ProductoContext)
    const items :Caracteristicas[] = caracteristricas.filter(caracteristica => caracteristica.tipo.nombre === 'CAT_PRO')
    
    return (
        <div >
           <div className='col-md-10'>
               <div>
                   <SpacerHorizontal />
                   {
                       items.map(categoria => (
                        <ItemCategoria 
                            key={categoria._id} 
                            categoria={categoria} 
                            callBack = {seleccionaCategoria}
                        />
                       ))
                   }
               </div>
           </div>
       </div>
   )
}




    