
import {
    Caracteristicas,
    CaracteristicasTipo,
    MasterDataResponse,
    Pais,
    Provincia,
    Canton,
    Distrito,
    Barrio,
    Param,
    Usuario
} from '../interfaces/interfaces';


export interface MasterDataState {
    caracteristicasTipo: CaracteristicasTipo[];
    caracteristicasTipoNewData: CaracteristicasTipo[];
    caracteristricas: Caracteristicas[];
    caracteristricasNewData: Caracteristicas[];
    paises: Pais[];
    paisesNewData: Pais[];
    provincias: Provincia[];
    provinciasNewData: Provincia[];
    cantones: Canton[];
    cantonesNewData: Canton[];
    distritos: Distrito[];
    distritosNewData: Distrito[];
    barrios: Barrio[];
    barriosNewData: Barrio[];
    param: Param[];
    paramNewData : Param[]
    paramNewDataValores : Param[]
}

export type masterDataActions =
    | { type: 'set_master_data', payload: MasterDataResponse }
    | { type: 'carga-param-newdata', payload: Param[] }
    | { type: 'carga-param-newdata-valores', payload: Param[] }
    | { type: 'purga-param-newdata' }
    | { type: 'purga-param-newdata-valores' }
    | { type: 'carga-param', payload: Param[] }

    | { type: 'carga-paises-newdata', payload: Pais[] }
    | { type: 'purga-paises-newdata' }
    | { type: 'add-paises-newdata', payload: Pais }
    | { type: 'update-paises-newdata', payload: Pais }

    | { type: 'carga-provincias-newdata', payload: Provincia[] }
    | { type: 'purga-provincias-newdata' }
    | { type: 'add-provincias-newdata', payload: Provincia }
    | { type: 'update-provincias-newdata', payload: Provincia }

    | { type: 'carga-cantones-newdata', payload: Canton[] }
    | { type: 'purga-cantones-newdata' }
    | { type: 'add-cantones-newdata', payload: Canton }
    | { type: 'update-cantones-newdata', payload: Canton }

    | { type: 'carga-distritos-newdata', payload: Distrito[] }
    | { type: 'purga-distritos-newdata' }
    | { type: 'add-distritos-newdata', payload: Distrito }
    | { type: 'update-distritos-newdata', payload: Distrito }

    | { type: 'carga-barrios-newdata', payload: Barrio[] }
    | { type: 'purga-barrios-newdata' }
    | { type: 'add-barrios-newdata', payload: Barrio }
    | { type: 'update-barrios-newdata', payload: Barrio }

    | { type: 'carga-caracteristicasTipo-newdata', payload: CaracteristicasTipo[] }
    | { type: 'purga-caracteristicasTipo-newdata' }
    | { type: 'add-caracteristicasTipo-newdata', payload: CaracteristicasTipo }
    | { type: 'update-caracteristicasTipo-newdata', payload: CaracteristicasTipo }

    | { type: 'carga-caracteristicas-newdata', payload: Caracteristicas[] }
    | { type: 'purga-caracteristicas-newdata' }
    | { type: 'add-caracteristicas-newdata', payload: Caracteristicas }
    | { type: 'update-caracteristicas-newdata', payload: Caracteristicas }

    | { type: 'carga-visitaroresPais', payload: Usuario[] } //Accion para los visitadores por Pais


export const masterDataReducer = (state: MasterDataState, action: masterDataActions): MasterDataState => {

    switch (action.type) {
        case 'set_master_data':
            return {
                ...state,
                caracteristicasTipo: action.payload.caracteristicasTipo,
                caracteristricas: action.payload.caracteristricas,
                paises: action.payload.paises,
                provincias: action.payload.provincias,
                cantones: action.payload.cantones,
                distritos: action.payload.distritos,
                barrios: action.payload.barrios,
                param: action.payload.params,
            }
        case 'carga-param-newdata':
            return {
                ...state,
                paramNewData: action.payload,
            }
        case 'carga-param-newdata-valores':
            return {
                ...state,
                paramNewDataValores: action.payload,
            }    
        case 'purga-param-newdata':
            return {
                ...state,
                paramNewData: [],
            }
        case 'purga-param-newdata-valores':
            return {
                ...state,
                paramNewDataValores: [],
            }                     
        case 'carga-param':
            return {
                ...state,
                param: action.payload,
            }
        
        //Paises
        
        case 'carga-paises-newdata':
            return {
                ...state,
                paisesNewData: action.payload,
            }
        case 'purga-paises-newdata':
            return {
                ...state,
                paisesNewData: [],
            }
        case 'add-paises-newdata':
            return {
                ...state,
                paises: [...state.paises, action.payload],
            }
        case 'update-paises-newdata':
            return {
                ...state,
                paises: state.paises.map((e) => e._id === action.payload._id ? action.payload : e)
            }                                                            

        //Provincias
        
        case 'carga-provincias-newdata':
            return {
                ...state,
                provinciasNewData: action.payload,
            }
        case 'purga-provincias-newdata':
            return {
                ...state,
                provinciasNewData: [],
            }
        case 'add-provincias-newdata':
            return {
                ...state,
                provincias: [...state.provincias, action.payload],
            }
        case 'update-provincias-newdata':
            return {
                ...state,
                provincias: state.provincias.map((e) => e._id === action.payload._id ? action.payload : e)
            } 

        //Cantones
        
        case 'carga-cantones-newdata':
            return {
                ...state,
                cantonesNewData: action.payload,
            }
        case 'purga-cantones-newdata':
            return {
                ...state,
                cantonesNewData: [],
            }
        case 'add-cantones-newdata':
            return {
                ...state,
                cantones: [...state.cantones, action.payload],
            }
        case 'update-cantones-newdata':
            return {
                ...state,
                cantones: state.cantones.map((e) => e._id === action.payload._id ? action.payload : e)
            } 

        //Distritos
        
        case 'carga-distritos-newdata':
            return {
                ...state,
                distritosNewData: action.payload,
            }
        case 'purga-distritos-newdata':
            return {
                ...state,
                distritosNewData: [],
            }
        case 'add-distritos-newdata':
            return {
                ...state,
                distritos: [...state.distritos, action.payload],
            }
        case 'update-distritos-newdata':
            return {
                ...state,
                distritos: state.distritos.map((e) => e._id === action.payload._id ? action.payload : e)
            } 

        //Barrios
        
        case 'carga-barrios-newdata':
            return {
                ...state,
                barriosNewData: action.payload,
            }
        case 'purga-barrios-newdata':
            return {
                ...state,
                barriosNewData: [],
            }
        case 'add-barrios-newdata':
            return {
                ...state,
                barrios: [...state.barrios, action.payload],
            }
        case 'update-barrios-newdata':
            return {
                ...state,
                barrios: state.barrios.map((e) => e._id === action.payload._id ? action.payload : e)
            } 

        //Caractersticas Tipo
        
        case 'carga-caracteristicasTipo-newdata':
            return {
                ...state,
                caracteristicasTipoNewData: action.payload,
            }
        case 'purga-caracteristicasTipo-newdata':
            return {
                ...state,
                caracteristicasTipoNewData: [],
            }
        case 'add-caracteristicasTipo-newdata':
            return {
                ...state,
                caracteristicasTipo: [...state.caracteristicasTipo, action.payload],
            }
        case 'update-caracteristicasTipo-newdata':
            return {
                ...state,
                caracteristicasTipo: state.caracteristicasTipo.map((e) => e._id === action.payload._id ? action.payload : e)
            } 

        //Caractersticas
        
        case 'carga-caracteristicas-newdata':
            return {
                ...state,
                caracteristricasNewData: action.payload,
            }
        case 'purga-caracteristicas-newdata':
            return {
                ...state,
                caracteristricasNewData: [],
            }
        case 'add-caracteristicas-newdata':
            return {
                ...state,
                caracteristricas : [...state.caracteristricas, action.payload]
            }
        case 'update-caracteristicas-newdata':
            return {
                ...state,
                caracteristricas: state.caracteristricas.map((e) => e._id === action.payload._id ? action.payload : e)
            } 
            
            
        default:
            return state
    }

}