import { useState } from "react"

export const useForm = <T extends Object>(initForm: T ) => {

    const [state, setState] = useState(initForm)

    const onChange = <K extends Object>(value: K, field: keyof T) =>{
        setState({
            ...state,
            [field] : value
        })
    }
    return {
        ...state,
        form: state,
        onChange,
        setForm : setState
    }
    
}
