import { DataFilters, ProductosTransaccion, Visita } from '../interfaces/interfaces';



export interface VisitasState {
    visitas: Visita[],
    visitasFilter: Visita[],
    visitasDay: Visita[],
    visitasFiltradas: Visita[],
    visitaSeleccionada: Visita | null,
    idVisitaSeleccionada: string | null,
    productosTransaccion: ProductosTransaccion[]
    dataFiltros: DataFilters[],
    visitasIndexFilter: number | null,
    
}


type VisitaAction =
    | { type: 'carga-data-visitas', payload: Visita[] }
    | { type: 'carga-data-visitasday', payload: Visita[] }
    | { type: 'selecciona-visita', payload: Visita }
    | { type: 'selecciona-id-visita', payload: string }
    | { type: 'seleccionada-visita', payload: string }
    | { type: 'purga-visita' }
    | { type: 'add-visita', payload: Visita }
    | { type: 'update-visita', payload: Visita }
    | { type: 'delete-visita' }
    | { type: 'carga-data-filtros', payload: any[] }
    | { type: 'purga-data-filtros' }
    | { type: 'add-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'delete-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'update-producto-transaccion', payload: ProductosTransaccion }
    | { type: 'purga-producto-transaccion' }
    | { type: 'carga-data-visita-filtrados', payload: Visita[] }
    | { type: 'purga-data-visita-filtrados' }

    | { type: 'add-visitasFilter', payload: Visita[] }
    | { type: 'remove-visitasFilter' }

    | { type: 'clear-visitasIndexFilter' }

    | {type: 'set-visitaIndexFilter', payload : number}
    | {type : 'clear-visitaIndexFilter'}


    


export const VisitaReducer = (state: VisitasState, action: VisitaAction) => {

    switch (action.type) {
        case 'carga-data-visitas':
            return {
                ...state,
                visitas: action.payload
            }
        case 'carga-data-visitasday':
            return {
                ...state,
                visitasDay: action.payload
            }
        case 'selecciona-visita':
            return {
                ...state,
                visitaSeleccionada: action.payload
            }
        case 'selecciona-id-visita':
            return {
                ...state,
                idVisitaSeleccionada: action.payload
            }
        case 'purga-visita':
            return {
                ...state,
                visitaSeleccionada: null
            }
        case 'add-visita':
            return {
                ...state,
                visitas: [...state.visitas, action.payload]
            }
        case 'update-visita':
            return {
                ...state,
                visitas: state.visitas.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'delete-visita':
            return {
                ...state,
                visitas: state.visitas.filter((e) => e._id !== state.idVisitaSeleccionada)
            }
        case 'carga-data-filtros':
            return {
                ...state,
                dataFiltros: action.payload
            }
        case 'purga-data-filtros':
            return {
                ...state,
                dataFiltros: []
            }
        //Transaccion de muestras

        case 'add-producto-transaccion':
            return {
                ...state,
                productosTransaccion: [...state.productosTransaccion, action.payload]
            }
        case 'delete-producto-transaccion':
            return {
                ...state,
                productosTransaccion: state.productosTransaccion.filter((e) => e !== action.payload)
            }
        case 'update-producto-transaccion':
            return {
                ...state,
                productosTransaccion: state.productosTransaccion.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'purga-producto-transaccion':
            return {
                ...state,
                productosTransaccion: []
            }

        case 'carga-data-visita-filtrados':
            return {
                ...state,
                visitasFiltradas: action.payload
            }
        case 'purga-data-visita-filtrados':
            return {
                ...state,
                visitasFiltradas: []
            }

        case 'add-visitasFilter':
            return {
                ...state,
                visitasFilter: action.payload
            }
        case 'remove-visitasFilter':
            return {
                ...state,
                visitasFilter: []
            }

        case 'clear-visitasIndexFilter':
            return {
                ...state,
                visitasIndexFilter: null
            } 

        case 'set-visitaIndexFilter':
            return {
                ...state,
                visitasIndexFilter: action.payload
            }
            
        case 'clear-visitaIndexFilter':
            return {
                ...state,
                eventosIndexFilter: null
            } 

        default:
            return state;
    }


}