import { createContext, useContext, useEffect, useReducer } from 'react';
// import { ImagePickerResponse } from 'react-native-image-picker';
import AppApi from '../api/AppApi';
import { ChildrenProps, Producto, ProductosResponse } from '../interfaces/interfaces';
import { productoReducer, Productostate } from '../reducers/productoReducer';
import { AppContext } from './AppContext';
import { AuthContext } from './AuthContext';

//TODO: imagenes de los productos


type ProductoContextProps = {
    productos: Producto[],
    productoseleccionado: Producto | null,
    idproductoseleccionado: string | null,
    idcategoriaseleccionada: string | null,
    cargaProductos: () => void;
    seleccionaCategoria: (categoria: string) => void;
    purgaCategoria: () => void;
    seleccionaProducto: (Producto: Producto) => void;
    crearProducto: (Producto: any) => void;
    updateProducto: (Producto: any) => void;
    purgaProductoSeleccionado: () => void;
    // uploadImage: (userIde: string, data: ImagePickerResponse) => void;
}


const initialState: Productostate = {
    productos: [],
    productoseleccionado: null,
    idproductoseleccionado: null,
    idcategoriaseleccionada: null,
}

const limite = 500;


export const ProductoContext = createContext({} as ProductoContextProps)
export const ProductoProvider = ({ children }: ChildrenProps) => {

    const [state, dispatch] = useReducer(productoReducer, initialState)
    const { status, user } = useContext(AuthContext)
    const { showProcesing , hideProcesing} = useContext(AppContext)

    useEffect(() => {
        if (status === 'authenticated') {
            cargaProductos()
        }
    }, [status])

    const cargaProductos = async () => {
        const { data } = await AppApi.get<ProductosResponse>(`/productos?limite=${limite}&desde=0`);
        dispatch({
            type: 'carga-data-productos',
            payload: data.productos
        })
    }

    const seleccionaProducto = (producto: Producto) => {
        dispatch({
            type: 'selecciona-producto',
            payload: producto
        })
    }

    const seleccionaCategoria = (categoria: string) => {
        dispatch({
            type: 'selecciona-categoria',
            payload: categoria
        })
    }
    const purgaCategoria = () => {
        dispatch({
            type: 'purga-categoria'
        })
    }

    const purgaProductoSeleccionado = () => {
        dispatch({
            type: 'purga-producto',
        })
    }

    const crearProducto = async (Producto: any) => {
        const { _id, ...dataClean } = Producto
        const dataSend = {
            ...dataClean,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.post<ProductosResponse>(`/productos`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaProductos()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Producto registrado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }


    // const uploadImage = async (productoId: string, data: ImagePickerResponse) => {

    //     const fileToUpload = {
    //         uri: data.assets![0].uri,
    //         type: data.assets![0].type,
    //         name: data.assets![0].fileName
    //     }

    //     const formData = new FormData()
    //     formData.append('archivo', fileToUpload)

    //     try {
    //         const resp = await AppApi.put(`/uploadsimg/productos/${productoId}`, formData);
    //         // dispatch({
    //         //     type: 'registraUsuario',
    //         //     payload: resp.data.modelo,
    //         // })
    //     } catch (error: any) {
    //         registraNotification({
    //             title: 'Error',
    //             msg: error.response.data.msg,
    //             type: 'error'
    //         })
    //     }

    // }


    const updateProducto = async (producto: any) => {
        const dataSend = {
            ...producto,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.put<ProductosResponse>(`/productos/${producto._id}`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaProductos()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Producto actualizado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }

    return (
        <ProductoContext.Provider value={{
            ...state,
            cargaProductos,
            seleccionaProducto,
            purgaProductoSeleccionado,
            crearProducto,
            updateProducto,
            // uploadImage,
            seleccionaCategoria,
            purgaCategoria,
        }}>
            {children}
        </ProductoContext.Provider>
    )

}