import { useContext, useEffect, useState } from 'react';
import { SpacerHorizontal } from '../ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { BtnTypeEnter, BtnTypeEnterCallBack, SelectItem, SelectLote, TextControlNum2 } from '../ui/atom/FormControls';
import { useForm } from '../../hooks/useForm';
import { AuthContext } from '../../context/AuthContext';
import { ProductoContext } from '../../context/ProductoContext';
import { InventarioContext } from '../../context/InventarioContext';
import { MessagesContext } from '../../context/MessagesContext';
import { InventarioDetalleItemAdd } from './InventarioDetalleItemAdd';

interface Form {
    producto: string;
    cantidad: number;
  }

  interface InventarioDetalleAddProps {
    visitador : string
  }

  
export const InventarioDetalleAdd = ({visitador} : InventarioDetalleAddProps) => {

    const initForm = { cantidad: 1, producto: '', lote: '', loteId: '' }
      

    const {user} = useContext(AuthContext)
    const { registraNotification } = useContext(MessagesContext)
    const {productos} = useContext(ProductoContext)  
    const productosMuestras = productos.filter(producto => producto.original === false)
    const { lotes, productosTransaccion, addProductoTransaccion } = useContext(InventarioContext)
    
    const { cantidad, producto, lote, loteId, onChange: onchangeCantidad, form, setForm: setFormCantidad } =
        useForm(initForm)

    const lotesSelect = lotes.filter(
        lote => lote.producto._id === producto  && 
        user && 
        lote.usuario._id === visitador)

        useEffect(() => {
            setFormCantidad(initForm)
        }, [visitador])
    
    const addDetalle = () => {
        if (!producto || producto.length === 0) {
            return registraNotification({
                type: 'error',
                title: 'Falta informacin',
                msg: 'Debe seleccionar un producto'
            })
        } else if (!lote || lote.length === 0) {
            return registraNotification({
                type: 'error',
                title: 'Falta informacin',
                msg: 'Debe seleccionar un lote'
            })
        }

        const existeProducto = productosTransaccion.filter(item => item.producto === producto && item.lote === lote)
        if (existeProducto.length > 0) {
            return registraNotification({
                type: 'error',
                title: 'Verifique',
                msg: `El Producto, YA EXISTE EN EL DETALLE`,
            })
        }

        const data = {
            lote,
            loteId,
            producto,
            cantidad,
        }
        addProductoTransaccion(data)
        setFormCantidad(initForm)
    }

    return (
        <div >
            <div style={{display:'flex', alignItems:'flex-end'}}>
                <SelectItem 
                    label = "Producto"
                    name="producto"
                    value = {producto}
                    data={productosMuestras}
                    callBack={(e) => onchangeCantidad(e, "producto")}
                />
                {
                    producto !== "" && (
                        <SelectLote 
                            label = "Lotes Disponibles"
                            name="loteId"
                            value = {lote}
                            data={lotesSelect}
                            callBack={(e) => onchangeCantidad(e, "lote")}
                        />
                    )
                }   
                <TextControlNum2 
                    label="Cantidad"
                    name='cantidad'
                    value={cantidad}
                    callBack={(e) => onchangeCantidad(e, "cantidad")}
                />
                <BtnTypeEnterCallBack
                    label={"Cargar"}
                    callBack={(e)=>{
                        e.preventDefault()
                        addDetalle()
                    }}
                    icon="fa fa-plus"
                    color='green'
                    type = 'small'
                />                   
                <BtnTypeEnter
                    label={"Limpiar"}
                    callBack={() => setFormCantidad(initForm)}
                    icon="fa fa-trash "
                    color='red'
                    type = 'small'
                />
       
            </div>
                                
            <Separacion />

            <div>
                {
                    productosTransaccion.map(item => (
                        <InventarioDetalleItemAdd
                            detalleItem = {item}
                            key={item.producto + item.lote}
                        />
                    ))
                }
            </div>
        </div>
    )
}

const Separacion  = () => {
    return (
        <div>
            <SpacerHorizontal />
            <Divider />
            <SpacerHorizontal />
        </div>
    )
}