import mapboxgl from 'mapbox-gl'
import { useCallback, useEffect, useRef, useState } from 'react';
import { MapPointer } from '../interfaces/interfaces';

//TODO  : Cambiar el ApiKey
//TODO  : Generar los ApiKeys de Infarma

mapboxgl.accessToken = 'pk.eyJ1IjoiY2xpemFubyIsImEiOiJja2w3NXgxOGgxZXRmMnVsYWRvcHY3eWpnIn0.24MUcUaXNmqc6M2Fp_YCqQ';

export const useMap  = (MapPointers : MapPointer[]) => {

    const mapaDiv = useRef();
    const setRef = useCallback((node) => {
        mapaDiv.current = node;
      }, []);

    //   console.log(MapPointers);
      

    const mapa = useRef();
    const [zoom, setZoom] = useState(7)
    const [styleMap, setStyleMap] = useState(localStorage.getItem('mapStyle') || 'calles')

    const handleSelectStyle  = (e: any) => {
        localStorage.setItem("mapStyle", e);
        setStyleMap(e)
    }

    useEffect(() => {

        let lngs: number[] = []
        let lats: number [] = []
        MapPointers.map(point => {
            lngs.push(point.lng)
            lats.push(point.lat)
        })
        const long = Math.min(...lngs)
        const lati = Math.max(...lats)
        const initCoords = lngs.length === 0 || lats.length === 0
                            ? [-84.38, 9.97]
                            : [long.toFixed(2), lati.toFixed(2)]

        const map = new mapboxgl.Map({
          container: mapaDiv.current,
          style: mapStyle(styleMap),
          center: initCoords,
          zoom: zoom,
        });
        
        // Crear el Mapa
        mapa.current = map;
        
        MapPointers.map(({especialidad, lng, lat, id, description, name}) => {
            const el = document.createElement('div');
            const width = 41-zoom;
            const height = 40-zoom;
            const pointer = especialidad === "FARMACIA"
            ? 'https://res.cloudinary.com/desm1rvpy/image/upload/v1642636374/Infarma/logo/mark8_frfzgj.png'
            : 'https://res.cloudinary.com/desm1rvpy/image/upload/v1642636334/Infarma/logo/mark1_j7u0ny.png'        
            el.style.backgroundImage = `url(${pointer})`;
            el.style.width = `${width}px`;
            el.style.height = `${height}px`;
            el.style.cursor = `pointer`
            el.style.backgroundSize = '100%';
            new mapboxgl.Marker(el)
                .setLngLat([lng, lat]).addTo(mapa.current).setDraggable(false)
                .setPopup(new mapboxgl.Popup({ offset: 35 }).setHTML(
                    `<div>
                        <p>${name}</p>
                        <hr/>
                        <p>${description}</p>
                     </div>`
                    ))
                .id = id
        })

    // Crear el Marcador en el DOM

        //Listener del Zoom en el mapa
        map.on('zoom', ({target}) => {
            const currentZoom = target.transform._zoom.toFixed(2)
            if (currentZoom - zoom > 0.2 || currentZoom - zoom < 0.2) {
                setZoom(currentZoom)
            }
        })
      }, [MapPointers, styleMap]);

    return {
        styleMap,
        setRef,
        handleSelectStyle,
    }

}



const mapStyle  = (style : string) => {
    switch (style) {
        case 'calles':
            return 'mapbox://styles/mapbox/streets-v11'
        case 'exteriores':
            return 'mapbox://styles/mapbox/outdoors-v11'
        case 'claro': 
            return 'mapbox://styles/mapbox/light-v10'
        case 'oscuro':
            return 'mapbox://styles/mapbox/dark-v10'
        case 'satelite':
            return 'mapbox://styles/mapbox/satellite-v9'
        case 'calles+satelite':
            return 'mapbox://styles/mapbox/satellite-streets-v11'
        case 'navegacion+dia':
            return 'mapbox://styles/mapbox/navigation-day-v1'
        case 'navegacion+noche':
            return 'mapbox://styles/mapbox/navigation-night-v1'
        default:
            return 'mapbox://styles/mapbox/streets-v11'
    }
}




