import { PanelVacio } from "./PanelVacio";
import { PanelNotificaciones } from "./PanelNotificaciones";
import { PanelParametros } from "./PanelParametros";
import { PanelHome } from "./PanelHome";
import { PanelCalendario } from "./PanelCalendario";
import { PanelClientes } from "./PanelClientes";
import { PanelVisitas } from "./PanelVisitas";
import { PanelInventario } from "./PanelInventario";
import { PanelRutas } from "./PanelRutas";
import { PanelVademecum } from './PanelVademecum';

export const ConstruyeMenu = ({ option }) => {
  switch (option) {
    case "home":
      return (<PanelHome etiquetaHome="Panel Principal" />);

    case "usuarios":
    case "usuariosadd":
    case "geo":
    case "caracteristicas":
    case "caracteristicasgrupos":
    case "param":
    case "admin":
      return (<PanelParametros etiquetaHome="Panel Administracin" />);

    case "calendario":
    case "calendar":
    case "agendas":
    case "eventos":
      return (<PanelCalendario etiquetaHome="Panel Calendario" />);


    case "clientes":
    case "cliente":
    case "clientesactivos":
    case "clientesinactivos":
    case "clientesmapeo":
      return (<PanelClientes etiquetaHome="Panel Clientes"/>);

    case "visitashistorico":
    case "visitas":
    case "visita":
      return (<PanelVisitas etiquetaHome="Panel Visitas"/>);


    case "vade":
    case "vademecum":
    case "vademecumall":
      return (<PanelVademecum etiquetaHome="Panel Vademecum"/>);

    case "inventario":
    case "inventarioadd":
    case "inventariosaldos":
    case "inventariotransacciones":
    case "inventariotransaccion":
      return (<PanelInventario etiquetaHome="Panel Inventario"/>);

    case "rutas":
    case "rutasconsulta" :      
      return (<PanelRutas etiquetaHome="Panel Rutas"/>);            
      
    case "notisends":
      return <PanelNotificaciones etiquetaHome="Panel Mensajes" />;
    case "vacia":
      return <PanelParametros etiquetaHome="" />;
    default:
      return <PanelVacio />;
  }
};
