import {  useContext } from "react"
import { Divider } from "@material-ui/core"
import { TextControl } from "../../components/ui/atom/FormControls"
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { NavbarLeft } from "../../components/NavbarLeft"
import { TableCargaTipoCaracteristica } from "../../components/tablas/params/TableTipoCaracteristica"
import { TitlePage } from "../../components/ui/atom/TitlePage"

export const CaracteristicasTiposScreen = () => {

    const { nombre, onChange  } = useForm({nombre: ""})

    const {crearTipoCaracteristica} = useContext(MasterDataContext)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearTipoCaracteristica({nombre});
    }

    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>            
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Tipo de Caracterstica' />

                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <TextControl
                    label="Nombre"
                    name='nombre'
                    value={nombre}
                    callBack={(e) => onChange(e, 'nombre')}
                />
             
            </div>
            <div
                className="col-md-6"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Tipos de Caractersticas' />
                <SpacerHorizontal />
                <Divider />
                <TableCargaTipoCaracteristica/>              
            </div>            
        </div>
    )
}

