import { Evento } from "../interfaces/interfaces";



export interface CalendarState {
    eventos: Evento[],
    eventosToDay: Evento[],
    eventosSeleccionados: Evento[],
    eventosProximos: Evento[],
    eventosFilter: Evento[],
    eventosIndexFilter : number | null,
    eventoSeleccionado: Evento | null,
    diaSeleccionado: string;
}

type CalendarAction =
    | { type: 'carga-data-eventos', payload: Evento[] }
    | { type: 'selecciona-evento', payload: Evento }
    | { type: 'selecciona-dia', payload: string }
    | { type: 'purga-evento' }

    | { type: 'add-eventosSeleccionados', payload: Evento[] }
    | { type: 'remove-eventosSeleccionados' }

    | { type: 'add-eventosToDay', payload: Evento[] }

    | { type: 'add-eventosProximos', payload: Evento[] }
    | { type: 'remove-eventosProximos' }

    | { type: 'add-eventosFilter', payload: Evento[] }
    | { type: 'remove-eventosFilter' }

    | { type: 'set-eventosIndexFilter', payload: number }
    | { type: 'clear-eventosIndexFilter' }

export const calendarReducer = (
    state: CalendarState,
    action: CalendarAction)
    : CalendarState => {

    switch (action.type) {
        case 'carga-data-eventos':
            return {
                ...state,
                eventos: action.payload
            }
        case 'selecciona-evento':
            return {
                ...state,
                eventoSeleccionado: action.payload
            }
        case 'purga-evento':
            return {
                ...state,
                eventoSeleccionado: null
            }
        case 'selecciona-dia':
            return {
                ...state,
                diaSeleccionado: action.payload
            }
        case 'add-eventosSeleccionados':
            return {
                ...state,
                eventosSeleccionados: action.payload
            }
        case 'add-eventosToDay':
            return {
                ...state,
                eventosToDay: action.payload
            }
        case 'remove-eventosSeleccionados':
            return {
                ...state,
                eventosSeleccionados: []
            }

        case 'add-eventosProximos':
            return {
                ...state,
                eventosProximos: action.payload
            }
        case 'remove-eventosProximos':
            return {
                ...state,
                eventosProximos: []
            }

        case 'add-eventosFilter':
            return {
                ...state,
                eventosFilter: action.payload
            }
        case 'remove-eventosFilter':
            return {
                ...state,
                eventosFilter: []
            }
        case 'set-eventosIndexFilter':
            return {
                ...state,
                eventosIndexFilter: action.payload
            }
            
        case 'clear-eventosIndexFilter':
            return {
                ...state,
                eventosIndexFilter: null
            }                          
        default:
            return state;
    }

}