import { useContext } from 'react';
import { InventarioContext } from '../../context/InventarioContext';
import { ProductoContext } from '../../context/ProductoContext';
import { Detalle, ProductosTransaccion } from '../../interfaces/interfaces';

interface InventarioDetalleItemProps {
    detalleItem : Detalle
}

export const InventarioDetalleItem = ({detalleItem} :InventarioDetalleItemProps) => {
    const { productos } = useContext(ProductoContext)
    const { lotes } = useContext(InventarioContext)
    let nombreProducto : string = '';
    let loteDescription : string = '';

    const productoDB = productos.filter((item) => item._id === detalleItem.producto._id);

    if (productoDB.length > 0) {
        nombreProducto = productoDB[0].nombre;
    } else {
        nombreProducto =  "";
    }
    
    
    const loteDB = lotes.filter((item) => item._id === detalleItem.lote._id);
    if (loteDB.length > 0) {
        loteDescription= loteDB[0].lote;
    } else {
        loteDescription= "";
    }

    return (
        <div style={{
            display:'flex', 
            flexDirection:'row', 
            justifyContent:'space-between', 
            alignItems: 'center'}}
        >
            <label style={{ flex: 5, fontSize: 18, }}>{nombreProducto}</label>
            <label style={{ flex: 2, fontSize: 18, }}>{loteDescription}</label>
            <label style={{ flex: 1, fontSize: 18, }}>{detalleItem.cantidad.toString()}</label>          
        </div>
    )
}