import { Cliente, DataFilters, MapPointer } from '../interfaces/interfaces';


export interface ClienteState {
    clientes: Cliente[],
    clientesFiltrados: Cliente[],
    clienteSeleccionado: Cliente | null,
    idClienteSeleccionado: string | null,
    dataFiltros: DataFilters[],
    indexClienteSeleccionado: number | null,
    mapPointers : MapPointer[]
}

type ClienteAction =
    | { type: 'carga-data-clientes', payload: Cliente[] }
    | { type: 'selecciona-cliente', payload: Cliente }
    | { type: 'selecciona-id-cliente', payload: string }
    | { type: 'seleccionado-cliente', payload: string }
    | { type: 'purga-cliente' }
    | { type: 'add-cliente', payload: Cliente }
    | { type: 'update-cliente', payload: Cliente }
    | { type: 'delete-cliente' }
    | { type: 'carga-data-filtros', payload: any[] }
    | { type: 'purga-data-filtros' }
    | { type: 'carga-data-cliente-filtrados', payload: Cliente[] }
    | { type: 'purga-data-cliente-filtrados' }

    | { type: 'carga-dataFilter-clientes', payload: Cliente[] }
    | { type: 'clear-dataFilter-clientes'}

    | { type: 'carga-index-cliente', payload: number}
    | { type: 'clear-index-cliente'}

    | { type: 'carga-map-pointers', payload: MapPointer[] }
    | { type: 'clear-map-pointers'}


export const clienteReducer = (state: ClienteState, action: ClienteAction) => {

    switch (action.type) {
        case 'carga-data-clientes':
            return {
                ...state,
                clientes: action.payload
            }
        case 'selecciona-cliente':
            return {
                ...state,
                clienteSeleccionado: action.payload
            }
        case 'selecciona-id-cliente':
            return {
                ...state,
                idClienteSeleccionado: action.payload
            }
        case 'purga-cliente':
            return {
                ...state,
                clienteSeleccionado: null
            }

        case 'add-cliente':
            return {
                ...state,
                clientes: [...state.clientes, action.payload]
            }
        case 'update-cliente':
            return {
                ...state,
                clientes: state.clientes.map((e) => e._id === action.payload._id ? action.payload : e)
            }
        case 'delete-cliente':
            return {
                ...state,
                clientes: state.clientes.filter((e) => e._id !== state.idClienteSeleccionado)
            }

        case 'carga-data-filtros':
            return {
                ...state,
                dataFiltros: action.payload
            }
        case 'purga-data-filtros':
            return {
                ...state,
                dataFiltros: []
            }

        case 'carga-data-cliente-filtrados':
        case 'carga-dataFilter-clientes':
            return {
                ...state,
                clientesFiltrados: action.payload
            }
        case 'purga-data-cliente-filtrados':
        case 'clear-dataFilter-clientes':
            return {
                ...state,
                clientesFiltrados: []
            }
        case 'carga-index-cliente':
            return {
                ...state,
                clienteSeleccionado: state.clientesFiltrados[action.payload]
            }
        case 'clear-index-cliente':
            return {
                ...state,
                indexClienteSeleccionado: null
            }
        case 'carga-map-pointers':
            return {
                ...state,
                mapPointers: action.payload
            }
        case 'clear-map-pointers':
            return {
                ...state,
                mapPointers: []
            }            
        default:
            return state;
    }


}