
import { Usuario } from '../interfaces/interfaces';
import { Chat, Notificaciones } from '../interfaces/socketInterfaces';


export interface SocketState {
    usuarios : Usuario[],
    usuariosOnLine: Usuario[],
    usuariosNewData: Usuario[],
    total: number,
    chat: Chat[],
    chatNoLeido: Chat[],
    chatActivo: string,

    notificacionesEnviadas: Notificaciones[],
    notificacionesRecibidas: Notificaciones[],
    notificacionActiva: Notificaciones | null,
    usuariosSeleccionados: Usuario[],
    usuariosDisponibles: Usuario[],

}

interface NotificacionesCarga {
    notificacionesEnviadas: Notificaciones[]
    notificacionesRecibidas: Notificaciones[]
}

type SocketAction =
    | { type: 'carga-usuarios', payload: Usuario[] }
    | { type: 'carga-usuarios-online', payload: Usuario[] }
    | { type: 'carga-usuarios-newdata', payload: Usuario[] }
    | { type: 'purga-usuarios-online' }
    | { type: 'purga-usuarios-newdata' }
    | { type: 'carga-chats', payload: Chat[] }
    | { type: 'purga-chats' }
    | { type: 'activa-chat', payload: string }
    | { type: 'reg-msg-chat', payload: Chat }
    | { type: 'leido-msg-chat', payload: Chat }
    | { type: 'no-leidos-msg-chat', payload: Chat[] }
    | { type: 'carga-notifis', payload: NotificacionesCarga }  //Notificaciones
    | { type: 'carga-notifis-recibidas', payload: Notificaciones[] }
    | { type: 'carga-notifis-enviadas', payload: Notificaciones[] }
    | { type: 'activa-notifi', payload: Notificaciones }
    | { type: 'carga-all-destino-notifi', }
    | { type: 'purga-destino-notifi', }
    | { type: 'carga-destino-notifi', payload: Usuario }
    | { type: 'elimina-destino-notifi', payload: Usuario }
    | { type: 'carga-disponibles', }
    | { type: 'agregar-disponible', payload: Usuario }
    | { type: 'eliminar-disponible', payload: Usuario }
    | { type: 'purgar-disponibles', }
    | { type: 'actualiza-notifi-activa', payload: Notificaciones }

    | { type: 'desactiva-notifi', }

export const socketReducer = (state: SocketState, action: SocketAction): SocketState => {

    switch (action.type) {

        case 'carga-usuarios':
            return {
                ...state,
                usuarios: action.payload,
            }                    
        //Usuarios en linea

        case 'carga-usuarios-online':
            return {
                ...state,
                usuariosOnLine: action.payload,
            }
        case 'purga-usuarios-online':
            return {
                ...state,
                usuariosOnLine: [],
            }

        // New Data de Usuarios
        case 'carga-usuarios-newdata':
            return {
                ...state,
                usuariosNewData: action.payload,
            }
        case 'purga-usuarios-newdata':
            return {
                ...state,
                usuariosNewData: [],
            }


        ///Chat

        case 'carga-chats':
            return {
                ...state,
                chat: action.payload,
            }
        case 'purga-chats':
            return {
                ...state,
                chat: [],
            }
        case 'activa-chat':
            return {
                ...state,
                chatActivo: action.payload,
            }
        case 'reg-msg-chat':
            return {
                ...state,
                chat: [...state.chat, action.payload],
            }
        case 'leido-msg-chat':
            return {
                ...state,
                chat: (state.chat.map((e) =>
                    e._id === action.payload._id ? action.payload : e
                ))
            };
        case 'no-leidos-msg-chat':
            return {
                ...state,
                chatNoLeido: action.payload,
            }

        //Notificaciones
        case 'carga-notifis':
            return {
                ...state,
                notificacionesEnviadas: action.payload.notificacionesEnviadas,
                notificacionesRecibidas: action.payload.notificacionesRecibidas,
            };
        case 'carga-notifis-recibidas':
            return {
                ...state,
                notificacionesRecibidas: action.payload,
            };
        case 'carga-notifis-enviadas':
            return {
                ...state,
                notificacionesEnviadas: action.payload,
            };
        case 'activa-notifi':
            return {
                ...state,
                notificacionActiva: action.payload,
            };
        case 'carga-all-destino-notifi':
            return {
                ...state,
                usuariosSeleccionados: state.usuariosOnLine,
            };
        case 'purga-destino-notifi':
            return {
                ...state,
                usuariosSeleccionados: [],
            };
        case 'carga-destino-notifi':
            return {
                ...state,
                usuariosSeleccionados: [...state.usuariosSeleccionados, action.payload],
            };
        case 'elimina-destino-notifi':
            return {
                ...state,
                usuariosSeleccionados: (state.usuariosSeleccionados.filter((e) =>
                    e.uid !== action.payload.uid
                ))
            };

        case 'carga-disponibles':
            return {
                ...state,
                usuariosDisponibles: state.usuariosOnLine
            };
        case 'agregar-disponible':
            return {
                ...state,
                usuariosDisponibles: [...state.usuariosDisponibles, action.payload],
            };
        case 'eliminar-disponible':
            return {
                ...state,
                usuariosDisponibles: (state.usuariosDisponibles.filter((e) =>
                    e.uid !== action.payload.uid
                ))
            };
        case 'purgar-disponibles':
            return {
                ...state,
                usuariosDisponibles: []
            };
        case 'actualiza-notifi-activa':
            return {
                ...state,
                notificacionActiva: action.payload,
            };
        //Default
        default:
            return state;
    }

}