import { useContext } from "react";
import { Link } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import { MenuItem } from "./ui/atom/MenuItem";
import { MenuItemPerfil } from "./ui/atom/MenuItemPerfil";

export const NavbarMain = () => {
  const { user } = useContext(AuthContext);

  if (!user) return null

  return (
    <div>
      <nav className="navbar navbar-expand-sm bg-dark navbar-dark ">
        <Link className="navbar-brand" to="/">
          <i className="fa fa-home bigicon mr-3"></i>
          Visita Mdica
        </Link>
        <div className="navbar-collapse">
          <div className="navbar-nav">
            {(user.rol === "SUPER_ADMIN_ROLE" || user.rol === "ADMIN_ROLE") && (
              <MenuItem route="/home/admin" caption="Administracin" />
            )}
            <MenuItem route="/home/calendar" caption="Calendario" />
            <MenuItem route="/home/clientes" caption="Clientes" />
            <MenuItem route="/home/visitas" caption="Visitas" />
            <MenuItem route="/home/vademecum" caption="Vademecum" />
            <MenuItem route="/home/inventario" caption="Inventario" />
            <MenuItem route="/home/rutas" caption="Rutas" />
          </div>
        </div>
        <div className="navbar-collapse collapse w-100 order-3 dual-collapse2" style={{justifyItems:'center'}}>
          <ul className="navbar-nav ml-auto">
            <MenuItemPerfil route="/home/user/perfil" caption={user.nombre} />
          </ul>
        </div>
      </nav>
    </div>
  );
};
