import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { useContext } from 'react';
import { InventarioContext } from '../../context/InventarioContext';
import { TextControlView } from '../../components/ui/atom/FormControls';
import moment from 'moment';
import { InventarioDetalleItem } from '../../components/inventario/InventarioDetalleItem';
import { Separacion } from '../../components/miselaneos/Separacion';
    
export const InventarioTransaccionScreen = () => {

const {transaccionSeleccionada} = useContext(InventarioContext)

    if(!transaccionSeleccionada){ return <text>Cargando</text>}

    const { signo, anotaciones, aplicado, fecha, usuario, detalle, visita } = transaccionSeleccionada

    const titulo = signo === 1 ? 'Transaccin de Entrada' : 'Transaccin de Salida'

    

    return (
        <div className='flexbox-container'>
            <div className='col-md-2'>
                <NavbarLeft />
            </div>

            <div
                className='col-md-10'
                style={{ height: '88.5vh', overflowY: 'scroll' }}
            >
                <div>
                    <TitlePage title={titulo} />
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <TextControlView 
                        label = {'Fecha: '}
                        value = {moment(transaccionSeleccionada.fecha).format('DD-MM-YYYY | HH:MM a')}
                    />
                    <TextControlView 
                        label = {'Anotaciones: '}
                        value = {transaccionSeleccionada.anotaciones}
                    />
                    <TextControlView 
                        label = {'Visitador: '}
                        value = {transaccionSeleccionada.usuario.nombre}
                    />
                    <TextControlView 
                        label = {'Aplicada: '}
                        value = {transaccionSeleccionada.aplicado ? 'Si' : 'No'}
                    />                    
                    <TextControlView 
                        label = {'Descargada: '}
                        value = {transaccionSeleccionada.descargado ? 'Si' : 'No'}
                    />                                                          
                </div>
                <h3>Detalle</h3>
                <Separacion />

                <div>
                <div style={{
                    display:'flex', 
                    flexDirection:'row', 
                    justifyContent:'space-between', 
                    alignItems: 'center'}}
                >
                    <label style={{ flex: 5, fontSize: 18, }}>Producto</label>
                    <label style={{ flex: 2, fontSize: 18, }}>Lote</label>
                    <label style={{ flex: 1, fontSize: 18, }}>Cantidad</label>          
                </div>
                <Separacion />
                    {
                        detalle.map(item => (
                            <div key={item.producto._id + item.lote}>
                                <InventarioDetalleItem
                                    detalleItem = {item}
                                />
                            <Divider />
                            </div>
                        ))
                    }
                </div>                
            </div>
        </div>
    )
}