import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage';
import { useHistory } from 'react-router';
import { BtnTypeEnter } from '../../components/ui/atom/FormControls';
import { EnvededReportBIMax } from '../bi/EnvededReportBIMax';
    
export const RutasConsulta = () => {

    const history = useHistory();

    const handleMaximize  = () => {
        history.push("/home/rutasmax");
    }


    return (
        <div className='flexbox-container'>
            <div className='col-md-2'>
                <NavbarLeft />
            </div>
            <div
                className='col-md-10'
                style={{ height: '88.5vh', overflowY: 'scroll' }}
            >
                <div>
                    <TitlePage title='Consulta de Recorridos' />
                    <div className='ml-4'>
                        <BtnTypeEnter 
                            label='Ver +'
                            callBack={handleMaximize}
                            icon="fa fa-window-maximize "
                            color='yellow'
                            type = 'small'
                        />
                    </div>
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <EnvededReportBIMax tipo ={"kpis"} />
                </div>

            </div>
        </div>
        )
}