import { Status, Usuario } from '../interfaces/interfaces';

export interface AuthState {
    status: Status;
    token: string | null;
    errorMessage: string;
    user: Usuario | null;
    tmpImage: string | null;
}

type AuthAction =
    | {
        type: 'singUp', payload: {
            token: string,
            user: Usuario
        }
    }
    | { type: 'registraUsuario', payload: Usuario }
    | { type: 'addError', payload: string }
    | { type: 'removeError' }
    | { type: 'notAuthenticated' }
    | { type: 'logout' }
    | { type: 'loadingAuth' }
    | { type: 'add-imgTmp', payload: string }
    | { type: 'remove-imgTmp' }


export const authReducer = (state: AuthState, action: AuthAction): AuthState => {

    switch (action.type) {

        case 'addError':
            return {
                ...state,
                status: 'not-authenticated',
                user: null,
                token: null,
                errorMessage: action.payload
            }

        case 'loadingAuth':
            return {
                ...state,
                status: 'loading-authenticated',
            }
        case 'registraUsuario':
            return {
                ...state,
                user: action.payload
            }

        case 'removeError':
            return {
                ...state,
                errorMessage: ''
            }

        case 'singUp':
            return {
                ...state,
                errorMessage: '',
                status: 'authenticated',
                token: action.payload.token,
                user: action.payload.user,
            }
        case 'add-imgTmp':
            return {
                ...state,
                tmpImage: action.payload,
            }
        case 'remove-imgTmp':
            return {
                ...state,
                tmpImage: null,
            }

        case 'notAuthenticated':
        case 'logout':
            return {
                ...state,
                status: 'not-authenticated',
                user: null,
                token: null,
            }

        default:
            return state;
    }

}