import { useContext, useState } from "react";
import { Calendar, momentLocalizer } from "react-big-calendar";
import moment from "moment";

import { messages } from "../../helpers/calendar-messages-es";
import { CalendarEvent } from "./CalendarEvent";

import "react-big-calendar/lib/css/react-big-calendar.css";
import "moment/locale/es";

import { AddNewFab } from "../../components/AddNewFab";
import { AppContext } from "../../context/AppContext";
import { AuthContext } from "../../context/AuthContext";
import { NavbarLeft } from "../../components/NavbarLeft";
import { DateColorEvent } from "../../helpers/eventsFilter";
import { EventsPageCol } from "./EventsPageCol";
import { CalendarContext } from '../../context/CalendarContext';
import { SpacerHorizontal } from "../../components/ui/atom/Spacer";
import { Divider } from "@material-ui/core";
import { TitlePage } from "../../components/ui/atom/TitlePage";

moment.locale("es");
const localizer = momentLocalizer(moment);



export const CalendarioScreen = () => {
  const { showModalLoadingCalendar } = useContext(AppContext);
  const {eventos, selecEvento, purgaEventoSelecciondo} = useContext(CalendarContext)


  const { user } = useContext(AuthContext);
  const [lastView, setLastView] = useState(
    localStorage.getItem("lastView") || "month"
  );

  const calendarSize = user?.rol === 'USER_ROLE' ? '75vh' : '65vh'

  if (!eventos) return null;

  const onSelectEvent = (e) => {
    selecEvento(e);
    showModalLoadingCalendar();
  };
  const onViewChange = (e) => {
    setLastView(e);
    localStorage.setItem("lastView", e);
  };

  const onSelectSlot = (e) => {
    purgaEventoSelecciondo();
  };

  const eventStyleGetter = (event, start, end, isSelected) => {
    const style = {
      // backgroundColor: user.uid === event.user._id ? "#000000" : "#99FF00",
      color: user?.uid === event.user._id ? "#367cf7" : "#000000",
      backgroundColor: DateColorEvent(start),
      borderRadius: "0px",
      opacity: 0.8,
      display: "block",
      // color: "#367cf7",
    };
    return {
      style,
    };
  };

  return (
    <div className="flexbox-container">
      <div className="col-md-2">
        <NavbarLeft />
      </div>

      <div className="col-md-8">
        <div className="mt-1 calendar-screen mr-0 ml-0">
          
          <TitlePage title='Calendario' />
          <SpacerHorizontal />
          <Divider />
          <SpacerHorizontal />          
          <h6>Filtros aqui</h6>
          <Calendar
            // views={{ month: true, a }}
            views={['month']}
            localizer={localizer}
            events={eventos}
            startAccessor="start"
            endAccessor="end"
            style={{ height: calendarSize }}
            messages={messages}
            eventPropGetter={eventStyleGetter}
            onSelectEvent={onSelectEvent}
            onView={onViewChange}
            onSelectSlot={onSelectSlot}
            selectable={true}
            view={lastView}
            components={{ event: CalendarEvent }}
            
          />
        </div>
      </div>

      
      <div className="col-md-2" style={{ height: "87vh", overflowY: "scroll" }}>
        <h6>Eventos en el tiempo</h6>
        <EventsPageCol />
        {(user?.rol === "ADMIN_ROLE" || user?.rol === "SUPER_ADMIN_ROLE") && (
          <AddNewFab />
        )}
      </div>
    </div>
  );
};
