import { Caracteristicas } from "../../interfaces/interfaces";
import { BtnTypeEnterInvert } from "../ui/atom/FormControls";

interface itemCategoriaProps {
    categoria : Caracteristicas;
    callBack : (categoria : string) => void;
}

export const ItemCategoria = ({categoria, callBack} : itemCategoriaProps) => {
    return (
        <div className='mt-2'  style={{display:'flex'}}>
            <BtnTypeEnterInvert 
                label={categoria.nombre}
                callBack={() => callBack(categoria._id)}
                icon="fa fa-capsules"
                color='black'
                type = 'large'
            />
        </div>
    )
}

