import { createContext, useReducer } from 'react';
import { Message, messagesReducer, MessagesState } from '../reducers/messagesReducer';

//Interfaces
interface ContextProps {
    errorMessage: Message[]
    notificationMessage: Message[]
    registraError: (error: Message) => void;
    prugaError: () => void;
    registraNotification: (notifi: Message) => void;
    prugaNotification: () => void;
}

const initialState: MessagesState = {
    errorMessage: [],
    notificationMessage: [],
}

//Contexto
export const MessagesContext = createContext({} as ContextProps)
//provider
export const MessagesProvider = ({ children }: any) => {
    const [state, dispatch] = useReducer(messagesReducer, initialState)

    const registraError = ({ title = '', msg = '', type = 'error' }: Message) => {
        dispatch({
            type: 'addError',
            payload: [{
                type,
                title,
                msg
            }]
        })
    }
    const prugaError = () => {
        dispatch({
            type: 'removeError',
        })
    }
    const registraNotification = ({ title = '', msg = '', type = 'error' }: Message) => {
        dispatch({
            type: 'addNotification',
            payload: [{
                type,
                title,
                msg
            }]
        })
    }
    const prugaNotification = () => {
        dispatch({
            type: 'removeNotification',
        })
    }

    return (
        <MessagesContext.Provider value={{
            ...state,
            registraError,
            prugaError,
            registraNotification,
            prugaNotification,
            // messageToast
        }}>
            {children}
        </MessagesContext.Provider>
    )
}
