import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { useContext } from 'react';
import { InventarioContext } from '../../context/InventarioContext';
import moment from 'moment';

interface TransaccionScreenProps {
    idVisita : string
}
    
export const TransaccionScreen = ({idVisita} :TransaccionScreenProps) => {

    const {transacciones, lotes} = useContext(InventarioContext)
    const transaccion = transacciones.filter((item) => item.visita === idVisita)

    const loteDesc  = (uid: string) => {
        const lote = lotes.filter((lote) => uid === lote._id);
        if (lote.length > 0) {
            return lote[0].lote;
        } else {
            return "";
        }        
    }
   
    return (
            <div>
                <TitlePage title='Entrega de Muestras' />
                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                {transaccion.map(item => (
                    <div style={{ display: 'flex', flexDirection:'column' }} key={item._id}>
                        <span>{`Fecha: ${ moment( item.fecha).format("DD-MM-YYYY | HH:MM")}`}</span>
                        <br/>
                        <span>{item.anotaciones}</span>
                        <br/>
                        <span>{`Visitador: ${item.usuario.nombre}`}</span>
                        <br/>
                        <br/>
                        <div className='col-md-10' style={{display:'flex', justifyContent:'space-between'}}>
                            <span className='col-md-8'> Producto </span>
                            <span style={{textAlign:'center'}} className='col-md-2'> Cantidad </span>
                            <span style={{textAlign:'center'}} className='col-md-2'> Lote </span>
                        </div>
                        <Divider />
                        {
                            item.detalle.map(det => (
                                <div key={det._id} className='col-md-10' style={{display:'flex', justifyContent:'space-between'}}>
                                    <span className='col-md-8'> {det.producto.nombre} </span>
                                    <span style={{textAlign:'center'}} className='col-md-2'> {det.cantidad} </span>
                                    <span style={{textAlign:'center'}} className='col-md-2'> { loteDesc(det.lote._id)} </span>
                                </div>
                            ))
                        }
                        <Divider />
                    </div>
                ))}
                <SpacerHorizontal />
            </div>
        
   )
}