import { Divider } from '@material-ui/core'
import { useContext, useEffect } from 'react'
import { NavbarLeft } from '../../components/NavbarLeft'
import { TableClientesInactivos } from '../../components/tablas/clientes/TableClientesInactivos'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { ClienteContext } from '../../context/ClienteContext'
import { Cliente } from '../../interfaces/interfaces'

export const ClientesInactivosScreen = () => {

    const { clientes, cargaClientesFiltrados } = useContext(ClienteContext)
    
    useEffect(() => {
        const clientesInactivos: Cliente[] = clientes.filter(cliente => cliente.causabaja.nombre !== 'OPERATIVO')
        cargaClientesFiltrados(clientesInactivos)
    }, [clientes])

    return (
        <div className="flexbox-container">
            <div className="col-md-2">
                <NavbarLeft />
            </div>

            <div
                className="col-md-10"
                style={{ height: "88.5vh", overflowY: "scroll" }}
            >
                <div>
                    <TitlePage title='Clientes Inactivos' />
                    <SpacerHorizontal />
                    <Divider />
                    <SpacerHorizontal />
                    <TableClientesInactivos />
                </div>
            </div>
        </div>
    )
}
