import { useContext, useState } from "react"
import { Divider } from "@material-ui/core"
import { SelectPais, SelectProvincia, TextControl, SelectCanton } from '../../components/ui/atom/FormControls';
import { SpacerHorizontal } from "../../components/ui/atom/Spacer"
import { MasterDataContext } from "../../context/MasterDataContext"
import { useForm } from "../../hooks/useForm"
import { TableDistrito } from "../../components/tablas/geo/TableDistrito"
import { TitlePage } from "../../components/ui/atom/TitlePage";

export const DistritoScreen = () => {

    const { distrito, canton, onChange  } = useForm({
        distrito: "",
        canton: "",
    })

    const {paises, provincias, cantones, crearDistrito} = useContext(MasterDataContext)
    const [pais, setPais] = useState('')
    const [provincia, setProvincia] = useState('')
    const provinciasFilter = provincias.filter(provincia => provincia.pais._id === pais)
    const cantonesFilter = cantones.filter(canton => canton.provincia._id === provincia)

    const handleAdd =  (e: any) => {
        e.preventDefault()
        crearDistrito({nombre : distrito, canton});
    }

    const changePais  = (e : string) => {
        setPais(e)
        onChange('', 'canton')
    }

    return (
        <div className="flexbox-container">
            <div
                className="col-md-4"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Crear Distrito' />

                <SpacerHorizontal />
                <Divider />
                <SpacerHorizontal />
                <button 
                    className='btn btn-success ml-2' 
                    onClick={handleAdd}
                >
                    Guardar
                </button>
                <SpacerHorizontal />
                <SelectPais
                    label="Pas"
                    name='pais'
                    value={pais}
                    callBack={(e) => changePais(e)}
                    data={paises}
                />   
                <SelectProvincia
                    label="Provincia"
                    name='provincia'
                    value={provincia}
                    callBack={(e) => setProvincia(e)}
                    data={provinciasFilter}
                /> 
                <SelectCanton
                    label="Cantn"
                    name='canton'
                    value={canton}
                    callBack={(e) => onChange(e, 'canton')}
                    data={cantonesFilter}
                />                                          
                <TextControl
                    label="Distrito"
                    name='distrito'
                    value={distrito}
                    callBack={(e) => onChange(e, 'distrito')}
                />
            </div>
            <div
                className="col-md-8"
                style={{ height: "74vh", overflowY: "scroll" }}
            >
                <TitlePage title='Lista de Distritos' />
                <Divider />
                <TableDistrito/>              
            </div>            
        </div>
    )
}

