import { useContext, useEffect } from "react";
import { BrowserRouter as Router, Switch, Redirect } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import { AuthRouter } from "./AuthRouter";
import { DashboardRoutes } from "./DashboardRoutes";

import { PublicRouter } from "./PublicRouter";
import { PrivateRouter } from "./PrivateRouter";
import "../css/login-register.css";

import { UsuarioVerify } from "../components/miselaneos/UsuarioVerify";
import { MessagesContext } from "../context/MessagesContext";
import { errorSimpleFire, exitoFireOK } from "../components/ui/messagesUI";
import { AppContext } from "../context/AppContext";

export const AppRouter = () => {
  const { status } = useContext(AuthContext);
  const { state } = useContext(AppContext);
  const { notificationMessage, prugaNotification } = useContext(MessagesContext);

  useEffect(() => {
    if (notificationMessage.length > 0) {
      switch (notificationMessage[0].type) {
        case "error":
          errorSimpleFire(notificationMessage[0].msg)
          break;
        case "success":
          // exitoFire(notificationMessage[0].msg, 5000)
          exitoFireOK(notificationMessage[0].msg)
          break;

        default:
          break;
      }
      prugaNotification()
    }
  }, [notificationMessage, prugaNotification])

  //TODO: Crear una pantalla de loading

  if (status === 'checking') return <UsuarioVerify />
  if (status === 'loading-authenticated') return <UsuarioVerify />

  return (
    <Router >
      <div>
        <Switch>
          <PublicRouter
            path="/auth"
            component={AuthRouter}
            isAuthenticated={status}
          />
          <PrivateRouter
            path="/home"
            component={DashboardRoutes}
            isAuthenticated={status}
          />
          <Redirect to="/home" />
        </Switch>
      </div>

    </Router>
  );
};
