import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { CategoriasList } from '../../components/productos/CategoriasList';
import { ProductosList } from '../../components/productos/ProductosList';
    
export const VadeScreen = () => {
    return (
        <div className='flexbox-container'>
           <div className='col-md-2'>
               <NavbarLeft />
           </div>
    
           <div
               className='col-md-10'
               style={{ height: '88.5vh', overflowY: 'scroll' }}
           >
               <div >
                   <TitlePage title='Vademecum' />
                   <SpacerHorizontal />
                   <Divider />
                   <SpacerHorizontal />
                   <div style={{display:'flex', flexDirection:'row' }}>
                        <CategoriasList/>
                        <ProductosList />
                   </div>
               </div>
           </div>
       </div>
   )
}