import { useContext } from "react";
import Modal from "react-modal";
import DateTimePicker from "react-datetime-picker";

import { useForm } from "../../hooks/useForm";
import { AppContext } from "../../context/AppContext";
import { AuthContext } from "../../context/AuthContext";
import { errorSimpleFire, exitoFire, viewErrors } from "../ui/messagesUI";
import { SelectItem, SelectVisitador, TextControl } from "../ui/atom/FormControls";
import { SocketContext } from "../../context/SocketContext";
import { ProductoContext } from '../../context/ProductoContext';
import { InventarioContext } from '../../context/InventarioContext';
import { LoteAddData } from "../../interfaces/interfaces";
import { useVisitadorPais } from "../../hooks/useVisitadorPais";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
    marginRight: "-50%",
    transform: "translate(-50%, -50%)",
  },
};

Modal.setAppElement("#root");

export const LoteModal = () => {
  const {
    state,
    hideLoteAdd,
  } = useContext(AppContext);
  
  const { isLoadingLoteAdd } = state;

  const {usuarios} = useContext(SocketContext)
  const {user} = useContext(AuthContext)

  const {productos} = useContext(ProductoContext)  
  const productosMuestras = productos.filter(producto => producto.original === false)

  const {addLote, cargaDataInventario} = useContext(InventarioContext)  
  const {visitadores} =  useVisitadorPais()


  // const now = new Date();
  
  const initLote :LoteAddData = {
    usuariopropietario: '',
    lote: '',
    producto: '',
    lotevence: '',
    original: false,
    saldo : 0,
  }

  const {usuariopropietario, lote, lotevence, producto, onChange, setForm, form } = useForm<LoteAddData>(initLote)

  const closeModal = () => {
    hideLoteAdd();
  };


  const handleStartDateChange = (e) => {
    setForm({
      ...form,
      lotevence: e,
    });
  };

  const handleSubmitForm = async (e) => {
    e.preventDefault();

      if(!lote || lote ==='') {
        return errorSimpleFire('El lote es requerido')
      }
      if(!usuariopropietario || usuariopropietario ==='') {
        return errorSimpleFire('El Visitador es requerido')
      }
      if(!producto || producto ==='') {
        return errorSimpleFire('El Producto es requerido')
      }
      if(!lotevence || lotevence ==='') {
        return errorSimpleFire('La fecha de Vencimiento es requerida')
      }
      const resp:any = await addLote(form);
      if (!resp.ok) {
        return viewErrors(resp);
      }

      exitoFire("Lote creado con xito");
      setForm(initLote)
      cargaDataInventario()
      closeModal();
  };


  return (
    <Modal
      isOpen={isLoadingLoteAdd}
      onRequestClose={closeModal}
      style={customStyles}
      closeTimeoutMS={200}
      className="modal"
      overlayClassName="modal-fondo"
    >
      <div
        style={{ height: "73vh", overflowY: "scroll", alignItems: "center" }}
      >
        <div style={{ backgroundColor: "#EEEEEE", borderRadius: 5 }}>
          <h1 className="ml-2">Crear Lote</h1>
        </div>
        <hr />
        <form className="container" onSubmit={handleSubmitForm}>

          <TextControl 
            label="Lote"
            name='lote'
            value={lote}
            callBack={(e) => onChange(e, "lote")}
          />  

          <hr/>          

          <div className="form-group" style={{display:'flex', alignItems:'center'}}>
            <SelectVisitador 
                label='Visitador'
                name='uid'
                value={usuariopropietario}
                data={visitadores}
                callBack={(e) => onChange( e, 'usuariopropietario')}
            />
          </div>

          <div className="form-group">
            <SelectItem 
              label = "Producto"
              name="producto"
              value = {producto}
              data={productosMuestras}
              callBack={(e) => onChange(e, "producto")}
            />
          </div>

          

            <div className="form-group">
              <label>Vencimiento</label>
              <DateTimePicker
                onChange={handleStartDateChange}
                value={lotevence}
                className="form-control"
                format="dd/MM/yyyy"
              />
          </div>

          <hr />

          <button
            type="submit"
            className="btn btn-outline-primary btn-block mr-2 mt-lg-1"
          >
            <i className="far fa-save"></i>
            <span> Guardar</span>
          </button>
        </form>
        
      </div>
    </Modal>
  );
};
