import moment, { Moment } from "moment";

const currentDate = moment().set({
  hour: 0,
  minute: 0,
  second: 0,
  millisecond: 0,
  year: 0,
});

export const diasDiff = (fechaOrigen : any) => {
  const dateCelebracion = moment(fechaOrigen);
  const dateCelebracionDia = moment(dateCelebracion).set({
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
    year: 0,
  });
  return dateCelebracionDia.diff(currentDate, "days");
};

export const fechaDiff = (fechaOrigen : Moment) => {
  const currentDate = moment().set({
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  });
  const date = moment(fechaOrigen);
  const dateDays = moment(date).set({
    hour: 0,
    minute: 0,
    second: 0,
    millisecond: 0,
  });
  return dateDays.diff(currentDate, "days");
};


export const fechaDiffMsg = (fechaOrigen : Moment) => {
  const dias = fechaDiff(fechaOrigen)
  if (dias === 0) return ' hoy'
  if (dias < 0) return ' hace ' + Math.abs(dias) + ' das'
  if (dias > 0) return ' dentro de ' + Math.abs(dias)  + ' das'
};

export const createCalendarDate = (start: string) => { 

  let horas = 0;
  let minutos = 0;

  if (start !== '') {
     horas = moment(start).get('hours')
     minutos = moment(start).get('minutes')
  }
  const fechaSalida = moment(start).set({
    hour: horas,
    minute: minutos,
    second: 0,
    millisecond: 0,
  })
  return fechaSalida
  
 } 