import { useContext } from "react";
import Modal from "react-modal";
import { AppContext } from "../../../context/AppContext";
import "../../../css/modal.css";

import { Spiner } from "./Spiner";

const customStyles = {
  content: {
    top: "50%",
    left: "50%",
    right: "auto",
    bottom: "auto",
  },
};

Modal.setAppElement("#root");

export const ProcesingModal = () => {
  const { state } = useContext(AppContext);
  
  return (
    <Modal
      isOpen={state.isLoadingProcesing}
      style={customStyles}
      closeTimeoutMS={200}
      className="modal-spiner"
      overlayClassName="modal-fondo"
    >
      <div
        style={{
          alignItems: "center",
          flexDirection: "column",
          display: "flex",
        }}
      >
        <label style={{ color: "#4285F4", marginBottom:40 }}>Procesando</label>
        <Spiner />
        <span style={{color: "#4285F4", marginTop:10 }}>No interrumpa el proceso</span>
        <span  style={{color: "#4285F4" }}>Por favor espere...!</span>

      </div>
    </Modal>
  );
};
