import { useMap } from '../../hooks/useMap';
import { MapStyleControlSelect } from '../ui/atom/FormControls';
import { MapPointer } from '../../interfaces/interfaces';

interface MapVisitaContainerProps {
    mapPointers : MapPointer[]
}

export const MapVisitaContainer = ({mapPointers} : MapVisitaContainerProps ) => {


    const {handleSelectStyle, styleMap, setRef} = useMap(mapPointers)

    return (
        <div className='mapContainer'>
            <MapStyleControlSelect 
                label = 'Capa'
                value = {styleMap}
                name = 'styleMap'
                callBack={(e) => handleSelectStyle(e)}
            />
            <div 
                className='mapContainer'
                ref = {setRef}
            />
       </div>
   )
}