import React, { useContext } from "react";
import { Divider } from "@material-ui/core";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelParametros = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/admin" caption={etiquetaHome} />

          <Divider />
          {user?.rol === "SUPER_ADMIN_ROLE" && (
            <>
              <MenuItem
                route="/home/param"
                caption="Parmetros"
                icon="fa fa-cogs"
              />
              <Divider />
            </>
          )}
          <MenuItem
            route="/home/usuariosadd"
            caption="Crear Usuarios"
            icon="fa fa-user-plus"
          />
          <MenuItem
            route="/home/usuarios"
            caption="Usuarios"
            icon="fa fa-users"
          />
          <Divider />
          <MenuItem
            route="/home/geo"
            caption="Dist. Geogrficas"
            icon="fa fa-atlas"
          />
          <Divider />
          <MenuItem
            route="/home/caracteristicasgrupos"
            caption="Tipos de Caractersticas"
            icon="fa fa-object-group"
          />          
          <MenuItem
            route="/home/caracteristicas"
            caption="Caractersticas"
            icon="fa fa-object-group"
          />
        </div>
      );
    default:
      return <PanelVacio />
  }
};
