import { useContext } from "react";
import { Divider } from "@material-ui/core";

import { MenuItem } from "../ui/atom/MenuItem";
import { AuthContext } from "../../context/AuthContext";
import { PanelVacio } from "./PanelVacio";

interface Props {
  etiquetaHome: string
}

export const PanelCalendario = ({ etiquetaHome = '' }: Props) => {
  const { user } = useContext(AuthContext);

  switch (user?.rol) {
    case "SUPER_ADMIN_ROLE":
    case "ADMIN_ROLE":
    case "JEF_VIS_ROLE":
    case "VIS_USER_ROLE" :
      return (
        // <div className="navbar-nav">
        <div className="navbar-collapse">
          <MenuItem route="/home/calendar" caption={etiquetaHome} />

          <Divider />
          <MenuItem
            route="/home/calendario"
            caption="Calendario"
            icon="fa fa-calendar-alt"
          /> 
          {/* {user?.rol !== "VIS_USER_ROLE" && (
            <> */}
              <MenuItem
                route="/home/agendas"
                caption="Agendas"
                icon="fa fa-calendar"
              />
              <Divider />
            {/* </>
          )}
         
          <MenuItem
            route="/home/eventos"
            caption="Eventos"
            icon="fa fa-calendar-alt"
          />        */}
        </div>
      );
    default:
      return <PanelVacio />
  }
};
