import moment from "moment"
import { useContext } from "react"
import { AuthContext } from "../../context/AuthContext"
import { SocketContext } from "../../context/SocketContext"
import { useForm } from "../../hooks/useForm"
import { BtnTypeEnter, SelectDateddMMyyyy, SelectVisitador, TextControlNumCol } from "../ui/atom/FormControls"
import { CalendarContext } from '../../context/CalendarContext';
import { Evento } from '../../interfaces/interfaces';
import { file_Download_Eventos } from '../../helpers/file_Download';
import { TableEventos } from '../tablas/calendario/TableEventos';
import { useVisitadorPais } from "../../hooks/useVisitadorPais"

interface Form {
    uid?: string
    inicio: any;
    fin: any;
    max: number;
}

export interface CalendarResponse {
    ok: boolean;
    msg: string;
    total: number;
    eventos: Evento[];
  }


export const AgendasFilterOptions  = () => {

    const {usuarios} = useContext(SocketContext)
    const {user} = useContext(AuthContext)
    const {cargaDataFilter, eventosFilter, purgaDataFilter,clearDataFilterIndex} = useContext(CalendarContext)

    const initForm = {
        uid : user?.uid,
        inicio: new Date(moment().startOf('isoWeek').toDate()),
        fin: new Date(moment().endOf('isoWeek').toDate()),
        max: 50
    }
    const {uid, inicio, fin, max, onChange, setForm, form } = useForm<Form>(initForm)
    const {visitadores} =  useVisitadorPais()

    const handleDownloadFile  = () => {
        file_Download_Eventos({eventosFilter})
    }

    const handleFilter  = () => {
        cargaDataFilter({inicio, fin, uid, max})
    }

      const onSubmitClear  = () => {
        purgaDataFilter()
        clearDataFilterIndex()
        setForm(initForm)
    }

    const handleStartDateChange = (e) => {
        setForm({
            ...form,
            inicio: e,
        });
        };
    
    const handleEndDateChange = (e) => {
        setForm({
            ...form,
            fin: e,
        });
    };

    return (
        <div>
            <div className="imput_box_container">
                <SelectDateddMMyyyy 
                    label={'Fecha Inicial'}
                    name = {"inicio"}
                    value = {inicio}
                    callBack={handleStartDateChange}
                />
                <SelectDateddMMyyyy 
                    label={'Fecha Final'}
                    name = {"final"}
                    value = {fin}
                    callBack={handleEndDateChange}
                />
                <TextControlNumCol
                    label='Mostrar'
                    value={max}
                    name='max'
                    callBack={(e) => onChange( e, 'max')}
                />
                <SelectVisitador 
                    label='Visitador'
                    name='uid'
                    value={uid}
                    data={visitadores}
                    callBack={(e) => onChange( e, 'uid')}
                />
                <div className='ml-4 mt-3'>
                    <BtnTypeEnter 
                        label='Filtrar'
                        callBack={handleFilter}
                        icon="fa fa-filter"
                        color='yellow'
                        type = 'small'
                    />
                    <BtnTypeEnter 
                        label='Limpiar'
                        callBack={onSubmitClear}
                        icon="fa fa-broom"
                        color='red'
                        type = 'small'
                    />
                    {
                        eventosFilter.length > 0 &&
                        <BtnTypeEnter 
                            label='Archivo'
                            callBack={handleDownloadFile}
                            icon="fa fa-file-csv"
                            color='green'
                            type = 'small'
                        />                    
                    }                            
                </div>
            </div>
            {
                eventosFilter.length > 0 &&
                <TableEventos eventos={eventosFilter} />
            }
        </div>
    )
}