import { NavbarLeft } from '../../components/NavbarLeft'
import { SpacerHorizontal } from '../../components/ui/atom/Spacer'
import { Divider } from '@material-ui/core';
import { TitlePage } from '../../components/ui/atom/TitlePage'
import { useContext, useEffect } from 'react';
import { ClienteContext } from '../../context/ClienteContext';
import { ImgPerfil } from '../../components/ui/atom/ImgPortada';
import { MapContainer } from '../../components/mapa/MapContainer';
import { MapPointer } from '../../interfaces/interfaces';
    
export const ClienteScreen = () => {

    const {clienteSeleccionado, cargaMapPointers} = useContext(ClienteContext)

    useEffect(() => {
        if (!clienteSeleccionado) {return}
        const data : MapPointer[] = [{
            lng: gpsLong,
            lat: gpsLat,
            id: _id,
            name: nombre,
            description: direccion,
            especialidad : especialidad.nombre,
        }]
        cargaMapPointers(data)
    }, [clienteSeleccionado])


    if (!clienteSeleccionado) {return null}

    const {nombre,
            telefono1,
            telefono2,
            email,
            especialidad,
            grupocliente,
            causabaja,
            provincia,
            canton,
            distrito,
            barrio,
            direccion,
            colegiado,
            secretaria,
            diasvisita,
            horavisita,
            observaciones,
            images,
            gpsLong,
            gpsLat,
            _id

    } = clienteSeleccionado



    return (
        <div className='flexbox-container'>
           <div className='col-md-2'>
               <NavbarLeft />
           </div>
    
           <div
               className='col-md-10'
               style={{ height: '88.5vh', overflowY: 'scroll' }}
           >
               <div>
                   <TitlePage title='Ficha del Cliente' />
                   <SpacerHorizontal />
                   <Divider />
                   <SpacerHorizontal />
               </div>
               <div style={{display :'flex', flexDirection:'column'}}>
                <TitlePage title={nombre} />
                <Divider />

                <label>Especialidad: {especialidad.nombre}</label>
                <label>Grupo: {grupocliente.nombre}</label>
                <label>Estado: {causabaja.nombre}</label>
                <Divider />

                <label>Provincia: {provincia.nombre}</label>
                <label>Cantn: {canton.nombre}</label>
                <label>Distrito: {distrito.nombre}</label>
                <label>Barrio: {barrio.nombre}</label>
                <label>Direccin: {direccion}</label>
                <Divider />

                <label>Telfono 1: {telefono1}</label>
                <label>Telfono 2: {telefono2}</label>
                <label>Email: {email}</label>
                <Divider />

                <label>Colegiado: {colegiado}</label>
                <label>Secretaria: {secretaria}</label>
                <label>Das Visita: {diasvisita}</label>
                <label>Hora Visita: {horavisita}</label>
                <Divider />

                <label>Obsrevaciones: {observaciones}</label>
                <Divider />
                <SpacerHorizontal />

                <ImgPerfil img={images} />
                <SpacerHorizontal />
                <Divider />                
                <SpacerHorizontal />

                <MapContainer />
                <SpacerHorizontal />

               </div>
           </div>
       </div>
   )
}