import { createContext, useContext, useEffect, useReducer } from 'react';
// import { ImagePickerResponse } from 'react-native-image-picker';
import moment from 'moment';

import { VisitaReducer, VisitasState } from '../reducers/visitaReducer';
import { AuthContext } from './AuthContext';

import AppApi from '../api/AppApi';
import {
    ChildrenProps,
    Visita,
    VisitaResponse,
    ResponsePostVisita,
    ProductosTransaccion,
    ResponsePostTransaccion,
    DataFilters,
    VisitaFilterResponse,
    VisitasResponse,
    DataFilterProps,
    DataFilterVisitasProps
} from '../interfaces/interfaces';
import { AppContext } from './AppContext';

//TODO: Subir imagenes


type VisitaContextProps = {
    visitas: Visita[],
    visitasIndexFilter : number | null,
    visitasDay: Visita[],
    visitasFilter: Visita[],
    visitasFiltradas: Visita[],
    visitaSeleccionada: Visita | null,
    idVisitaSeleccionada: string | null,
    productosTransaccion: ProductosTransaccion[]
    dataFiltros: DataFilters[],

    addFiltrosVisita: (filtros: any) => void;
    addProductoTransaccion: (detalle: ProductosTransaccion) => void;
    cargaDataFilter: (Props : DataFilterVisitasProps) => void;
    cargaVisitas: () => void;
    cargaVisitasFiltrado: () => void;
    clearDataFilterIndex: () => void;
    clearFiltrosVisita: () => void;
    crearVisita: (visita: any) => void;
    deleteProductoTransaccion: (detalle: ProductosTransaccion) => void;
    prugaProductoTransaccion: () => void;
    purgaDataFilter: () => void;
    purgaVisitaSeleccionado: () => void;
    seleccionaVisita: (visita: Visita) => void;
    updateVisita: (visita: any) => void;
    selecVisita : (visita : Visita) => void;
    setDataFilterIndex: (index : number) => void;


}


const initialState: VisitasState = {
    visitas: [],
    visitasFilter: [],
    visitasDay: [],
    visitasFiltradas: [],
    dataFiltros: [],
    visitaSeleccionada: null,
    idVisitaSeleccionada: null,
    productosTransaccion: [],
    visitasIndexFilter : null,

}

const limite = 500;

export const VisitaContext = createContext({} as VisitaContextProps)
export const VisitaProvider = ({ children }: ChildrenProps) => {

    const [state, dispatch] = useReducer(VisitaReducer, initialState)
    const { status, user } = useContext(AuthContext)
    const { toogleUpdateSaldos, showProcesing , hideProcesing } = useContext(AppContext)

    useEffect(() => {
        if (status === 'authenticated') {
            cargaVisitas()
        }
    }, [status])

    useEffect(() => {
        if (status === 'authenticated') {
            cargaVisitasFiltrado()
        }
    }, [status, state.dataFiltros, state.visitas])


    const cargaVisitas = async () => {
        const { data } = await AppApi.get<VisitaResponse>(`/visitas?limite=${limite}&desde=0`);
        dispatch({
            type: 'carga-data-visitas',
            payload: data.visitas
        })
        const fechaEval = toMs(moment().format('YYYY-MM-DD'))
        const visitasTmp: Visita[] = data.visitas.filter((visita) => {
            return toMs(moment(visita.fecha).format('YYYY-MM-DD')) === fechaEval
        })
        dispatch({
            type: 'carga-data-visitasday',
            payload: visitasTmp
        })
    }

    const cargaVisitasFiltrado = async () => {

        if (state.dataFiltros.length === 0) {
            dispatch({
                type: 'carga-data-visita-filtrados',
                payload: state.visitas
            })
        } else {
            const { data } = await AppApi.post<VisitaFilterResponse>(`/search/visitas`, state.dataFiltros);
            dispatch({
                type: 'carga-data-visita-filtrados',
                payload: data.results
            })
        }
    }

    const seleccionaVisita = (visita: Visita) => {
        dispatch({
            type: 'selecciona-visita',
            payload: visita
        })
    }

    const addFiltrosVisita = (filtros: any) => {
        dispatch({
            type: 'carga-data-filtros',
            payload: filtros
        })
    }

    const clearFiltrosVisita = () => {
        dispatch({
            type: 'purga-data-filtros',
        })
    }


    const purgaVisitaSeleccionado = () => {
        dispatch({
            type: 'purga-visita',
        })
    }

    const crearVisita = async (visita: any) => {
        const { _id, ...dataClean } = visita

        const dataSend = {
            ...dataClean,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.post<ResponsePostVisita>(`/visitas`, dataSend);
            if (data.ok) {
                await cargaVisitas()
                //Si todo sale bien creamos la transaccion de las muestras
                const visita = data.visita

                if (state.productosTransaccion) {
                    if (state.productosTransaccion.length > 0) {
                        const dataSend = {
                            fecha: moment(),
                            visita: visita._id,
                            signo: -1,
                            anotaciones: `Entrega de Muestras a ${visita.cliente.nombre}`,
                            detalle: state.productosTransaccion
                        }
                        const { data } = await AppApi.post<ResponsePostTransaccion>(`/transacciones`, dataSend);
                        if (data.ok) {
                            const { data: aplicacion } = await AppApi.put<ResponsePostTransaccion>(`/transacciones/aplica/${data.transaccionInv._id}`);
                            if (!aplicacion.ok) {
                                hideProcesing()
                                return {
                                    ok: false,
                                    msg: 'No pude aplicar la transacion de las muestras, comunicarse con soporte',
                                    visita: []
                                }
                            }
                        } else {
                            hideProcesing()
                            return {
                                ok: false,
                                msg: 'No pude crear la transacion de las muestras, comunicarse con soporte',
                                visita: []
                            }
                        }
                    }
                    hideProcesing()
                    toogleUpdateSaldos()
                    prugaProductoTransaccion()
                }
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Registrado con xito',
                    visita: data.visita
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg,
                    visita: []
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }


    // const uploadImage = async (visitaId: string, data: ImagePickerResponse) => {

    //     const fileToUpload = {
    //         uri: data.assets![0].uri,
    //         type: data.assets![0].type,
    //         name: data.assets![0].fileName
    //     }

    //     const formData = new FormData()
    //     formData.append('archivo', fileToUpload)

    //     try {
    //         const resp = await AppApi.put(`/uploadsimg/visitas/${visitaId}`, formData);
    //         // dispatch({
    //         //     type: 'registraUsuario',
    //         //     payload: resp.data.modelo,
    //         // })
    //     } catch (error: any) {
    //         registraNotification({
    //             title: 'Error',
    //             msg: error.response.data.msg,
    //             type: 'error'
    //         })
    //     }

    // }


    const updateVisita = async (visita: any) => {
        const dataSend = {
            ...visita,
            visitador: user?.uid,
            pais: user?.pais,
        }
        try {
            showProcesing()
            const { data } = await AppApi.put<VisitaResponse>(`/visitas/${visita._id}`, dataSend);
            if (data.ok) {
                //Si todo sale bien
                await cargaVisitas()
                hideProcesing()
                return {
                    ok: true,
                    msg: 'Actualizado con xito'
                }
            } else {
                hideProcesing()
                return {
                    ok: false,
                    msg: data.msg
                }
            }
        } catch (error: any) {
            hideProcesing()
            return {
                ok: false,
                msg: error.response.data.msg
            }
        }
    }


    const addProductoTransaccion = (detalle: ProductosTransaccion) => {
        dispatch({
            type: 'add-producto-transaccion',
            payload: detalle
        })
    }

    const deleteProductoTransaccion = (detalle: ProductosTransaccion) => {
        dispatch({
            type: 'delete-producto-transaccion',
            payload: detalle
        })
    }

    const prugaProductoTransaccion = () => {
        dispatch({
            type: 'purga-producto-transaccion',
        })
    }


    const toMs = (dateStr: any) => {
        const [year, month, day] = dateStr.split("-")
        return new Date(year, month - 1, day).getTime()
    }


    const cargaDataFilter = async ({ inicio, fin, uid, max, cliente } : DataFilterVisitasProps) => {
        const start = moment(inicio).format('DD-MM-YYYY').toString()
        const end = moment(fin).format('DD-MM-YYYY').toString()
        const { data } = await AppApi.get<VisitasResponse>
            (`/visitas/cliente/${uid}?inicio=${start}&final=${end}&limite=${max}&cliente=${cliente}&orden=asc`);
            // console.log(data);
            
        if (data) {
            dispatch({
                type: 'add-visitasFilter',
                payload: data.visitas
            })            
        } else {
            dispatch({
                type: 'remove-visitasFilter',
            })
        }
    }

    const purgaDataFilter  = () => {
        dispatch({
            type: 'remove-visitasFilter',
        })    
    }

    const clearDataFilterIndex  = () => {
        dispatch({
            type: 'clear-visitasIndexFilter',
        })    
    }


    const selecVisita  = (visita : Visita) => {
        dispatch({
            type: 'selecciona-visita',
            payload: visita
        })        
    }

    const setDataFilterIndex  = (index :  number) => {
        dispatch({
            type: 'set-visitaIndexFilter',
            payload : index
        })    
    }

    return (
        <VisitaContext.Provider value={{
            ...state,
            addFiltrosVisita,
            addProductoTransaccion,
            cargaDataFilter,
            cargaVisitas,
            cargaVisitasFiltrado,
            clearDataFilterIndex,
            clearFiltrosVisita,
            crearVisita,
            deleteProductoTransaccion,
            prugaProductoTransaccion,
            purgaDataFilter,
            purgaVisitaSeleccionado,
            seleccionaVisita,
            updateVisita,
            selecVisita,
            setDataFilterIndex
        }}>
            {children}
        </VisitaContext.Provider>
    )

}