import moment from "moment";
import { Cliente, Evento, Visita } from "../interfaces/interfaces";

interface DataEventosProps {
    eventosFilter : Evento[]
}

interface DataVisitasProps {
    visitasFilter : Visita[]
}

interface DataClientesProps {
    clientesFiltrados : Cliente[]
}

const file_Download  = (data: any, name : string) => {
    let csvContent = "data:text/csv;charset=utf-8," + data.map(e => e.join(",")).join("\n");
    const fileName = `${name}_${moment().format('DD-MM-YYYY_HH:MM:SS').toString()}.csv`
    var encodedUri = encodeURI(csvContent);
    var link = document.createElement("a");
    link.setAttribute("href", encodedUri);
    link.setAttribute("download", fileName);
    document.body.appendChild(link); 
    link.click();
}

export const removeAccents = (str: string) => {
    //COMENTARIO : Elimna asentos de un string
    return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
  } 


export const file_Download_Eventos  = ({eventosFilter=[]} : DataEventosProps) => {
    //COMENTARIO : Crea los encabezados
    const data = [
        ["Visitador",
         "Fecha Inicio",
         "Fecha Fin",
         "Hora inicio",
         "Hora Fin",
         "Tipo",
         "Estado",
         "Cliente",
         "Evento", 
         "Notas"
    ],
    ];
    //COMENTARIO : Inserta los registros de la Data recibida
    eventosFilter.map(event => {
        const {user, start, end, tipo, estado, cliente, title, notes} = event
        data.push([ 
            removeAccents(user.nombre),
            moment(start).format('DD-MM-YYYY').toString(), 
            moment(end).format('DD-MM-YYYY').toString(), 
            moment(start).format('HH:MM:SS').toString(), 
            moment(end).format('HH:MM:SS').toString(), 
            removeAccents(tipo.nombre), 
            estado, 
            cliente ? removeAccents(cliente.nombre) : "", 
            removeAccents(title.replace(",", " ")), 
            removeAccents(notes.replace(",", " "))
        ])
    })
    //COMENTARIO : Exporta data a CSV
    file_Download(data, "Eventos")
}


export const file_Download_Clientes  = ({clientesFiltrados} : DataClientesProps) => {
    //COMENTARIO : Crea los encabezados
    const data = [
        ["Nombre",
         "Especialidad",
         "Direccion",
         "Telefono1",
         "Telefono2",
         "Email",
         "Colegiado",
         "Secretaria",
         "Dias visita",
         "Hora visita",
         "Pais",
         "Provincia",
         "Canton",
         "Distrito",
         "Grupocliente",
         "Especialidad",
         "Zona",
         "Visitador",         
    ],
    ];
    //COMENTARIO : Inserta los registros de la Data recibida
    clientesFiltrados.map(cliente => {
        const {nombre, 
            especialidad,
            direccion,
            telefono1,
            telefono2,
            email,
            colegiado,
            secretaria,
            diasvisita,
            horavisita,
            pais,
            provincia,
            canton,
            distrito,
            grupocliente,
            zona,
            visitador} = cliente
        data.push([ 
            removeAccents(nombre),
            removeAccents(especialidad.nombre),
            removeAccents(direccion),
            telefono1,
            telefono2,
            email,
            colegiado,
            removeAccents(secretaria),
            removeAccents(diasvisita),
            removeAccents(horavisita),
            removeAccents(pais.nombre),
            removeAccents(provincia.nombre),
            removeAccents(canton.nombre),
            removeAccents(distrito.nombre),
            removeAccents(grupocliente.nombre),
            removeAccents(zona.nombre),
            removeAccents(visitador.nombre),
        ])
    })
    //COMENTARIO : Exporta data a CSV
    file_Download(data, "Clientes")
}


export const file_Download_Visitas  = ({visitasFilter=[]} : DataVisitasProps) => {
    //COMENTARIO : Crea los encabezados
    const data = [
        [
            "Fecha",
            "Anotaciones",
            "Longitud",
            "Latitud",
            "Presicion",
            "Cliente",
            "Tipo",
            "Alerta",
            "Estado",
            "Visitador",
    ],
    ];
    //COMENTARIO : Inserta los registros de la Data recibida
    visitasFilter.map(event => {
        const {fecha, anotaciones, gpsLong, gpsLat, gpsPresicion, cliente, tipo, alerta, novisita, usuario} = event
        data.push([ 
            moment(fecha).format('DD-MM-YYYY').toString(), 
            removeAccents(anotaciones.replace(",", " ")),
            gpsLong.toString(),
            gpsLat.toString(),
            gpsPresicion.toString(),
            cliente ? removeAccents(cliente.nombre) : "", 
            removeAccents(tipo.nombre.replace(",", " ")),
            removeAccents(alerta.nombre.replace(",", " ")),
            removeAccents(novisita.nombre.replace(",", " ")),
            removeAccents(usuario.nombre),
        ])
    })
    //COMENTARIO : Exporta data a CSV
    file_Download(data, "Visitas")
}
